import React, {useState, useRef, useEffect} from 'react';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    Icon,
    IconButton,
    Typography,
    Toolbar,
    AppBar, MenuItem, FormControl,
} from '@material-ui/core';
import Formsy from 'formsy-react';
import {makeStyles} from "@material-ui/styles";
import {TextFieldFormsy, SelectFormsy} from "../../components/formsy";
import PaperDropzone from "../../components/Dropzone/Dropzone";
import Axios from "axios";
import {URL} from "../../BaseUrl";

const useStyles = makeStyles({
    justifyCenter: {
        display: 'flex',
        width: '100%',
        justifyContent: 'space-between',
        alignItems: 'center'
    },
    mb_20: {
        marginBottom: '20px'
    }
});

function VisitHistoryDialog(props) {
    const classes = useStyles();

    const {open, type, handleClose, dataForEdit, getTableData} = props;

    const [conditions, setConditions] = useState([]);


    const formRef = useRef(null);
    const [isFormValid, setIsFormValid] = useState(false);
    const [clientSuggestions, setClientSuggestions] = useState([]);


    useEffect(()=>{
        const body = {"nKeywordTitle":"","nEmail":"","nPhoneNumber":"","nOrderBy":"ClientName","nPageSize":1000000}
        Axios.post(URL + '/api/GetAllClients', body).then(response=>{
            if(response.data.Result.result){
                setClientSuggestions(response.data.Result.ClientsData)
            }
        })
    },[])

    useEffect(()=>{
        if(open && type === 'Save' && dataForEdit){
            setConditions(dataForEdit.AttachmentsArray || [])
        }
    },[type,open, dataForEdit])

    function disableButton() {
        setIsFormValid(false);
    }

    function enableButton() {
        setIsFormValid(true);
    }

    function handleSubmit(model) {
        const body = {
            ID: type === 'Add' ? -1 : dataForEdit.ID,
            ClientID: model.ClientID || '',
            ProjectID: dataForEdit.ProjectID || '',
            VisitType: 2,
            CreatedBy: localStorage.getItem('name'),
            Remarks: model.Remarks || '',
            PortalID: 2,
            IsNew: type === 'Add',
            PlotNo: model.PlotNo || '',
            BlockNo: model.BlockNo || '',
            Area: model.Area || '',
            ClientDecision: dataForEdit.ClientDecesion || '',
            Attachment: conditions || []
        }

        Axios.post(URL + '/api/SaveClientVisit', body).then(response => {
            if(response.data.result){
                getTableData();
                closeDialog();
            }
        }).catch(err => console.error(err))

    }

    function closeDialog() {
        handleClose();
    }

    return (
        <Dialog
            classes={{
                paper: 'm-24'
            }}
            open={open}
            fullWidth
            disableEnforceFocus
            maxWidth="md"
        >
            <AppBar position="static" elevation={1} className={classes.mb_20}>
                <Toolbar className={classes.justifyCenter}>
                    <Typography variant="subtitle1" color="inherit">
                        {`${type} Visit`}
                    </Typography>
                    <IconButton color="inherit" onClick={closeDialog}>
                        <Icon>close</Icon>
                    </IconButton>
                </Toolbar>
            </AppBar>
            <Formsy
                id="project-visit-form"
                onValidSubmit={handleSubmit}
                onValid={enableButton}
                onInvalid={disableButton}
                ref={formRef}
                style={{overflowY: 'inherit'}}
            >
                <DialogContent>
                    <div className={classes.mb_20}>
                        <TextFieldFormsy
                            value={dataForEdit.PlotNo}
                            variant="outlined"
                            fullWidth
                            required
                            type="text"
                            name="PlotNo"
                            label="Plot No."
                        />
                    </div>
                    <div className={classes.mb_20}>
                        <TextFieldFormsy
                            value={dataForEdit.Remarks}
                            variant="outlined"
                            fullWidth
                            type="text"
                            name="Remarks"
                            label="Name"
                        />
                    </div>
                    <div className={classes.mb_20}>
                        <TextFieldFormsy
                            value={dataForEdit.BlockNo}
                            variant="outlined"
                            fullWidth
                            required
                            type="text"
                            name="BlockNo"
                            label="Block No."
                        />
                    </div>
                    <div className={classes.mb_20}>
                        <TextFieldFormsy
                            value={dataForEdit.Area}
                            variant="outlined"
                            fullWidth
                            required
                            type="text"
                            name="Area"
                            label="Area"
                        />
                    </div>
                    <div className={classes.mb_20}>
                        <FormControl disabled fullWidth variant='outlined'>
                            <SelectFormsy
                                variant="outlined"
                                disabled
                                fullWidth
                                value={2}
                                type="text"
                                name="status"
                                label="Status"
                            >
                                <MenuItem value={2}>Follow Up</MenuItem>
                            </SelectFormsy>
                        </FormControl>
                    </div>


                    <div className={classes.mb_20}>
                        <PaperDropzone
                            title="Attachments"
                            setImageData={setConditions}
                            data={conditions}
                            multiple
                            type={[
                                ".dxf",
                                ".dif",
                                ".pdf",
                                ".png",
                                ".jpeg",
                                ".doc",
                                ".docx",
                            ]}
                        />
                    </div>
                    <div className={classes.mb_20}>
                        <SelectFormsy
                            value={dataForEdit.ClientID}
                            variant="outlined"
                            fullWidth
                            required
                            type="text"
                            name="ClientID"
                            label="Client Name*"
                        >
                            {
                                clientSuggestions?.map(client => <MenuItem key={client.ID} value={client.ID}>{client.ClientName}</MenuItem>)
                            }
                        </SelectFormsy>
                    </div>
                </DialogContent>
            </Formsy>
            <DialogActions>
                <Button autoFocus type="button" onClick={closeDialog} color="primary">
                    Close
                </Button>
                <Button
                    disabled={!isFormValid}
                    type="submit"
                    form="project-visit-form"
                    color="primary"
                    autoFocus
                >
                    Save
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default VisitHistoryDialog;
