import React, { Fragment } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import LanguageIcon from '@material-ui/icons/Language';
import { withInternationalization } from '../../../redux/Internationalization/withInternationalization';
import { Avatar, createMuiTheme } from '@material-ui/core';
import { styles } from '../../../styles';
import EngLanguage from '../../../assets/images/eng-flag.svg';
import ArabicLanguage from '../../../assets/images/arabic-flag.svg';

function InternationalizationMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const { toggleLanguage } = props;

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose(lang) {
    setAnchorEl(null);
    if (lang === 'en' || lang === 'ar') {
      toggleLanguage(lang);
      let dir = lang === 'ar' ? 'rtl' : 'ltr';
      let langID = lang === 'ar' ? '2' : '1';
      window.localStorage.setItem('language', langID);
      createMuiTheme({ direction: dir });
      document.getElementsByTagName('body')[0].setAttribute('dir', dir);
    }
  }

  return (
    <div>
      <IconButton
        aria-label="More"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        <Avatar src={EngLanguage} style={styles.flag_style} />
      </IconButton>
      <Menu
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            width: 175,
            marginTop: '48px'
          }
        }}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center'
        }}
      >
        <MenuItem
          style={styles.d_flex}
          onClick={() => {
            handleClose('en');
          }}
        >
          <Avatar src={EngLanguage} style={styles.flag_style} />
          English
        </MenuItem>
        {/* <MenuItem
          style={styles.d_flex}
          onClick={() => {
            handleClose('ar');
          }}
        >
          <Avatar src={ArabicLanguage} style={styles.flag_style} />
          Arabic
        </MenuItem> */}
      </Menu>
    </div>
  );
}

export default withInternationalization(InternationalizationMenu);
