import Axios from 'axios';
import { api } from '../api/api';
import { getVitals } from './utils';

export function HandleActivityLog(parameters) {
  const {
    id,
    assignTo,
    conditions,
    startDate,
    taskAction,
    remarks,
    isNew,
    activityID
  } = parameters;
  const { Name } = getVitals();
  let params = {
    nUserID: assignTo,
    nTaskActivityID: activityID,
    nTaskAction: taskAction,
    nActionDate: startDate,
    IsDeleted: false,
    nRemarks: remarks,
    nCreatedBy: Name,
    nCreatedDate: '',
    nModifiedBy: Name,
    nModifiedDate: '',
    nCurrentID: isNew ? -1 : id,
    nPortalID: 2,
    isNew: isNew,
    nAttachments: conditions
  };

  let link = `${api}/SaveTaskActivityLog`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function GetLogs(id) {
  let link = `${api}/GetTaskActivityLogByTaskActivityID`;
  let params = {
    nTaskActivityID: id
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function GetVisitTasks(id) {
  let link = `${api}/GetClientVisitTaskByVisitID`;
  let params = { VisitID: id, PortalID: 2 };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function MultipleLogsDelete(ids) {
  let params = {
    nTaskActivityLogID: ids.toString(),
    // nTaskActivityLogID: parseInt(ids, 10),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeleteTaskActivityLog`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
