import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Button,
  IconButton,
} from "@material-ui/core";
import history from "../../history";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { Table } from "../../components/Table";
import {
  sent_module_columns,
  projects_module_columns,
} from "../../server/columns";
import {
  GetAllUsers,
  GetAllReceivedNotifications,
  GetAllProjects,
  GetAllProjectNotifications,
} from "../../server";
import { styles } from "../../styles";
import { DatePicker } from "@material-ui/pickers";
import * as Action2 from "../../redux/actions/user.actions";
import { useSelector, useDispatch } from "react-redux";

function Inquiry(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const dispatch = useDispatch();
  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(true);
  const [project, setProject] = useState("");
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [projectID, setProjectID] = useState(null);
  const [clients, setClients] = useState([]);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [BtnColor, setBtnColor] = useState("#ff8800");
  const [BtnDisable, setBtnDisable] = useState(true);

  const [page, setPage] = useState(0);
  const [Record, setRecord] = useState(0);

  useEffect(() => {
    dispatch(Action2.setModule("Inquiry"));
  }, []);

  const reloadContent = (NewPages) => {
    setData([]);
    setPage(NewPages);
    setLoading(true);
    setError("");
    GetAllProjectNotifications(NewPages)
      .then((res) => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          setRecord(res.data.TotalCount);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.NotificationData;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  function SearchData() {
    setData([]);
    setLoading(true);
    setError("");
    GetAllProjectNotifications(page, startDate, endDate, projectID)
      .then((res) => {
        if (res.data.result) {
          let users = res.data.NotificationData;
          setRecord(res.data.TotalCount);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }

  // function getContent(id) {
  //   setError("");
  //   let filteredProject = [];

  //   if (id !== "date") {
  //     filteredProject = projects.filter((item) => item.ID == id);
  //     setStartDate(null);
  //     setEndDate(null);
  //   } else {
  //     setProject("");
  //     filteredProject = projects.filter((item) => {
  //       let projectDate = new Date(item.CreatedDate).toString();
  //       let start = startDate.toString();
  //       let end = endDate.toString();
  //       return projectDate >= start && projectDate <= end;
  //     });
  //   }
  //   setData(filteredProject);
  // }

  useEffect(() => {
    GetAllProjects( 0,
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      1000)
      .then((res) => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
        setLoadingProjects(false);
      })
      .catch((error) => {
        console.log("Error fetching projects...");
      });
    GetAllUsers()
      .then((res) => {
        let data = res.data.Result.UsersData || [];
        setUsers(data);
      })
      .catch((error) => {
        console.log("Error fetching users...");
      });
    GetAllProjectNotifications()
      .then((res) => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          setRecord(res.data.TotalCount);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.NotificationData;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }, []);

  const UserRole = useSelector((user) => user.userReducer.UserRole[0]);

  function setColorUp() {
    if (UserRole && UserRole.RoleTypeName) {
      let value =
        UserRole.RoleTypeName === "Supervisor" ||
        UserRole.RoleTypeName === "Adminstration" ||
        UserRole.RoleTypeName === "Specifications Engineer" ||
        UserRole.RoleTypeName === "Specifications Auditor"
          ? "#ff8800"
          : "#E8E6E6";
      setBtnColor(value);
    }
  }
  function setBtnUp() {
    if (UserRole && UserRole.RoleTypeName) {
      let value =
        UserRole.RoleTypeName === "Supervisor" ||
        UserRole.RoleTypeName === "Adminstration" ||
        UserRole.RoleTypeName === "Specifications Engineer" ||
        UserRole.RoleTypeName === "Specifications Auditor"
          ? true
          : false;
      setBtnDisable(value);
    }
  }

  useEffect(() => {
    setColorUp();
    setBtnUp();
  }, [UserRole]);

  return (
    <Grow in={true}>
      <div>
        <IconButton
          onClick={(ev) => {
            ev.preventDefault();
            history.goBack();
          }}
        >
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h6" gutterBottom>
          Inquiry
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <FormControl style={{ ...styles.my_3 }} fullWidth>
                <InputLabel htmlFor="age-simple">Project</InputLabel>
                <Select
                  fullWidth
                  value={project}
                  onChange={(e) => {
                    setProject(e.target.value);
                    setProjectID(e.target.value);
                    // getContent(parseInt(e.currentTarget.id, 10));
                  }}
                >
                  {projects &&
                    projects.map((item) => (
                      <MenuItem value={item.ID} id={item.ID}>
                        {item.ProjectTitle}
                      </MenuItem>
                    ))}
                </Select>
              </FormControl>
            )}
            {!loadingProjects && (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <DatePicker
                    autoOk
                    label="Start Date"
                    clearable
                    disableFuture
                    value={startDate}
                    onChange={(date) => setStartDate(date)}
                  />
                </Grid>
                <Grid item xs={3}>
                  <DatePicker
                    autoOk
                    label="End Date"
                    clearable
                    disableFuture
                    value={endDate}
                    onChange={(date) => setEndDate(date)}
                  />
                </Grid>
                <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={(e) => SearchData()}
                    color="primary"
                    BtnColor
                    variant="contained"
                    disabled={(startDate === null) && (endDate === null) && projectID === null}
                  >
                    Search
                  </Button>
                </Grid>
              </Grid>
            )}
            {data.length > 0 && (
              <Table
                title="Projects list"
                columns={projects_module_columns}
                data={data}
                isLoading={loading}
                error={error}
                onChangePage={(page) => reloadContent(page)}
                page={page}
                totalRecords={Record}
                onSuccess={reloadContent}
                onRefresh={reloadContent}
                nullSelected={nullSelected}
                variant={4}
                setData={setData}
                // listType={"inquiry"}
              />
            )}
            {data.length === 0 && !loading && (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <p style={{...styles.text_bold, fontSize:"16px"}}>There is no Data for this project!</p>
              </div>
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Inquiry);
