import React,{useState,useEffect} from 'react';
import moment from 'moment';
import axios from 'axios'
import { URL } from '../../BaseUrl';
import ReactTable from 'react-table';
import { withStyles } from '@material-ui/core/styles';
import rt from "react-table/react-table.css";
import { CircularProgress } from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import './TABLE_CSS_ALL_LOADING.css'

const DailyReportApp = () => {
    
    const [dailyReports, setDailyReports] = useState(null);
    const [loading,setLoading] = useState(true)
    const [startDate, setStartDate] = useState("2019-07-22 13:30:21.893")
    const [endDate, setEndDate] = useState(moment(new Date()).endOf('day'));
    // const startDay = moment(new Date()).startOf('day').subtract(1,'day')
    // const endDay = moment(new Date()).endOf('day');

    useEffect(() => {
        const body = {
            nDateFrom: startDate,
            nDateTo: endDate
        }
        axios.post(`${URL}/Api/Project_Visit_Report`, body).then((response) => {
            setDailyReports(response.data);
            setLoading(false)
        })
    },[startDate,endDate])

    return (
        <React.Fragment>
            <MuiPickersUtilsProvider utils={MomentUtils}>
                <div className="Second_TABLE_DATE_PICKERS_CODE_HEAD" >

                    <div>

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            label="From ( Date )"
                            format="MM-DD-YYYY"
                            inputVariant="outlined"
                            value={startDate}
                            onChange={(date) => setStartDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            inputVariant="outlined"
                            label="To ( Date )"
                            format="MM-DD-YYYY"
                            value={endDate}
                            onChange={(date) => setEndDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                    </div>

                </div>

            </MuiPickersUtilsProvider>

            {dailyReports !== null && dailyReports.ProjectsData === null && !loading && <div>

                <h4>No Data Found</h4>

            </div>}

            {
                loading && <div id="NEW_ALL_TABLE_LOADING" >
                    <div>
                        <CircularProgress />
                        <div>
                            Loading Data...
                        </div>
                    </div>
                </div>
            }

            {dailyReports !== null && dailyReports.ProjectsData !== null && !loading && <div>

                <header id="NEW_ALL_TABLE_Header">
                    <span>{`Number Of Projects : ${dailyReports.NoOfProject}`}</span>
                </header>    
            
            <ReactTable
                    className='-striped -highlight h-full overflow-hidden'
                    data={dailyReports.ProjectsData || []}
                    columns={
                        [
                            // {
                            //     Header: "id",
                            //     accessor: 'ID',
                            //     className: "justify-start",
                            //     sortable: false,
                            //     Cell: (row) => <div style={{ textAlign : 'center' }} > {row.original.ID}</div>
                            // },
                            {
                                Header: "Project Title",
                                accessor: 'ProjectTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Project Type",
                                accessor: 'ProjectTypeTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Status",
                                accessor: 'StatusTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Stage",
                                accessor: 'StageTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Block No",
                                accessor: 'BlockNo',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Area",
                                accessor: 'Area',
                                className: "justify-start",
                                sortable: false
                            },
                            // {
                            //     Header: "Client ID",
                            //     accessor: 'ClientID',
                            //     className: "justify-start",
                            //     sortable: false
                            // },
                        ]
                    }
                    defaultPageSize={20}
                    noDataText={"No Data"}
                    // showPageSizeOptions={false}
                    // showPagination={false}
                />
            </div>}

            {/* Client Visit Data */}

            <div id="NEW_ALL_TABLE_Spacer"></div>
            
            {dailyReports !== null && dailyReports.ProjectsData !== null && !loading && <div>

                <header id="NEW_ALL_TABLE_Header">
                    <span>{`Number Of Client Visits : ${dailyReports.NoOfVisits}`}</span>
                </header>

                <ReactTable
                    className='-striped -highlight h-full overflow-hidden'
                    data={dailyReports.ClientVisitData || []}
                    columns={
                        [
                            // {
                            //     Header: "id",
                            //     accessor: 'ID',
                            //     className: "justify-start",
                            //     sortable: false,
                            //     Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ID}</div>
                            // },
                            {
                                Header: "Client Name",
                                accessor: 'ClientName',
                                className: "justify-start",
                                sortable: false
                            },
                            // {
                            //     Header: "Client ID",
                            //     accessor: 'ClientID',
                            //     className: "justify-start",
                            //     sortable: false,
                            //     Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ClientID}</div>
                            // },
                            // {
                            //     Header: "Project ID",
                            //     accessor: 'ProjectID',
                            //     className: "justify-start",
                            //     sortable: false,
                            //     Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ProjectID}</div>
                            // },
                            {
                                Header: "Visit Type",
                                accessor: 'VisitTypeTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Visit Number",
                                accessor: 'VisitNumber',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Client Decesion",
                                accessor: 'ClientDecesion',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Remarks",
                                accessor: 'Remarks',
                                className: "justify-start",
                                sortable: false
                            },
                        ]
                    }
                    defaultPageSize={20}
                    noDataText={"No Data"}
                    // showPageSizeOptions={false}
                    // showPagination={false}
                />
            </div>}

        </React.Fragment>
    )
}

export default withStyles(rt)(DailyReportApp);