export function HandleUser(parameters) {
  const {
    Username,
    Familyname,
    Name,
    Email,
    Phone,
    Gender,
    Nationality,
    IsActive,
    id,
    GroupID,
    Image,
    IsNew,
    role,
    password
  } = parameters;
  const { username, token, UserID } = getVitals();
  let params = {
    nName: Name,
    nFamilyName: Familyname,
    nEmail: Email,
    nPhoneNumber: Phone,
    nIsNew: IsNew,
    nPortalID: 2,
    nActive: IsActive,
    nRoleId: role,
    nUserType: 1,
    nUserName: Username,
    nPassword: password,
    nSex: Gender,
    nNationality: Nationality || "",
    nPhoto: Image,
    nPhotoExtention: "png",
    nIsdeleted: false,
    nCreatedBy: UserID,
    nCurrentID: IsNew ? -1 : id,
    nGroupID: parseInt(GroupID, 10) || 1013
  };
  let link = `${api}/UserRegistration`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

