export function HandleGroup(parameters) {
  const { name, description, isPublished, id, isNew, role } = parameters;
  const { Name } = getVitals();
  let params = {
    nPortalID: 2,
    nDesc: description,
    nTitle: name,
    nCreatedBy: Name,
    nActive: isPublished,
    isNew: isNew,
    nCurrentID: isNew ? -1 : id,
    nRoleID: role
  };

  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/SaveGroup`, params)
      .then(res => resolve(res))
      .catch(err => {
        reject(err);
      });
  });
  return promise;
}

