import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import {
  expenses_module_columns
} from "../../server/columns";
import { GetAllClients, GetAllProjects, GetAllExpenses } from "../../server";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";

function Expenses(props) {
  const dispatch = useDispatch();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  useEffect(() =>{
    dispatch(Action2.setModule("Expenses"))
  },[])

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetAllExpenses(page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.ExpenseData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllExpenses(page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.ExpenseData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects(0, null, null, null, null, null, null, null, 100000)
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });
  }, []);

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetAllExpenses(page)
      .then(res => {
        let data = res.data.ExpenseData || [];
        let count = res.data.TotalCount;

        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch(error => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Expenses
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={expenses_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              onSuccess={reloadContent}
              page={page}
              totalRecords={count}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={12}
              projects={projects}
              setData={setData}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Expenses);
