export function AddProjectStatus(params) {
  let link = `${URL}/Api/SaveProjectStatus`;
  //   let params = {
  //     nCurrentID: -1,
  //     nIsNew: true,
  //     nTitle: "XYZ",
  //   };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

