import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  IconButton
} from "@material-ui/core";
import { Table } from "../../components/Table";
import { sent_module_columns } from "../../server/columns";
import {
  GetAllUsers,
  GetAllReceivedNotifications,
  GetAllProjects,
  GetAllProjectNotifications
} from "../../server";
import { styles } from "../../styles";
import { ArrowBackTwoTone } from "@material-ui/icons";

function ProjectNotifications(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(true);
  const [project, setProject] = useState("");
  const [projectID, setProjectID] = useState(0);
  const [clients, setClients] = useState([]);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetAllProjectNotifications(projectID, page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.NotificationData;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  function getContent(id) {
    setData([]);
    setLoading(true);
    setError("");
    GetAllProjectNotifications(id, page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.NotificationData;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }

  useEffect(() => {
    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
        setLoadingProjects(false);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });
    GetAllUsers()
      .then(res => {
        let data = res.data.Result.UsersData || [];
        setUsers(data);
      })
      .catch(error => {
        console.log("Error fetching users...");
      });
  }, []);

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetAllProjectNotifications(projectID, page)
      .then(res => {
        let data = res.data.NotificationData || [];
        let count = res.data.TotalCount;
        setCount(count);

        setData(data);
        setLoading(false);
      })
      .catch(error => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <IconButton
            style={{ ...styles.mr_2 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone />
          </IconButton>
          Project Notifications
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <FormControl style={{ ...styles.my_3 }} fullWidth>
                <InputLabel htmlFor="age-simple">Project*</InputLabel>
                <Select
                  fullWidth
                  value={project}
                  onChange={e => {
                    setProject(e.target.value);
                    setProjectID(parseInt(e.currentTarget.id, 10));
                    getContent(parseInt(e.currentTarget.id, 10));
                  }}
                >
                  {projects &&
                    projects.map(item => (
                      <MenuItem value={item.ProjectTitle} id={item.ID}>
                        {item.ProjectTitle}
                      </MenuItem>
                    ))}
                </Select>
              </FormControl>
            )}
            {projectID !== 0 && (
              <Table
                title=""
                columns={sent_module_columns}
                data={data}
                isLoading={loading}
                error={error}
                onChangePage={changePage}
                onSuccess={reloadContent}
                onRefresh={reloadContent}
                nullSelected={nullSelected}
                variant={16}
                page={page}
                totalRecords={count}
                projects={projects}
                clients={clients}
                users={users}
                notificationType="project"
              />
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(ProjectNotifications);
