import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {
  Divider,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Tooltip,
  Chip,
  IconButton,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  CircularProgress
} from '@material-ui/core';
import { styles } from '../../styles';
import { GetJSDateFromMS, getVitals } from '../../server/utils';
import { DateRange, Add, Edit, Close, Save } from '@material-ui/icons';
import FullScreenDialog from '../FullScreenDialog/FullScreenDialog';
import {
  GetVisitTasks,
  GetAllActiveUsers,
  HandleVisitTask
} from '../../server';
import { DateTimePicker } from '@material-ui/pickers';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: 650,
    overflowY: 'auto'
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '55.66%',
    flexShrink: 0,
    display: 'flex',
    alignItems: 'center'
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
    display: 'flex',
    alignItems: 'center',
    flexBasis: '33.33%',
    flexShrink: 0
  }
}));

export default function ControlledExpansionPanels(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [edit, setEdit] = React.useState([]);
  const [editLoading, setEditLoading] = React.useState(false);
  const [editTask, setEditingTask] = React.useState([]);
  const [assign_to_users, set_assign_to_users] = React.useState([]);
  const [assignTo, setAssignTo] = useState('');
  const [startDate, setStartDate] = useState('');
  const [ID, setID] = useState(0);
  const [endDate, setEndDate] = useState('');
  const [taskType, setTaskType] = useState('');
  const [clientDecision, setClientDecision] = useState('');
  const [remarks, setRemarks] = useState('');
  const [role, setRole] = useState(1);
  const [loadingActiveUsers, setLoadingActiveUsers] = useState(false);
  let task_types = [{ name: 'Design', id: 1 }, { name: 'Inspection', id: 2 }];
  let clientDecisions = [
    { name: 'Agree', id: 1 },
    { name: 'Design modification', id: 2 },
    { name: 'Disagree', id: 3 }
  ];

  const { tasks, error, loading, id, reloadTasks, roles, visitType } = props;

  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  const handleEdit = id => {
    setEdit([id]);
    let editingTask = tasks.filter(task => task.ID === id);
    setEditingTask(editingTask);
    let editingObject = editingTask[0];
    setTaskType(editingObject.TaskType || '');
    setAssignTo(editingObject.AssignToID || '');
    setStartDate(editingObject.StartedDate || '');
    setEndDate(editingObject.EndDate || '');
    setClientDecision(editingObject.ClientDecesion || '');
    setRemarks(editingObject.Remarks || '');
  };

  const handleChangeRole = event => {
    let roleId = parseInt(event.target.value, 10);
    setRole(roleId);
    setLoadingActiveUsers(true);
    GetAllActiveUsers(roleId)
      .then(res => {
        setLoadingActiveUsers(false);
        let assign_to_users = res.data.Result.UsersData || [];
        set_assign_to_users(assign_to_users);
      })
      .catch(err => {
        setLoadingActiveUsers(false);
      });
  };

  const handleSave = () => {
    let params = {
      clientVisitID: id,
      taskType,
      startDate,
      endDate,
      assignTo,
      clientDecision,
      remarks,
      id: edit[0],
      isNew: false,
      VisitType: visitType
    };
    setEditLoading(true);
    HandleVisitTask(params)
      .then(res => {
        let result = res.data.result;
        if (result) {
          reloadTasks(id);
          setEditLoading(false);
          setEdit([]);
        } else {
          alert('Unable to perform.');
          setEditLoading(false);
        }
      })
      .catch(err => {
        setEditLoading(false);
        alert(err.message);
      });
  };

  return (
    <>
      <FullScreenDialog
        title={'Add New Task'}
        setOpen={() => {
          setOpen(false);
        }}
        open={open}
        type="new"
        id={id}
        data={[]}
        onSuccess={() => {
          reloadTasks(id);
        }}
        variant={6}
        roles={roles}
        visitType={visitType}
      />
      {tasks.length > 0 ? (
        <div className={classes.root}>
          <div
            style={{
              ...styles.d_flex,
              ...styles.justify_between,
              minHeight: 64,
              backgroundColor: 'rgb(179, 176, 175)',
              ...styles.alignItems_center
            }}
          >
            <Typography variant="h6" style={{ ...styles.ml_4 }}>
              Showing {tasks.length} tasks for selected Visit
            </Typography>
            <Tooltip title="Add Task">
              <IconButton
                style={{ ...styles.mr_2 }}
                onClick={() => setOpen(true)}
              >
                <Add />
              </IconButton>
            </Tooltip>
          </div>
          {edit.length === 1 &&
            editTask.map(task => (
              <div
                style={{ ...styles.p_4, ...styles.relative, paddingBottom: 80 }}
              >
                <div
                  style={{
                    ...styles.d_flex,
                    ...styles.justify_between,
                    ...styles.flex_wrap
                  }}
                >
                  <FormControl
                    className={classes.formControl}
                    style={
                      visitType === 'Follow Up'
                        ? { ...styles.mt_3, ...styles.w_30 }
                        : { ...styles.mt_3, ...styles.w_45 }
                    }
                  >
                    <InputLabel htmlFor="age-simple">Task Type*</InputLabel>
                    <Select
                      value={taskType}
                      onChange={e => setTaskType(e.target.value)}
                    >
                      {task_types.map(item => (
                        <MenuItem value={item.name}>{item.name}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  <FormControl
                    className={classes.formControl}
                    style={
                      visitType === 'Follow Up'
                        ? { ...styles.mt_3, ...styles.w_30 }
                        : { ...styles.mt_3, ...styles.w_45 }
                    }
                  >
                    <InputLabel htmlFor="age-simple">Role*</InputLabel>
                    <Select value={role} onChange={handleChangeRole}>
                      {roles &&
                        roles.map(item => {
                          return (
                            <MenuItem value={item.ID}>{item.RoleName}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>
                  {
                    <FormControl
                      className={classes.formControl}
                      style={
                        visitType === 'Follow Up'
                          ? { ...styles.mt_3, ...styles.w_30 }
                          : { ...styles.mt_3, ...styles.w_45 }
                      }
                    >
                      {loadingActiveUsers ? (
                        <CircularProgress size="25px" />
                      ) : (
                        <>
                          <InputLabel htmlFor="age-simple">
                            Assign to*
                          </InputLabel>
                          <Select
                            value={assignTo}
                            onChange={e => setAssignTo(e.target.value)}
                          >
                            {assign_to_users.map(item => (
                              <MenuItem value={item.ID}>{item.Name}</MenuItem>
                            ))}
                          </Select>
                        </>
                      )}
                    </FormControl>
                  }
                  <DateTimePicker
                    value={startDate === '' ? null : startDate}
                    onChange={date => {
                      let date_test = new Date(date)
                        .toString()
                        .split('G')[0]
                        .trim();
                      setStartDate(date_test);
                    }}
                    clearable
                    label="Start Date & Time*"
                    style={{ ...styles.mt_3, ...styles.w_45 }}
                    variant="inline"
                  />
                  <DateTimePicker
                    value={endDate === '' ? null : endDate}
                    onChange={date => {
                      let date_test = new Date(date)
                        .toString()
                        .split('G')[0]
                        .trim();
                      setEndDate(date_test);
                    }}
                    clearable
                    label="End Date & Time*"
                    style={{ ...styles.mt_3, ...styles.w_45 }}
                    variant="inline"
                  />
                  <FormControl
                    className={classes.formControl}
                    style={{ ...styles.my_3, ...styles.w_100 }}
                  >
                    <InputLabel htmlFor="age-simple">
                      Client Decision
                    </InputLabel>
                    <Select
                      value={clientDecision}
                      onChange={e => setClientDecision(e.target.value)}
                    >
                      {clientDecisions.map(item => (
                        <MenuItem value={item.name}>{item.name}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  <TextField
                    fullWidth
                    label="Remarks"
                    value={remarks}
                    onChange={e => {
                      setRemarks(e.target.value);
                    }}
                  />
                </div>
                <div
                  style={{
                    position: 'absolute',
                    right: 10,
                    bottom: 5,
                    display: 'flex'
                  }}
                >
                  <IconButton
                    onClick={() => setEdit([])}
                    style={{
                      ...styles.bg_dark
                    }}
                  >
                    <Close style={styles.text_white} />
                  </IconButton>
                  <IconButton
                    onClick={() => handleSave()}
                    style={{
                      ...styles.bg_success,
                      ...styles.ml_4
                    }}
                  >
                    {editLoading ? (
                      <CircularProgress style={styles.text_white} size="25px" />
                    ) : (
                      <Save style={styles.text_white} />
                    )}
                  </IconButton>
                </div>
              </div>
            ))}
          {tasks
            .filter(task => task.ID !== edit[0])
            .map(task => (
              <>
                <ExpansionPanel
                  expanded={expanded === task.ID}
                  onChange={handleChange(task.ID)}
                  elevation={0}
                >
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1bh-content"
                    id="panel1bh-header"
                  >
                    <Typography className={classes.heading}>
                      Task:
                      <Chip
                        label={task.TaskType}
                        variant="outlined"
                        style={styles.ml_2}
                      />
                    </Typography>
                    <Typography className={classes.secondaryHeading}>
                      Decision:{' '}
                      <Chip
                        label={task.ClientDecesion || '---'}
                        style={
                          task.ClientDecesion === 'Agree'
                            ? {
                                ...styles.bg_success,
                                ...styles.ml_2,
                                ...styles.text_white
                              }
                            : task.ClientDecesion === 'Disagree'
                            ? {
                                ...styles.bg_danger,
                                ...styles.ml_2,
                                ...styles.text_white
                              }
                            : {
                                ...styles.bg_warning,
                                ...styles.ml_2,
                                ...styles.text_white
                              }
                        }
                      />
                    </Typography>
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails
                    style={{ ...styles.d_block, ...styles.relative }}
                  >
                    <List
                      style={{
                        ...styles.flex_1,
                        ...styles.d_flex,
                        ...styles.flex_wrap,
                        ...styles.justify_between
                      }}
                    >
                      <ListItem dense style={styles.w_45}>
                        <ListItemAvatar>
                          <Avatar>
                            <DateRange />
                          </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                          primary="Start Date"
                          secondary={task.StartedDate.replace('T', ' at:')}
                          primaryTypographyProps={{
                            style: { ...styles.text_bold, ...styles.text_sm }
                          }}
                          secondaryTypographyProps={{
                            style: { ...styles.text_sm }
                          }}
                        />
                      </ListItem>

                      <ListItem dense style={styles.w_45}>
                        <ListItemAvatar>
                          <Avatar>
                            <DateRange />
                          </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                          primary="End Date"
                          secondary={task.EndDate.replace('T', ' at:')}
                          primaryTypographyProps={{
                            style: { ...styles.text_bold, ...styles.text_sm }
                          }}
                          secondaryTypographyProps={{
                            style: { ...styles.text_sm }
                          }}
                        />
                      </ListItem>
                    </List>

                    <List
                      style={{
                        ...styles.flex_1,
                        ...styles.d_flex,
                        ...styles.flex_wrap,
                        ...styles.justify_between
                      }}
                    >
                      <ListItem dense style={styles.w_45}>
                        <ListItemText
                          primary="Assigned By"
                          secondary={task.AssignByName}
                          primaryTypographyProps={{
                            style: { ...styles.text_bold, ...styles.text_sm }
                          }}
                          secondaryTypographyProps={{
                            style: { ...styles.text_md }
                          }}
                        />
                      </ListItem>

                      <ListItem dense style={styles.w_45}>
                        <ListItemText
                          primary="Assigned To"
                          secondary={task.AssignToName}
                          primaryTypographyProps={{
                            style: { ...styles.text_bold, ...styles.text_sm }
                          }}
                          secondaryTypographyProps={{
                            style: { ...styles.text_md }
                          }}
                        />
                      </ListItem>

                      <ListItem dense>
                        <ListItemText
                          primary="Remarks"
                          secondary={task.Remarks}
                          primaryTypographyProps={{
                            style: { ...styles.text_bold, ...styles.text_sm }
                          }}
                        />
                      </ListItem>
                    </List>
                    <IconButton
                      style={{
                        position: 'absolute',
                        right: 10,
                        bottom: 5,
                        ...styles.bg_dark
                      }}
                      onClick={() => handleEdit(task.ID)}
                    >
                      <Edit style={styles.text_white} />
                    </IconButton>
                  </ExpansionPanelDetails>
                </ExpansionPanel>
                <Divider />
              </>
            ))}
        </div>
      ) : loading ? (
        <Typography variant="h6" style={{ ...styles.mt_3, ...styles.ml_4 }}>
          Loading
        </Typography>
      ) : error.length > 0 ? (
        <div style={{ ...styles.d_flex, ...styles.justify_between }}>
          <Typography variant="h6" style={{ ...styles.mt_3, ...styles.ml_4 }}>
            {error}
          </Typography>
          <Tooltip title="Add Task" onClick={() => setOpen(true)}>
            <IconButton>
              <Add />
            </IconButton>
          </Tooltip>
        </div>
      ) : (
        <Typography variant="h6" style={{ ...styles.mt_3, ...styles.ml_4 }}>
          Select a visit from left pane
        </Typography>
      )}
    </>
  );
}
