export function HandleAddTask(data, conditions) {
  let link = `${api}/SaveProjectTask`;

  let params = {
    nProjectID: data.projectID,
    nTaskType: data.person || localStorage.getItem("TabData") || "Design",
    nStatus: data.status || data.Status || "working",
    nStartedDate: data.StartedDate || "2019-08-01T13:14:16.8346929+05:00",
    nEndDate: new Date().toISOString(),
    nCreatedBy: localStorage.getItem("name"),
    nCreatedDate: data.CreatedDate || "2019-08-01T13:14:16.8346929+05:00",
    nModifiedBy: data.ModifiedBy || "sample string 8",
    nModifiedDate: data.ModifiedDate || "2019-08-01T13:14:16.8356915+05:00",
    nIsDeleted: false,
    oRemarks: data.remarks || data.Remarks || data.description,
    nCurrentID: localStorage.getItem("ModuleID") || -1,
    nPortalID: 2,
    isNew: data.ID ? false : true,
    nAttachments: conditions || [],
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

