export function HandlingAttachments(conditions) {
  let link = `${api}/SaveAttachments`;
  let params = {
    ID: 0,
    File: conditions[0].File,
    FileExtenstion: conditions[0].FileExtenstion,
    FileName: conditions[0].FileName,
    CategoryID:
      localStorage.getItem("ModuleID") || localStorage.getItem("ModuleIDSub"),
    CategoryTitle: "ProjectTask"
    // `Attachments${localStorage.getItem("name")}`,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function MultipleTasksDelete(ids) {
  let params = {
    nProjectTaskID: ids.toString(),
    nPortalID: 2,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(`${api}/DeleteProjectTask`, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}
