import React,{useState} from 'react';
import './index.css';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button'
import Divider from '@material-ui/core/Divider';
import PermMediaIcon from '@material-ui/icons/PermMedia';
import ImagePreviewModal from './ImagePreviewModal';
import axios from 'axios'
import { URL } from '../../BaseUrl'
import { CircularProgress } from '@material-ui/core';

const ProjectOverview = () => {

    const [id, setId] = useState(0);
    const [imageModal, setImageModal] = useState(false);
    const [ModalData,setModalData] = useState(null)
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);

    const openImageModal = () => {
        setImageModal(true)
    }

    const closeImageModal = () => {
        setImageModal(false)
    }

    const resetModalData = () => {
        setModalData(null)
    }

    const setImageModalData = (obj) => {
        setModalData({
            image: URL + obj.File,
            remarks: obj.Description !== null ? obj.Description : "No Remarks Found!",
            name: obj.FileName
        })
        openImageModal()
    }

    // API CALL

    const getData = (id) => {
        setData(null);
        setLoading(true);
        const body = {
            "nKeywordTitle": "",
            "nProjectType": "",
            "nProjectID": id,
            "nClientID": null,
            "nDateFrom": "",
            "nDateTo": "",
            "nContractType": "",
            "nMobileNumber": "",
            "nPlotNo": "111222",
            "nStatus": null,
            "nOrderBy": "A.ProjectTitle",
            "nPageIndex": 0,
            "nPageSize": 1000
        }

        axios.post(URL + "/Api/GetAllProjects", body).then((res) => {
            setLoading(false);
            if (res.data.Result.result) {  
                setData(res.data.Result.ProjectsData[0])
                console.log(res.data.Result.ProjectsData[0])
            } else {
                setData(null)
            }
        })
    }


    return (
        <div id="client_External_Screen" >
            <h1>Project Overview </h1>
            <div id="SEARCH_FEILDS_HERE" >

                <Grid container spacing={3} >

                    <Grid item xs={12} sm={6} >
                        <TextField
                            label="Enter Your Project ID"
                            value={id}
                            onChange={(e) => !isNaN(e.target.value) ? setId(Number(e.target.value)) :null  }
                            variant="outlined"
                            fullWidth
                        />
                    </Grid>

                    <Grid item xs={12} sm={4} >
                        <Button className="button_class " variant="contained" onClick={() =>   id > 0 ? getData(id) : null} color="primary" fullWidth >
                            Search
                        </Button>
                    </Grid>

                </Grid>

            </div>

            <Divider />
                
            {data !== null && !loading && <div id="Actual_Content_Around_ID" >

                <div className="TEXT_FEILDS_CONTENT" >
                    <h1>Project ID : <span> {data.ID} </span> </h1>
                </div>

                <div className="TEXT_FEILDS_CONTENT" >
                    <h1>Project Status : <span> {data.StatusTitle} </span> </h1>
                </div>

                <div className="TEXT_FEILDS_CONTENT_2" >
                    <h1>Supervisor Remarks</h1>
                    <p>{data.Description}</p>
                </div>

                <div id="Images_Wrapper_HERE_MERE" >
                    <h1>Attachments : ( Click to View )</h1>
                    <div>
                        {
                            data.AttachmentsArray !== null ?data.AttachmentsArray.map((obj) => {
                                return <div onClick={()=> setImageModalData(obj)} id="PER_PREVIEW" key={obj.ID} >
                                    {/* <PermMediaIcon /> */}
                                    <img src={URL + obj.File} alt={obj.FileName} />
                                </div>
                            }) : <div>
                                    No Attachments Found!
                            </div>
                        }
                    </div>
                </div>

                <div>
                    <ImagePreviewModal
                        ModalData={ModalData}
                        imageModalState={imageModal} closeImageModal={closeImageModal} resetModalData={resetModalData}
                        title={ModalData !== null ? ModalData.name : "Image Preview"}
                    />
                </div>

            </div>}
            
            {
                !loading && data === null && <div id="ERROR_MESSAGE_ALL" >
                    <h1>Please Enter a Valid Project ID</h1>
                </div>
            }
            
            {
                loading && <div id="LOADING_DATA_ProjectData" >
                    <div>
                        <CircularProgress />
                       <div>Loading Data ...</div> 
                    </div>
                </div>
            }

        </div>
    )
}

export default ProjectOverview;

/* 
    Project ID 
    Project Status
    Project Attachments 
    Show Image Messages
*/