import Axios from "axios";
import { api } from "../api/api";
// import { getVitals } from "./utils";

export function GetClientVisitByClientID(
  page,
  description,
  type,
  loc,
  status,
  assign,
  clientID
) {
  let params = {
    nAssignTo: assign || null,
    nLocation: loc || "",
    nDescription: description || "",
    nVisitType: type || null,
    nClientDecision: status || "",
    nVisitNumber: "",
    nClientID: clientID || null,
    nPortalID: 2,
    nOrderBy: "A.ID DESC",
    nPageIndex: page,
    nPageSize: 10,
  };

  let link = `${api}/GetClientVisitByClientID`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function GetClientVisitBySpecificClientID(id) {
  let params = {
    ID: id,
    PortalID: 2,
  };

  let link = `${api}/GetClientVisitByID`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

