import React, { useState } from "react";
import styled from "styled-components";
import RemarksTab from "./Workflow components/Remarks";

const Workflow = () => {
  const [selected, setSelected] = useState("remarks");

  return (
    <div>
      <div></div>
      <WrapperComponent>
        <div>
          <TabButton
            onClick={() => {
              setSelected("remarks");
            }}
            selected={selected === "remarks"}
          >
            Remarks
          </TabButton>
          <TabButton
            onClick={() => {
              setSelected("attachment");
            }}
            selected={selected === "attachment"}
          >
            Attachments
          </TabButton>
        </div>

        {selected === "remarks" && <RemarksTab />}
      </WrapperComponent>
    </div>
  );
};

export default Workflow;

const WrapperComponent = styled.div`
  margin-top: 10px;
  padding: 10px;
`;

const TabButton = styled.button`
  background-color: ${(props) => (props.selected ? "#1D56B3" : "#262b36")};
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 500;
  margin: 2px;
  outline: none;
  border: none;
  cursor: pointer;
`;
