import React, { useState, useEffect } from 'react';
import moment from 'moment';
import axios from 'axios'
import { URL } from '../../BaseUrl';
import ReactTable from 'react-table';
import { CircularProgress } from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import './TABLE_CSS_ALL_LOADING.css'


const ExpenseDetails = () => {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true)
    const [startDate, setStartDate] = useState(new Date('2019-08-18T22:33:53'))
    const [endDate, setEndDate] = useState(new Date());

    // const startDay = moment(new Date()).startOf('day').subtract(1, 'day')
    // const endDay = moment(new Date()).endOf('day');

    useEffect(() => {
        const body = {
            nDateFrom: startDate,
            nDateTo: endDate
        }
        setLoading(true)
        axios.post(`${URL}/Api/ExpenseReport`, body).then((response) => {
            console.log("REPONSE DATA", response)
            setData(response.data);
            setLoading(false);
        })
    }, [startDate, endDate])


    return (
        <React.Fragment>
            <MuiPickersUtilsProvider utils={MomentUtils}>
                <div className="Second_TABLE_DATE_PICKERS_CODE_HEAD" >

                    <div>

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            label="From ( Date )"
                            format="MM-DD-YYYY"
                            inputVariant="outlined"
                            value={startDate}
                            onChange={(date) => setStartDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            inputVariant="outlined"
                            label="To ( Date )"
                            format="MM-DD-YYYY"
                            value={endDate}
                            onChange={(date) => setEndDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                    </div>

                </div>

            </MuiPickersUtilsProvider>

            {
                loading && <div id="NEW_ALL_TABLE_LOADING" >
                    <div>
                        <CircularProgress />
                        <div>
                            Loading Data...
                        </div>
                    </div>
                </div>
            }

            {data !== null && data.ExpenseData === null && !loading && <div>

                <h4>No Data Found</h4>

            </div>}

            {data !== null && data.ExpenseData !== null && !loading && <div>

                <header id="NEW_ALL_TABLE_Header">
                    <span>{`Payment Details : Total Record ( ${data.ExpenseData.length} ) `}</span>
                </header>

                <ReactTable
                    className='-striped -highlight h-full overflow-hidden'
                    data={data.ExpenseData || []}
                    columns={
                        [
                            {
                                Header: "id",
                                accessor: 'ID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ID}</div>
                            },
                            {
                                Header: "Expense Title",
                                accessor: 'ExpenseTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Date",
                                accessor: 'Date',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.Date).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Amount",
                                accessor: 'Amount',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.Amount}</div>
                            },
                            {
                                Header: "Project ID",
                                accessor: 'ProjectID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ProjectID}</div>
                            },
                            {
                                Header: "Expense Type",
                                accessor: 'ExpenseType',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Expense Type Title",
                                accessor: 'ExpenseTypeTitle',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Remarks",
                                accessor: 'Remarks',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "Created By",
                                accessor: 'CreatedBy',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "CreatedDate",
                                accessor: 'CreatedDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.CreatedDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Modified By",
                                accessor: 'ModifiedBy',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.ModifiedBy).format("MM-DD-YYYY")
                            }
                        ]
                    }
                    defaultPageSize={data.ExpenseData.length}
                    noDataText={"No Data"}
                    showPageSizeOptions={false}
                    showPagination={false}
                />
            </div>}
        </React.Fragment>
    )
}


export default ExpenseDetails;