import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Button,
  Input,
} from "@material-ui/core";
import { DatePicker } from "@material-ui/pickers";

import { GetAllClients } from "../../server";
import { styles } from "../../styles";

function SearchProjectComponent({
  projectID,
  setprojectID,
  dates,
  setDate,
  site,
  setSite,
  match,
  GetProject,
}) {
  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(false);

  return (
    <Grow in={true}>
      <div>
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Project ID</InputLabel>
                    <Input
                      value={projectID}
                      onChange={(e) => {
                        setprojectID(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <DatePicker
                      autoOk
                      label="Start Date"
                      clearable
                      disableFuture
                      value={dates}
                      onChange={(date) => setDate(date)}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Site Visit</InputLabel>
                    <Input
                      value={site}
                      onChange={(e) => {
                        setSite(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
              </Grid>
            )}
            {!loadingProjects && (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={(e) => {
                      GetProject(e);
                    }}
                    color="primary"
                    variant="contained"
                    disabled={
                      projectID === "" && site === "" && dates === null
                        ? true
                        : false
                    }
                  >
                    Search
                  </Button>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(SearchProjectComponent);
