import Axios from 'axios';
import { api } from '../api/api';
import { category_management, content_management } from '../api/modules';
import { getVitals } from './utils';

export function GetCategories() {
  const { username, token, websiteID, language } = getVitals();
  let promise = new Promise(function(resolve, reject) {
    Axios.get(
      `${api}/${category_management}/GetCategriesByParentID?nUserName=${username}&token=${token}&websiteID=${websiteID}&languageID=${language}&nCategoryType=ContentDAL.Model.Content&nParentID=0`
    )
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function GetGenericContentListing(id, page, search) {
  const { username, token, websiteID, language } = getVitals();
  let params = {
    nUserName: username,
    token: token,
    nWebsite: websiteID,
    nKeyword: '',
    nLanguage: language,
    nCategoryID: -1,
    nPageSize: 10,
    nPageKey: 1
  };
  let link = `${api}/${content_management}/ForTest_GenericContent`;
  if (id) {
    params.nCategoryID = id;
    link = `${api}/${content_management}/GetGenericContentByCategoryIDListing`;
  }
  if (page) {
    params.nPageKey = page;
  }

  if (search) {
    params.nKeyword = search;
  }

  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleCategory(parameters) {
  const {
    name,
    uniqueName,
    description,
    image,
    hoverImage,
    isPublished,
    id,
    isNew,
    ParentID
  } = parameters;

  const { username, token, websiteID, language, Name } = getVitals();
  let params = {
    nUserName: username,
    token: token,
    nWebsiteID: websiteID,
    nPortalID: 1,
    ParentID: 0,
    Name: name,
    UniqueName: uniqueName,
    Description: description,
    Image: image,
    ImageOver: hoverImage,
    IsPublished: isPublished,
    IsRecycled: false,
    IsTranslated: true,
    CreatedBy: Name,
    ModifiedBy: Name,
    LanguageID: language,
    Keywords: '',
    CategoryType: 'ContentDAL.Model.Content',
    OrderID: 1,
    isNew: isNew,
    nCurrentID: isNew ? -1 : id,
    ImageExtension: 'jpg',
    ImageOverExtension: 'jpg'
  };
  if (ParentID && ParentID !== 'main') {
    params.ParentID = ParentID;
  }

  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/${category_management}/AddCategory`, params)
      .then(res => resolve(res))
      .catch(err => {
        reject(err);
      });
  });
  return promise;
}

export function MultipleStatusChange(ids, value) {
  const { username, token, websiteID, language } = getVitals();
  let params = {
    nUserName: username,
    token: token,
    nWebsite: websiteID,
    nLanguage: language,
    nStatus: value,
    ContentIDs: ids
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(
      `${api}/${content_management}/UpdateMultipleGenericContentStatus`,
      params
    )
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function MultipleDelete(ids) {
  const { username, token } = getVitals();
  let params = {
    nUserName: username,
    token: token,
    ContentIDs: ids.toString(),
    PortalID: 1
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/${content_management}/DeleteContent`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function SaveContent(
  data,
  name,
  uniquename,
  description,
  image,
  hoverImage,
  isPublished,
  id,
  isNew,
  ContentData,
  RelatedItems
) {
  const { username, token, websiteID, language, Name } = getVitals();
  let params = {};
  if (!isNew) {
    params = data[0];
  }
  params.Title = name;
  params.UniqueName = uniquename;
  params.Description = description;
  params.ContentImage = image;
  params.isPublished = isPublished;
  params.LanguageID = language;
  params.nCurrentID = isNew ? -1 : id;
  params.ContentData = ContentData || '';
  params.IsRecycled = false;
  params.IsTranslated = true;
  params.nCategories = id;
  params.ModifiedBy = Name;
  params.CreatedBy = Name;
  params.nImageContent = image;
  params.nImageExtention = 'jpg';
  params.nUserName = username;
  params.token = token;
  params.PortalID = 1;
  params.OrderID = 1;
  params.isNew = isNew;
  params.ModuleType = 'ContentDAL.Model.Content';
  params.MetaPageTitle = 'This is MetaPage Title 1.2';
  params.MetaPageDescription = 'This is MetaPage Description 2';
  params.Keywords = 'jpg';
  delete params.ID;
  delete params.ModifiedDate;
  delete params.ContentUniqueName;
  delete params.CategoryName;
  delete params.ContentImage;
  delete params.CreatedDate;
  delete params.IsDeleted;
  delete params.RelatedArray;
  if (isNew) {
    delete params.RelatedItems;
  }
  if (!isNew && params.RelatedArray === null) {
    delete params.RelatedItems;
  }
  let relatedItems = RelatedItems.filter(item => item.Title.length > 0);
  if (relatedItems.length > 0) {
    params.RelatedItems = relatedItems;
  }
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/${content_management}/Save`, params)
      .then(res => resolve(res))
      .catch(err => {
        reject(err);
      });
  });
  return promise;
}

export function DeleteCategory(uniqueName) {
  const { username, token } = getVitals();
  let params = {
    nUserName: username,
    token: token,
    nUniqueName: uniqueName,
    nPortalID: 1
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.get(`${api}/${category_management}/DeleteCategory`, { params })
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
