export const GET_DATA = "GET_DATA";
const initialState = {
  getPayment: null
};

const reducerdata = (state = initialState, action) => {
  switch (action.type) {
    case GET_DATA: {
      return { ...state, getPayment: action.payload };
    }

    default:
      return state;
  }
};
export default reducerdata;
