import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
import { DatePicker } from "@material-ui/pickers";
import history from "../../history";
import Axios from "axios";
import moment from 'moment';
// import { Table } from "../../components/Table";
import { visit_list_module_columns } from "../../server/columns";
import {
  GetProjectTypes,
  GetFundTypes,
  GetContractType,
  HandleProject,
} from "../../server";
import Alert from "../../components/Alert";
import PaperDropzone from "../../components/Dropzone/Dropzone";
// import SearchVisitListComponent from "./SearchVisitListComponent";
import { getVitals } from "../../server/utils";
// import  from '@material-ui/core/TableContainer';
import { styles } from "../../styles";
import axios from "axios";
import { useSelector } from "react-redux";
import {
  TextField,
  Switch,
  FormControlLabel,
  CircularProgress,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Divider,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  RadioGroup,
  Radio,
  FormHelperText,
  Input,
  Grow,
  Typography,
  Grid,
  Button,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Checkbox,
} from "@material-ui/core";
import { URL } from "../../BaseUrl";
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  expansionPanel: {
    marginBottom: 16,
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  table: {
    minWidth: 650,
  },
  formControlDrop: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    padding: theme.spacing(1),
    minWidth: 150,
  },
  CellHead: {
    fontWeight: "700",
    fontSize: "20px",
  },
}));

function FormforVisit(props) {
  const classes = useStyles();

  console.log("titleAndDescription =>", props.titleAndDescription)
  
  React.useEffect(() => {
    if (props.titleAndDescription) {
      const { Area, VisitNumber, BlockNo, clientID } = props.formState;
      console.log("SENDINGG STATE => ", props.formState)
      setArea(Area)
      setVisitNumber(VisitNumber)
      setBlockNo(BlockNo)
      setClientID(clientID)
      setProjectTitle(props.titleAndDescription);
      setProjectDescription(props.titleAndDescription);
    }
  }, [props.titleAndDescription, props.formState])

  const [BlockNo, setBlockNo] = useState();
  const [VisitNumber, setVisitNumber] = useState();
  const [Area, setArea] = useState();
  const [clientID, setClientID] = useState();
  const [projectTypes, setProjectTypes] = useState([]);
  const [projectType, setProjectType] = useState("");
  const [fundTypes, setFundTypes] = useState([]);
  const [fundType, setFundType] = useState("");
  const [contractTypes, setContractTypes] = useState([]);
  const [contractType, setContractType] = useState("");
  const [success, setSuccess] = useState("");
  const [id] = useState(props.match.params.id);
  const [proStatus] = useState(props.match.params.status);
  const [plot] = useState(props.match.params.plot);
  // STATES USED WHILE CALLING API'S

  const [projectTitle, setProjectTitle] = useState(null);
  const [projectDescription, setProjectDescription] = useState("");
  const [designFees, setDesignFees] = useState("");
  const [supervision, setSupervision] = useState("");
  const [contractor, setContractor] = useState("");

  const [attach, setAttach] = useState([]);
  const [conditions, setConditions] = useState([]);
  const [attachment, setAttachment] = useState([]);
  // ATTACHMENTS

  const [open, setOpen] = useState(false);
  const [AddedUser, setAddUser] = useState("");
  const [AddRole, setAddRole] = useState("");
  const [assign, setAssign] = useState([]);
  const [selectedID, setSelected] = useState([]);

  const [selectAll, setSelectAll] = useState(false);

  const [value, setValue] = useState(0);
  const [Roles, setRoles] = useState([]);
  const [UsersToRole, setUsersToRole] = useState([]);

  const [error, setError] = useState("");

  const [dropDownVal, setDropDownVal] = useState([]);

  const [projectID, setProjectID] = useState(0);
  const [projectStatus, setProjectStatus] = useState(true);

  const [ProStatus, setProStatus] = useState(0);
  const [TheProjectID, setTheProjectID] = useState(0);

  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const status = useSelector(
    (StatusReducer) => StatusReducer.StatusReducer.Dcc_Status
  );

  useEffect(() => {
    GetRolesType();
    GetUsers();
    return () => {
      setProjectType("");
      setFundType("");
      setContractType("");
      setProjectTitle("");
      setProjectDescription("");
      setDesignFees("");
      setSupervision("");
      setContractor("");
      setAttach([]);
      setConditions([]);
      setAttachment([]);
    }
  }, []);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const GetRolesType = async () => {
    let res = await Axios.post(URL + "/Api/GetAllRoles");
    if (res.data.result) {
      setRoles(res.data.RolesArray);
    } else {
      console.log("No roles");
    }
  };

  const GetUsers = async (id) => {
    let res = await Axios.post(URL + "/api/GetAllUsers");
    if (res.data.Result.result) {
      setUsersToRole(res.data.Result.UsersData);
    } else {
      console.log("No Users");
    }
  };

  function HandleRoleAdd(e, role) {
    e.preventDefault();
    let arr = [];
    arr = {
      User: e.target.value,
      RolId: role.ID,
      Role: role.RoleName,
    };
    setAssign([...assign, arr]);
  }

  function handleSubmit(e) {
    e.preventDefault();
    if (projectTitle === null) {
      setError("Add Project Title");
      return null;
    }
    if (projectType === "") {
      setError("Add Project Type");
      return null;
    }
    if (projectDescription === "") {
      setError("Add Project Description");
      return null;
    }
    if (fundType === "") {
      setError("Add Fund Type");
      return null;
    }
    if (contractType === "") {
      setError("Add Contract Type");
      return null;
    }
    if (designFees === "") {
      setError("Add Design Free");
      return null;
    }
    if (supervision === "") {
      setError("Add Supervision");
      return null;
    }
    if (contractor === "") {
      setError("Add Contractor");
      return null;
    }
    if (startDate === "") {
      return setError("Select a Start Date");
    }
    if (endDate === "") {
      return setError("Select a End Date");
    }
    if (attach === []) {
      setError("Select a File For SignedProjectConditions");
      return null;
    }
    if (conditions === []) {
      setError("Select a File For SignedAuthorizationLetter");
      return null;
    }
    if (attachment === []) {
      setError("Select a File For AgreedInitialDesign");
      return null;
    }

    let params = {
      projectTitle,
      projectType,
      projectDescription,
      fundType,
      contractType,
      designFees,
      supervision,
      contractor,
      startDate,
      endDate,
      attach,
      conditions,
      attachment,
      id,
      proStatus,
      plot,
      clientID,
      BlockNo,
      VisitNumber,
      Area
    };

    HandleProject(params, props.match.params.id).then((res) => {
      if (res.data.Result.result) {
        let message = "Succesfully Save!";
        setSuccess(message);
        setProjectID(res.data.Result.ProjectID);
        setProjectStatus(res.data.Result.ProjectStatus);
        setTheProjectID(res.data.Result.nModuleID);
        handleChange(e, 1);
      }
    });
  }

  async function handleAssignment(e) {
    e.preventDefault();
    if (!assign.length > 0) {
      setError("Add a Assignment");
      return null;
    }
    let resNew = false;
    let arr34 = [];

    assign &&
      assign.map((ass) => {
        arr34.push({
          nCurrentID: -1,
          nIsNew: true,
          nAssignDate: new Date().toISOString(),
          //  moment(new Date()).format("MM-DD-YYYY").toISOString,
          nDueDate: new Date().toISOString(),
          nAssignBy: parseInt(localStorage.getItem("userID")),
          nAssignTo: parseInt(ass.User),
          nStartedDate: new Date().toISOString(),
          oDescription: ass.Role,
          nProjectID: parseInt(TheProjectID),
          nStatus: status[1].ID,
        });
      });

    let body = {
      _SaveProjectWorkFlow: arr34,
    };

    let res = await Axios.post(URL + "/Api/SaveProjectWorkFlow", body);

    if (res.data.result) {
      setSuccess("Role Assigned");
      setAssign([]);
      setDropDownVal([])
      history.push('/visit-list');

    } else {
      setError("Error while Assigning");
      setAssign([]);
    }
  }

  return (
    <div
      style={{
        display: "flex",
        maxWidth: "1200px",
        marginLeft: "auto",
        marginRight: "auto",
      }}
    >
      <Alert
        open={success.length > 0}
        type="success"
        message={success}
        onClose={() => {
          setSuccess("");
        }}
      />
      <Alert
        open={error.length > 0}
        type="error"
        message={error}
        onClose={() => {
          setError("");
        }}
      />
      <div className={classes.root}>
        <AppBar position="static">
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="simple tabs example"
          >
            <Tab label="Create Project" {...a11yProps(0)} />
            <Tab label="Assign Roles" {...a11yProps(1)} />
            {/* <Tab label="Item Three" {...a11yProps(2)} /> */}
          </Tabs>
        </AppBar>
        <TabPanel value={value} index={0}>
          {
            <form onSubmit={handleSubmit}>
              <div
                style={{ flex: 1, overflowY: "auto", padding: "5px 20px 20px" }}
              >
                <FormControl style={{ ...styles.mt_3 }} fullWidth>
                  <TextField
                    fullWidth
                    label="ProjectTitle*"
                    value={projectTitle}
                    onChange={(e) => setProjectTitle(e.target.value)}
                    style={styles.mt_3}
                  />
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <InputLabel>ProjectType</InputLabel>
                  <Select
                    fullWidth
                    value={projectType}
                    onChange={(e) => {
                      setProjectType(e.target.value);
                    }}
                    onClick={() => {
                      GetProjectTypes().then((res) => {
                        if (res.data) {
                          setProjectTypes(res.data);
                        }
                      });
                    }}
                  >
                    {projectTypes.map((prioriy) => (
                      <MenuItem value={prioriy.ID} id={prioriy.ID}>
                        {prioriy.ProjectType}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <TextField
                    fullWidth
                    label="Project Description*"
                    value={projectDescription}
                    onChange={(e) => setProjectDescription(e.target.value)}
                    style={styles.mt_3}
                  />
                </FormControl>
                <FormControl style={{ ...styles.mt_3 }} fullWidth>
                  <InputLabel>FundType</InputLabel>
                  <Select
                    fullWidth
                    value={fundType}
                    onChange={(e) => setFundType(e.target.value)}
                    onClick={() => {
                      GetFundTypes().then((res) => {
                        if (res.data) {
                          setFundTypes(res.data);
                        }
                      });
                    }}
                  >
                    {fundTypes.map((prioriy) => (
                      <MenuItem value={prioriy.ID} id={prioriy.ID}>
                        {prioriy.FundType}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <InputLabel>ContractType</InputLabel>
                  <Select
                    fullWidth
                    value={contractType}
                    onChange={(e) => {
                      setContractType(e.target.value);
                    }}
                    onClick={() => {
                      GetContractType().then((res) => {
                        if (res.data) {
                          setContractTypes(res.data);
                        }
                      });
                    }}
                  >
                    {contractTypes.map((prioriy) => (
                      <MenuItem value={prioriy.ID} id={prioriy.ID}>
                        {prioriy.ContractType}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <TextField
                    fullWidth
                    label="Design Fees*"
                    value={designFees}
                    onChange={(e) => {
                      setDesignFees(e.target.value);
                    }}
                    style={styles.mt_3}
                  />
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <TextField
                    fullWidth
                    label="Supervision Fees*"
                    value={supervision}
                    onChange={(e) => {
                      setSupervision(e.target.value);
                    }}
                    style={styles.mt_3}
                  />
                </FormControl>
                <FormControl
                  className={classes.formControl}
                  style={{ ...styles.mt_3 }}
                  fullWidth
                >
                  <TextField
                    fullWidth
                    label="Contractor Fees*"
                    value={contractor}
                    onChange={(e) => {
                      setContractor(e.target.value);
                    }}
                    style={styles.m_3}
                  />
                  <DatePicker
                    autoOk
                    label="Start Date"
                    clearable
                    format="dd/MM/yyyy"
                    value={startDate}
                    onChange={(date) => setStartDate(date)}
                  />
                  <DatePicker
                    autoOk
                    label="End Date"
                    clearable
                    format="dd/MM/yyyy"
                    // disablePast
                    minDate={moment(new Date()).add('day',1)}
                    value={endDate}
                    onChange={(date) => setEndDate(date)}
                  />
                </FormControl>
                <PaperDropzone
                  title="SignedProjectConditions"
                  setImageData={setAttach}
                  data={attach}
                  type={[
                    ".dxf",
                    ".dif",
                    ".pdf",
                    ".png",
                    ".jpeg",
                    ".doc",
                    ".docx",
                  ]}
                />
                <PaperDropzone
                  title="SignedAuthorizationLetter"
                  setImageData={setConditions}
                  data={conditions}
                  type={[
                    ".dxf",
                    ".dif",
                    ".pdf",
                    ".png",
                    ".jpeg",
                    ".doc",
                    ".docx",
                  ]}
                />
                <PaperDropzone
                  title="AgreedInitialDesign"
                  setImageData={setAttachment}
                  data={attachment}
                  type={[
                    ".dxf",
                    ".dif",
                    ".pdf",
                    ".png",
                    ".jpeg",
                    ".doc",
                    ".docx",
                  ]}
                />
                <Button variant="contained" color="primary" type="submit">
                  Submit
                </Button>
              </div>
            </form>
          }
        </TabPanel>
        <TabPanel value={value} index={1} disabled>
          {
            <div>
              {Roles && Roles.length > 0 && (
                <Paper elevation={3}>
                  <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                      <TableRow>
                        {/* <TableCell align="left">
                  <Checkbox
                         checked={selectAll}
                         onChange={(e) =>{
                           if(e.target.checked){
                             assign.filter((as) =>{
                              setSelected([...selectedID,as.User])
                              setSelectAll(true)
                             })
                           }else{
                            setSelected([])
                            setSelectAll(false)
                           }
                         }} 
                />
                </TableCell> */}
                        <TableCell className={classes.CellHead} align="left">
                          Roles
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          Users
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {Roles &&
                        Roles.map((val, i) => {
                          return (
                            <TableRow>
                              {/* <TableCell align="left">
                  <Checkbox 
                   checked={ selectedID.filter(
                     (sel) => sel === val.User 
                     ).length > 0
                      ? true
                      : false
                     }
                    onChange={(e) =>{
                      if(e.target.checked){
                        setSelected([...selectedID,val.User])
                      }else{
                        setSelected(selectedID.filter((fil) =>{
                          return fil !== val.User
                        }))
                      }
                    }}               
                   /></TableCell> */}
                              {/* <TableCell align="left"> 
                     <IconButton onClick={()=>{
                       let theNew = assign.filter((vals) =>{
                         if(vals.Role !== val.Role){
                           return vals
                         }
                       })
                       setAssign(theNew)
                     }}>
                       <DeleteIcon />
                     </IconButton>  
                   </TableCell> */}
                              <TableCell align="left">
                                <h3>{val.RoleName}</h3>
                              </TableCell>
                              <TableCell align="left">
                                <FormControl
                                  variant="filled"
                                  fullWidth
                                  className={classes.formControlDrop}
                                >
                                  <InputLabel id="demo-simple-select-filled-label">
                                    Select a {val.RoleName}
                                  </InputLabel>
                                  <Select
                                    labelId="demo-simple-select-filled-label"
                                    id="demo-simple-select-filled"
                                    value={dropDownVal[i]}
                                    onChange={(e) => {
                                      let newArr = dropDownVal;
                                      newArr[i] = e.target.value;
                                      setDropDownVal(newArr);
                                      HandleRoleAdd(e, val);
                                    }}
                                  >
                                    <MenuItem value="">
                                      <em>None</em>
                                    </MenuItem>
                                    {UsersToRole &&
                                      UsersToRole.map((see) => {
                                        if (
                                          see.RoleName.includes(val.RoleName)
                                        ) {
                                          return (
                                            <MenuItem value={see.ID}>
                                              {see.Name}
                                            </MenuItem>
                                          );
                                        }
                                      })}
                                  </Select>
                                </FormControl>
                              </TableCell>
                            </TableRow>
                          );
                        })}
                    </TableBody>
                  </Table>
                </Paper>
              )}
              <Button
                variant="contained"
                color="primary"
                type="submit"
                style={{ marginTop: "10px", marginBottom: "10px" }}
                onClick={(e) => {
                  handleAssignment(e);
                }}
              >
                Assign It
              </Button>
            </div>
          }
        </TabPanel>
      </div>
    </div>
  );
}

export default FormforVisit;
