import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  IconButton,
  Paper,
  TextField,
  Button,
  makeStyles,
  TableRow,
  TableCell,
  TableHead,
  TableBody,
  TableFooter,
  CircularProgress,
} from "@material-ui/core";
// import { Table } from '../../components/Table';
import Alert from "../../components/Alert";
import { ArrowBackTwoTone, ArrowLeft, ArrowRight } from "@material-ui/icons";
import {
  GetAllProjects,
  GetAllRoleTypes,
  HandleTaskActivity,
  TasksDelete,
} from "../../server";
import { styles } from "../../styles";
import {
  activity_logs_module_columns,
  project_activity_columns,
  work_flow_columns,
} from "../../server/columns";
import { GetLogs } from "../../server/logs";
import Axios from "axios";
import { api } from "../../api/api";
import { Table } from "../../components/Table";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "90%",
    overflowX: "auto",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    paddingTop: "50px",
    flexDirection: "column",
    margin: "auto",
  },
  table: {
    minWidth: "auto",
  },
  paper: {
    width: "90%",
    marginBottom: theme.spacing(2),
  },

  tableWrapper: {
    overflowX: "auto",
    paddingLeft: "20px",
    paddingRight: "20px",
  },
  visuallyHidden: {
    border: 0,
    clip: "rect(0 0 0 0)",
    height: 1,
    margin: -1,
    overflow: "hidden",
    padding: 0,
    position: "absolute",
    top: 20,
    width: 1,
  },
}));

function ActivityLogs(props) {
  const classes = useStyles();
  const { id } = props.match.params;
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  const [roles, setRoles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [description, setDescription] = useState("");

  const [success, setSuccess] = useState("");
  const [loadingOnSave, setLoadingOnSave] = useState(false);

  const [page, setPage] = useState(0);
  const [total, setTotal] = useState(0);

  const [page2, setPage2] = useState(0);
  const [total2, setTotal2] = useState(0);

  const [projectAData, setProjectAData] = useState(null);
  const [projectRecord, setProjectRecord] = useState(0);
  const [projectPage, setProjectPage] = useState(0);
  const [projectLoading, setProjectLoading] = useState(true);
  const [projectError, setProjectError] = useState("");

  const [taskActivities, setTaskActivities] = useState(null);
  const [workFlowRecord, setworkFlowRecord] = useState(0);
  const [workFlowPage, setWorkFlowPage] = useState(0);
  const [workFlowLoading, setWorkFlowLoading] = useState(false);
  const [workFlowError, setWorkFlowError] = useState("");

  const [AddBtn, setAddBtn] = useState(false);
  const [EditBtn, setEditBtn] = useState(false);
  const [ViewBtn, setViewBtn] = useState(false);
  const [DeleteBtn, setDeleteBtn] = useState(false);

  const UserRole = useSelector((user) => user.userReducer.UserRole);
  const FinalRoles = useSelector((user) => user.userReducer.FinalRoles);
  const Module = useSelector((user) => user.userReducer.Module);

  useEffect(() => {
    dispatch(Action2.setModule("Activity Log"));
  }, []);

  useEffect(() => {
    setAccess();
  }, [Module, UserRole]);

  function setAccess() {
    FinalRoles.map((val) => {
      if (val.ModuleName === Module) {
        if (parseInt(localStorage.getItem("RoleID")) === val.RoleTypeId) {
          if (val.Action === "ADD") {
            setAddBtn(!val.IsAllow);
          }
        }
      }
    });
  }

  useEffect(() => {
    dispatch(Action2.setModule("Activity Log"));
  }, []);

  useEffect(() => {
    apiCall(projectPage);
    apiCallActivity(workFlowPage);
  }, [projectPage, workFlowPage]);

  async function apiCall(newPage) {
    let data = await Axios.post(api + "/GetProjectTaskByProjectID", {
      nProjectID: localStorage.getItem("ProjectIDssss"),
      nTaskType: "",
      nStatus: "",
      nOrderBy: "ID",
      nPageIndex: newPage,
      nPageSize: 10,
    });
    if (data.data.result) {
      setProjectAData(data.data.ProjectTasksData);
      setProjectRecord(data.data.TotalCount);
      setProjectLoading(false);
    } else {
      setProjectError("Their is an error");
      setProjectLoading(false);
    }
  }

  async function apiCallActivity() {
    let data = await Axios.post(api + "/Get_TaskActivity_Pagged", {
      nPortalID: 2,
      nProjectTaskID: null,
      nAssignTo: null,
      nAssignedBy: null,
      nTaskActivityStatus: "",
      nOrderBy: "ID",
      nPageIndex: workFlowPage || 0,
      nPageSize: 10,
    });

    if (data.data.result) {
      console.log("data.data.TaskActivityArray",data.data)
      setTaskActivities(data.data.TaskActivityArray);
      setworkFlowRecord(data.data.TotalCount);
      setWorkFlowLoading(false);
    } else {
      setWorkFlowError("There is an Error");
      setProjectLoading(false);
    }
  }

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetLogs(id)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.TasksActivityLogData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetLogs(id)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.TasksActivityLogData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllRoleTypes()
      .then((res) => {
        let Roles = res.data.RolesArray;
        setRoles(Roles);
      })
      .catch((err) => setError("Error in role types"));
  }, []);

  const reloadContentProject = () => {
    setProjectAData([]);
    setProjectLoading(true);
    setProjectError("");
    apiCall();
  };

  const changePageProject = (newPage) => {
    setProjectPage(newPage);
    setProjectLoading(true);
    setProjectAData([]);
    apiCall(newPage);
  };

  const reloadWorkFlowProject = () => {
    setTaskActivities([]);
    setWorkFlowLoading(true);
    setWorkFlowError("");
    apiCallActivity();
  };

  const changeWorkFlowProject = (newPage) => {
    setWorkFlowPage(newPage);
    setWorkFlowLoading(true);
    setTaskActivities([]);
    apiCallActivity(newPage);
  };

  function handleAddingTask() {
    let params = { description };
    if (!description) {
      return setError("Add a Description");
    }
    HandleTaskActivity(params)
      .then((res) => {
        let result = res.data.Result.result;
        if (result === true) {
          localStorage.setItem("ModuleIDSub", res.data.Result.nModuleID);
          let title = "Task";
          setSuccess(`${title} Added successfully`);
          setDescription("");
          apiCall(projectPage);
        } else {
          setError(res.data.Result.message);
          setDescription("");
          setLoadingOnSave(false);
        }
      })
      .catch((err) => {
        setDescription("");
        setError(err.message);
        setLoadingOnSave(false);
      });
  }
  function handleRemove(e, id) {
    e.preventDefault();
    TasksDelete(id).then((res) => {
      if (res.data.Result.result === true) {
        let title = "Task";
        setSuccess(`${title} Deleted successfully`);
        setLoadingOnSave(false);
      } else {
        setError(res.data.Result.message);
        setLoadingOnSave(false);
      }
    });
  }

  function handleEdit(e) {
    e.preventDefault();
  }
  return (
    <Grow in={true}>
      <div>
        <Alert
          open={success.length > 0}
          type="success"
          message={success}
          onClose={() => {
            setSuccess("");
          }}
        />

        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <IconButton
            style={{ ...styles.mr_2 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone />
          </IconButton>
          Projects
        </Typography>
        <div
          style={{
            width: "100%",
            height: "auto",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div
            style={{
              width: "90%",
              display: "flex",
              flexDirection: "row",
              paddingTop: "5px",
            }}
          >
            <h3 style={{ marginRight: "5px" }}>Project Status</h3>
            <div
              style={{
                overflow: "hidden",
                borderRadius: "5px",
                height: "40px",
                width: "1000px",
                display: "flex",
                justifyContent: "space-around",
                marginRight: "10px",
                alignItems: "center",
              }}
            >
              <span
                style={{
                  background: "#0BD318",
                  flex: 1,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "40px",
                }}
              >
                New Visit
              </span>
              <span
                style={{
                  background: "#0BD318",
                  flex: 1,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "40px",
                }}
              >
                Follow up
              </span>
              <span
                style={{
                  background: "#0BD318",
                  flex: 1,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "40px",
                }}
              >
                Project
              </span>
              <span
                style={{
                  flex: 1,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "40px",
                }}
              >
                role4
              </span>
            </div>
          </div>
          <div></div>
          <div style={{ width: "90%", display: "flex", flexDirection: "row" }}>
            <h3 style={{ marginRight: "20px" }}>Remarks</h3>
            <TextField
              style={{ width: "80%", marginTop: "20px" }}
              multiline
              rows={5}
              variant="outlined"
              fullwidth
              value={description}
              onChange={(e) => {
                setDescription(e.target.value);
              }}
              // label="Description"
            />
          </div>
          <div
            style={{
              width: "90%",
              display: "flex",
              flexDirection: "row",
              marginTop: "20px",
            }}
          >
            <div style={{ width: "80%" }}></div>
            <Button
              style={{
                justifySelf: "flex-end",
                background: AddBtn ? "#E8E6E6" : "#ff8800",
                color: "white",
              }}
              variant="contained"
              onClick={() => {
                handleAddingTask();
              }}
              disabled={AddBtn}
            >
              Add
            </Button>
          </div>
        </div>
        <div className={classes.root}>
          <Grid container>
            <Grid item xs={12}>
              <Table
                title="Project Activity"
                columns={project_activity_columns}
                data={projectAData}
                isLoading={projectLoading}
                error={error}
                onChangePage={changePageProject}
                onSuccess={reloadContentProject}
                page={projectPage}
                totalRecords={projectRecord}
                // onRefresh={reloadContentProject}
                // nullSelected={nullSelected}
                variant={60}
              />
            </Grid>
          </Grid>
        </div>

        <div className={classes.root}>
          <Grid container>
            <Grid item xs={12}>
              {console.log("workFlowRecord => ", workFlowRecord, taskActivities)}
              <Table
                title="Work Flow Activities"
                columns={work_flow_columns}
                data={taskActivities}
                isLoading={workFlowLoading}
                error={workFlowError}
                onChangePage={changeWorkFlowProject}
                onSuccess={reloadWorkFlowProject}
                page={workFlowPage}
                totalRecords={workFlowRecord}
                // onRefresh={reloadContentForCheckDetails}
                nullSelected={nullSelected}
                variant={61}
              />
            </Grid>
          </Grid>

          {/* <Paper className={classes.paper}>
            <div
              style={{
                width: "100%",
                padding: "20px 20px",
                height: "100px",
                background: "#287DB3"
              }}
            >
              <h1 style={{ color: "white", marginTop: "0" }}>
                WorkFlow Activities
              </h1>
            </div>
            <Table className={classes.table} size={"medium"}>
              <TableHead>
                <TableRow style={{ height: "100px" }} selected>
                  <TableCell
                    style={{
                      fontSize: "22px",
                      fontWeight: "bold",
                      paddingLeft: "10px"
                    }}
                    align={"left"}
                    padding={"none"}
                  >
                    S/N
                  </TableCell>
                  <TableCell
                    style={{ fontSize: "22px", fontWeight: "bold" }}
                    align={"left"}
                  >
                    Action Taken By
                  </TableCell>
                  <TableCell
                    style={{ fontSize: "22px", fontWeight: "bold" }}
                    align={"left"}
                  >
                    Action
                  </TableCell>
                  <TableCell
                    style={{ fontSize: "22px", fontWeight: "bold" }}
                    align={"left"}
                  >
                    Assignee
                  </TableCell>
                  <TableCell
                    style={{ fontSize: "22px", fontWeight: "bold" }}
                    align={"left"}
                  >
                    Role
                  </TableCell>
                  <TableCell
                    style={{ fontSize: "22px", fontWeight: "bold" }}
                    align={"left"}
                  >
                    Start Date/Time
                  </TableCell>
                  <TableCell
                    style={{
                      fontSize: "22px",
                      fontWeight: "bold",
                      paddingRight: "15px"
                    }}
                    align={"left"}
                  >
                    End Date/Time
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {taskActivities &&
                  taskActivities.map(value => {
                    return (
                      <TableRow style={{ height: "100px" }}>
                        <TableCell
                          style={{ fontSize: "22px", paddingLeft: "10px" }}
                          align={"left"}
                          padding={"none"}
                        >
                          {value.ID}
                        </TableCell>
                        <TableCell style={{ fontSize: "22px" }} align={"left"}>
                          {value.AssignedByName}
                        </TableCell>
                        <TableCell style={{ fontSize: "22px" }} align={"left"}>
                          {value.TaskAction}
                        </TableCell>
                        <TableCell style={{ fontSize: "22px" }} align={"left"}>
                          {value.AssignToName}
                        </TableCell>
                        <TableCell style={{ fontSize: "22px" }} align={"left"}>
                          Role
                        </TableCell>
                        <TableCell style={{ fontSize: "22px" }} align={"left"}>
                          {value.ActionDate}
                        </TableCell>
                        <TableCell
                          style={{
                            fontSize: "22px",
                            fontWeight: "bold",
                            paddingRight: "15px"
                          }}
                          align={"left"}
                        >
                          {value.DueDate}
                        </TableCell>
                      </TableRow>
                    );
                  })}
              </TableBody>
              <TableFooter>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell>
                    <span>showing 1-1 of 1</span>
                    <ArrowLeft
                      onClick={() => {
                        if (page2 !== 0) {
                          setPage2(page2 - 1);
                        }
                      }}
                    />
                    <ArrowRight
                      onClick={() => {
                        if (page2 < total2) {
                          setPage2(page2 + 1);
                        }
                      }}
                    />
                  </TableCell>
                </TableRow>
              </TableFooter>
            </Table>
          </Paper> */}
        </div>

        {/* <Grid container>
          <Grid item xs={12}>
            <Table
              title="Activity Logs"
              columns={activity_logs_module_columns}
              data={data}
              roles={roles}
              isLoading={loading}
              error={error}
              onChangePage={page => this.changePage(page)}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={8}
            />
          </Grid>
        </Grid> */}
      </div>
    </Grow>
  );
}

export default withRouter(ActivityLogs);
