import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red, lightGreen, blue } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { Send, Receipt, AssignmentTurnedIn } from '@material-ui/icons';
import { styles } from '../../styles';

const useStyles = makeStyles(theme => ({
  media: {
    height: 0,
    paddingTop: '56.25%' // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: 'rotate(180deg)'
  },
  avatar1: {
    backgroundColor: lightGreen[500]
  },
  avatar2: {
    backgroundColor: blue[500]
  },
  avatar3: {
    backgroundColor: red[500]
  }
}));

export default function RecipeReviewCard(props) {
  const { variant } = props;
  const icon =
    variant === 'sent' ? (
      <Send />
    ) : variant === 'received' ? (
      <Receipt />
    ) : (
      variant === 'project' && <AssignmentTurnedIn />
    );
  const text =
    variant === 'sent'
      ? `This impressive feature allows you to redirect to the notifications
      that you were able to sent successfully!`
      : variant === 'received'
      ? `This impressive feature allows you to redirect to the notifications
     that you were received from others!`
      : variant === 'project' &&
        `This impressive feature allows you to redirect to the notifications
      that you received for your specific project!`;
  const src =
    variant === 'sent'
      ? 'https://d241gzwmzya7ka.cloudfront.net/uploads/images/Blog-Image-2017-08-01@2x.jpg'
      : variant === 'received'
      ? 'https://kinsta.com/wp-content/uploads/2016/10/free-smtp-server-2019.jpg'
      : variant === 'project' &&
        'https://img.freepik.com/free-vector/light-blue-project-management-concept_23-2147782704.jpg?size=338&ext=jpg';
  const classes = useStyles();
  const [entered, setEntered] = React.useState(false);

  function handleEnter() {
    setEntered(true);
  }
  function handleLeave() {
    setEntered(false);
  }

  return (
    <Link to={`/notifications/${variant}`} style={styles.link}>
      <Card
        className={classes.card}
        onMouseEnter={handleEnter}
        onMouseLeave={handleLeave}
        elevation={entered ? 11 : 1}
      >
        <CardHeader
          avatar={
            <Avatar
              aria-label="recipe"
              className={
                variant === 'sent'
                  ? classes.avatar1
                  : variant === 'received'
                  ? classes.avatar2
                  : variant === 'project' && classes.avatar3
              }
            >
              {icon}
            </Avatar>
          }
          title={`Visit ${variant} notifications`}
          subheader={`Today: ${
            new Date(Date.now()).toString().split('2019')[0]
          }`}
        />
        <CardMedia className={classes.media} image={src} />
        <CardContent>
          <Typography variant="body2" color="textSecondary" component="p">
            {text}
          </Typography>
        </CardContent>
      </Card>
    </Link>
  );
}
