import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import EmployeesPerformance from './EmployeesPerformance';
// import ProjectStatistics from './ProjectStatistics';
import PaymentDetails from './PaymentDetails';
import ExpenseDetails from './ExpenseDetails'

// TAB PANEL COMPONENT
function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-auto-tabpanel-${index}`}
            aria-labelledby={`scrollable-auto-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `scrollable-auto-tab-${index}`,
        'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
}));


const GeneralReportsApp = () => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={`${classes.root} Checking`}>
            <AppBar position="static" color="default">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="scrollable"
                    scrollButtons="auto"
                    aria-label="scrollable auto tabs example"
                >
                    <Tab label="Employees performance" {...a11yProps(0)} />
                    <Tab label="Payment details" {...a11yProps(1)} />
                    <Tab label="Expenses details" {...a11yProps(2)} />
                    {/* <Tab label="Project statistics" {...a11yProps(3)} /> */}
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <EmployeesPerformance />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <PaymentDetails />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <ExpenseDetails />
            </TabPanel>

            {/* <TabPanel value={value} index={3}>
                <ProjectStatistics />
            </TabPanel> */}

        </div>
    );
}

export default GeneralReportsApp;

