import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";

export function GetAllSubmissions(page) {
  let link = `${api}/GetAllSubmissions`;

  let params = {
    nkeywordTitle: "",
    nSubmittedTo: "",
    nSubmittedBy: "",
    nOrderBy: "ID",
    nPageIndex: page,
    nPageSize: 10
  };

  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleSubmission(parameters) {
  const {
    submittedTo,
    documentDescription,
    projectID,
    id,
    isNew,
    receivedDate,
    submissionDate,
    submissionDoc,
    submissionDocExt,
    receivedDoc,
    receivedDocExt,
    notes
  } = parameters;
  const { UserID } = getVitals();
  let params = {
    nIsNew: isNew,
    nPortalID: 2,
    nCurrentID: isNew ? -1 : id,
    nProjectID: localStorage.getItem("ProjectIDssss"),
    oDocumentDescription: documentDescription,
    nSubmittedTo: submittedTo,
    nSubmissionDate: submissionDate,
    oSubmittedDocs: submissionDoc,
    oSubmittedDocsExtension: submissionDocExt,
    nRecievedDate: receivedDate,
    oRecievedDocs: receivedDoc,
    oRecievedDocsExtension: receivedDocExt,
    oNotes: notes,
    nSubmittedBy: UserID
  };

  let link = `${api}/SaveSubmissions`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
