export function MultipleSubmissionsDelete(rows) {
 
  let params = {
    nSubmissionID: rows.ID.toString(),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeleteProjectSubmissions`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
