import Axios from "axios";
import { api } from "../api/api";
// import { getVitals } from "./utils";

export function GetProjectTaskByProjectId(parameters) {
  const { page, nProjectID, nTaskType } = parameters;
  let link = `${api}/GetProjectTaskByProjectID`;

  let params = {
    nProjectID: nProjectID,
    nTaskType: nTaskType,
    nStatus: "",
    nOrderBy: "ID",
    nPageIndex: page,
    nPageSize: 10,
  };

  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function handleDeleteProjectTaskByProjectId(row) {
  const { ID } = row;
  let link = `${api}/DeleteProjectTask`;
  let params = {
    nProjectTaskID: ID,
    nPortalID: 2,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function handleSaveProjectTask(parameters) {
  const {
    nModifiedBy,
    nProjectID,
    nStatus,
    oRemarks,
    nTaskType,
    nAttachments,
  } = parameters;
  let link = `${api}/SaveProjectTask`;
  let params = {
    nProjectID: localStorage.getItem("ProjectIDssss"),
    nTaskType: nTaskType,
    nStatus: nStatus,
    nStartedDate: "2019-08-01T13:14:16.8346929+05:00",
    nEndDate: "2019-08-01T13:14:16.8346929+05:00",
    nCreatedBy: localStorage.getItem("name") || "sample string 6",
    nCreatedDate: "2019-08-01T13:14:16.8346929+05:00",
    nModifiedBy: nModifiedBy,
    nModifiedDate: "2019-08-01T13:14:16.8356915+05:00",
    nIsDeleted: false,
    oRemarks: oRemarks,
    nCurrentID: -1,
    nPortalID: 2,
    isNew: true,
    nAttachments: nAttachments || [],
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}
