import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { Grow, Typography, Grid, Button } from '@material-ui/core';
import { ArrowBackTwoTone } from '@material-ui/icons';
import IconButton from '@material-ui/icons/KeyboardReturn';
import { Table } from '../../components/Table';
import { users_module_columns } from '../../server/columns';
import { GetUsersByGroupID, GetAllGroups } from '../../server/security_module';
import { styles } from '../../styles';

function Users(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [groups, setGroups] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [nullSelected, setNullSelected] = useState(false);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError('');
    GetUsersByGroupID(id)
      .then(res => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.UsersData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetUsersByGroupID(id)
      .then(res => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.UsersData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllGroups()
      .then(res => {
        let data = res.data.Result.GroupsData || [];
        setGroups(data);
      })
      .catch(error => {
        console.log('Error fetching groups...');
      });
  }, []);

  return (
    <Grow in={true}>
      <div>
        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <Button
          variant="contained" style={{ background: '#2f3b7d', color: '#fff', marginRight: '10px' }} 
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone style={{ marginRight: '7px' }} />
            Users
          </Button>
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title="Users listing"
              columns={users_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={page => this.changePage(page)}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={2}
              groups={groups}
              setData={setData}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Users);


