import axios from "axios";
import { URL } from '../../BaseUrl';

export const GET_USER_BY_ID = "GET_USER_BY_ID";
export const GET_ROLE_LOGGED_USER = "GET_ROLE_LOGGED_USER";
export const GET_ALL_RULES = "GET_ALL_RULES";
export const SET_MODULE = "SET_MODULE";

export const getUserID = () => async (dispatch) => {
   let body = {
    "nPortalID": 2,
    "nID": localStorage.getItem("userID")
  }
    let res = await axios.post(URL + "/Api/GetUserByID",body);

  dispatch({
    type: GET_USER_BY_ID,
    payload: res.data.Result.UsersData
  });
};

export const getUsersRole = (id) => async (dispatch) => {
    let body = {
        ID: id,
        PortalID: 2
      }
     let res = await axios.post(URL + "/Api/GetRolesByID",body);
 
   dispatch({
     type: GET_ROLE_LOGGED_USER,
     payload: res.data.RolesArray
   });
 };

 export const getAllRoles = () => async (dispatch) => {
  
  let res = await axios.post(URL +  '/api/GetAllRules')
  dispatch({
    type: GET_ALL_RULES,
    payload: res.data
  });
 }

 export const setModule = (mod) => (dispatch) => {
  dispatch({
    type: SET_MODULE,
    payload: mod
  })
 }


