import { GET_DATA } from "./reducerdata";
import { GetAllPaymentCheques } from "../server";

export const getCheque = () => async (dispatch) => {
  await GetAllPaymentCheques().then((res) => {
    if (res.data.result) {
      dispatch({
        type: GET_DATA,
        payload: res.data.PaymentChecqueData,
      });
    }
  });
};
