export function GetMyTaskPaged(page, UserID, size, projectID,Sparams
) {
  
  let link = `${api}/GetProjectWorkFlow_Pagged`;

  let body;
  if (Sparams) {
    const { AssignBy, Status, nKeywordTitle, Location, nID } = Sparams;
    body = {
      nKeywordTitle: nKeywordTitle,
      nID: nID,
      ProjectID: projectID,
      Status: Status,
      AssignBy: AssignBy,
      AssignTo: UserID,
      OrderBy: "",
      PageIndex: page,
      Location,
      PageSize: size || 10,
      Description: "",
    };
  } else {
    body = {
      nKeywordTitle: "",
      nID: null,
      ProjectID: projectID,
      Status: null,
      AssignBy: null,
      AssignTo: UserID,
      OrderBy: "",
      PageIndex: page,
      PageSize: size || 10,
      Description: "",
      Location : ""
    };
  }

  

  let params = {
    nClientVisitID: "",
    nVisitType: "",
    nTaskType: "",
    nAssignBy: null, // loged in user ki id pas kini hy
    nAssignTo: UserID,
    nStatus: null,
    nPortalID: 2,
    nOrderBy: "ID DESC",
    nTaskID: null,
    nLocation: "",
    nPageIndex: page,
    nPageSize: 10,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, body)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function GetMyTasksById(id) {
  let link = `${api}/Get_TaskActivityBy_TaskActivityID`;
  let params = { ID: id, PortalID: 2 };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

