import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";
import { URL } from "../BaseUrl";

export function HandleProject(parameters, ID) {
  const {
    projectTitle,
    projectType,
    projectDescription,
    fundType,
    contractType,
    designFees,
    supervision,
    contractor,
    startDate,
    endDate,
    attach,
    conditions,
    attachment,
    id,
    proStatus,
    plot,
    clientID,
    BlockNo,
    VisitNumber,
    Area,
  } = parameters;
  const { Name } = getVitals();
  let params = {
    nArea: Area,
    oBlockNo: BlockNo,
    nVisitNumber: VisitNumber,
    ProjectTitle: projectTitle,
    nProjectType: projectType,
    nDescription: projectDescription,
    nFundType: fundType,
    nContractType: contractType,
    nDesignFees: designFees,
    nSuperVisionFees: supervision,
    nContractorFees: contractor,
    nClientID: clientID,
    nClientVisitID: ID,
    nCreatedBy: Name,
    nCurrentID: -1,
    // isNew ? -1 : currentID,
    nStartDate: startDate.toISOString(),
    nEndDate: endDate.toISOString(),
    nPlotNumber: plot,
    nStatus: proStatus,
    nPortalID: 2,
    isNew: true,
    nStage: 3,
    SignedProjectConditions: attach,
    SignedAuthorizationLetter: conditions,
    AgreedInitialDesign: attachment,
  };

  let link = `${api}/SaveProject`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

