export function GetAllProjects(
  page,
  id,
  site,
  Cid,
  plot,
  status,
  startDate,
  endDate,
  SIZE
) {
  let link = id ? `${api}/GetAllProjects` : `${api}/GetAllProjects`;
  let params = {
    nKeywordTitle: "",
    nProjectType: "",
    nProjectID: id || null,
    nClientID: Cid || null,
    nDateFrom: startDate || "",
    nDateTo: endDate || "",
    nContractType: "",
    nMobileNumber: "",
    nPlotNo: plot || "",
    nStatus: status || null,
    nOrderBy: "A.ProjectTitle",
    nPageIndex: page || 0,
    nPageSize: SIZE || 10,
  };

  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function MultipleProjectsDelete(ids) {
  let params = {
    nProjectID: ids.toString(),
    // nProjectID: parseInt(ids, 10),
    nPortalID: 2,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(`${api}/DeleteProject`, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

