import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Alert from '../../Alert';
import DialogTitle from '@material-ui/core/DialogTitle';
import {
  MultipleStatusChange,
  MultipleDelete,
  DeleteCategory,
  MultipleGroupStatusChange,
  MultipleGroupDelete,
  MultipleUserStatusChange,
  MultipleUsersDelete,
  MultipleClientsDelete,
  MultipleProjectsDelete,
  MultipleVisitsDelete,
  MultipleTasksDelete,
  MultipleExpenseDelete,
  MultiplePaymentsDelete,
  MultipleVouchersDelete,
  MultipleSubmissionsDelete,
  MultipleNotificationsDelete
} from '../../../server';
import { CircularProgress } from '@material-ui/core';
import { styles } from '../../../styles';
import { MultipleLogsDelete } from '../../../server/logs';

export default function AlertDialog(props) {
  const { open, onClose, type, ids, uniqueName, variant } = props;
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  let Title =
    type === 'publish'
      ? variant == 2
        ? 'Are you sure you want to activate the selected users?'
        : 'Are you sure you want to publish the selected items?'
      : type === 'un-publish'
      ? variant == 2
        ? 'Are you sure you want to inactivate the selected users?'
        : 'Are you sure you want to un-publish the selected items?'
      : (type === 'delete' || type === 'deleteCat') &&
        'Are you sure you want to delete the selected items?';
  let Confirm =
    type === 'publish'
      ? variant == 2
        ? 'Activate'
        : 'Publish'
      : type === 'un-publish'
      ? variant == 2
        ? 'Inactivate'
        : 'Un-publish'
      : (type === 'delete' || type === 'deleteCat') && 'Delete';

  function handleClose(act) {
    setLoading(true);
    if (act === 1) {
      let items = ids && ids.toString();
      let PublishAction =
        variant === 1
          ? MultipleGroupStatusChange
          : variant === 2
          ? MultipleUserStatusChange
          : MultipleStatusChange;
      let DeleteAction =
        variant === 1
          ? MultipleGroupDelete
          : variant === 2
          ? MultipleUsersDelete
          : variant === 3
          ? MultipleClientsDelete
          : variant === 4
          ? MultipleProjectsDelete
          : variant === 5
          ? MultipleVisitsDelete
          : variant === 8
          ? MultipleLogsDelete
          : variant === 7
          ? MultipleTasksDelete
          : variant === 12
          ? MultipleExpenseDelete
          : variant === 13
          ? MultiplePaymentsDelete
          : variant === 14
          ? MultipleVouchersDelete
          : variant === 15
          ? MultipleSubmissionsDelete
          : variant === 17
          ? MultipleSubmissionsDelete
          : variant === 16
          ? MultipleNotificationsDelete
          : MultipleDelete;
      if (type === 'publish') {
        PublishAction(items, true)
          .then(res => {
            setLoading(false);
            setSuccess('Items successfully published!');
            onClose[0](false);
            onClose[1]();
          })
          .catch(error => {
            let err = error.message;
            setLoading(false);
            setError(err);
          });
      } else if (type === 'un-publish') {
        PublishAction(items, false)
          .then(res => {
            setLoading(false);
            setSuccess('Items successfully un-published!');
            onClose[0](false);
            onClose[1]();
          })
          .catch(error => {
            let err = error.message;
            setLoading(false);
            setError(err);
          });
      } else if (type === 'deleteCat') {
        DeleteCategory(uniqueName)
          .then(res => {
            setLoading(false);
            setSuccess('Category deleted successfully!');
            onClose[0](false);
            onClose[1]();
          })
          .catch(error => {
            let err = error.message;
            setLoading(false);
            setError(err);
          });
      } else {
        DeleteAction(ids)
          .then(res => {
            setLoading(false);
            setSuccess('Items deleted successfully!');
            onClose[0](false);
            onClose[1]();
          })
          .catch(err => {
            let error = err.message;
            setLoading(false);
            setError(error);
          });
      }
    } else {
      onClose[0](false);
      setLoading(false);
    }
  }
  return (
    <>
      <Alert
        open={error.length > 0}
        type="error"
        message={error}
        onClose={() => {
          setError('');
        }}
      />
      <Alert
        open={success.length > 0}
        type="success"
        message={success}
        onClose={() => {
          setSuccess('');
        }}
      />
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        scroll="body"
      >
        <DialogTitle id="alert-dialog-title">{Title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Remember, once done, you'll might not be able to undo this action!
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => handleClose(0)}
            color="primary"
            disabled={loading}
          >
            Cancel
          </Button>
          <Button
            onClick={() => handleClose(1)}
            color="primary"
            autoFocus
            disabled={loading}
          >
            {Confirm}{' '}
            {loading && <CircularProgress size={25} style={styles.ml_4} />}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
