import pkjson from '../../package.json';
const getVitals = () => {
  let token = window.localStorage.getItem('accessToken');
  let username = window.localStorage.getItem('email');
  let language = window.localStorage.getItem('language');
  let Name = window.localStorage.getItem('name');
  let UserID = window.localStorage.getItem('userID');
  let websiteID = 1;
  return { token, username, language, Name, websiteID, UserID };
};
function GetJSDateFromMS(str) {
  let returnable = str.split('T')[0];
  return returnable;
}
function isValidEmail(email) {
  let regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  let testEmail = regex.test(email);
  return testEmail;
}
function getFileExtension(name) {
  let regex = /\.([0-9a-z]+)(?=[?#])|(\.)(?:[\w]+)$/gim;
  let ext = name.match(regex)[0];
  ext = ext.replace('.', '');

  return ext;
}
function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

const version = pkjson.version;

export {
  getBase64,
  getVitals,
  GetJSDateFromMS,
  version,
  isValidEmail,
  getFileExtension
};
