import React, { useEffect, useState } from "react";
import { useDropzone } from "react-dropzone";
import { Paper } from "@material-ui/core";
import { getBase64, getFileExtension } from "../../server/utils";
import { styles } from "../../styles";
import { FileCopy } from "@material-ui/icons";

const thumbsContainer = {
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  marginTop: 16,
  maxHeight: 125,
  maxWidth: 400,
  overflowY: "auto",
  justifyContent: "space-evenly"
};

const thumb = {
  display: "inline-flex",
  borderRadius: 2,
  border: "1px solid #eaeaea",
  marginBottom: 8,
  marginRight: 8,
  width: 100,
  height: 100,
  padding: 4,
  boxSizing: "border-box"
};

const thumbInner = {
  display: "flex",
  minWidth: 0,
  overflow: "hidden"
};

const img = {
  display: "block",
  width: "auto",
  height: "100%"
};

export default function Previews(props) {
  const { setImageData, data, type, multiple } = props;
  const [files, setFiles] = useState(type ? data : [{ preview: data }]);
  const [error, setError] = useState("");
  let accept = "image/*";
  if (type) {
    accept = type;
  }
  const { getRootProps, getInputProps } = useDropzone({
    accept: accept,
    multiple: multiple || false,
    onDrop: acceptedFiles => {
      setError("");
      let Files = [];
      let base64Code = !type
        ? getBase64(acceptedFiles[0]).then(res => {
            setImageData(
              res.split(",")[1],
              acceptedFiles[0].type.replace("image/", "")
            );
            setFiles(
              acceptedFiles.map((file, i) => {
                return {
                  preview: res.split(",")[1],
                  name: file.name
                };
              })
            );
          })
        : acceptedFiles.map(item => {
            let baseCode64 = getBase64(item).then(res =>
              Files.push({
                File: res.split(",")[1],
                FileExtenstion: getFileExtension(item.name),
                FileName: item.name,
                ID: 0
              })
            );
          });
      if (type) {
        setFiles(acceptedFiles);
        setImageData(Files);
      }
    },
    onDropRejected: () => {
      setImageData("");
      setError("Inappropriate filetype.!");
    }
  });

  const { title } = props;
  const thumbs = () => {
    return !type
      ? files.map(file => (
          <div style={thumb} key={file.name}>
            <div style={thumbInner}>
              <img
                alt="alt value"
                src={
                  accept === "video/*" && file.preview
                    ? "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSzBc_W2hyq20hAjc4oGv2gI8xQKdzTgeDqYhIk_QJk_o969unTXA"
                    : file.preview && `data:image/png;base64,${file.preview}`
                }
                style={img}
              />
            </div>
          </div>
        ))
      : files.map(file => (
          <div
            style={{
              ...styles.d_flex,
              ...styles.flex_column,
              ...styles.justify_center,
              ...styles.alignItems_center,
              ...styles.mb_3
            }}
          >
            <FileCopy />
            <div
              style={{
                ...styles.mr_2,
                maxWidth: 100,
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis"
              }}
            >
              {file.name || file.FileName}
            </div>
          </div>
        ));
  };

  // useEffect(
  //   () => () => {
  //     // Make sure to revoke the data uris to avoid memory leaks
  //     files.forEach(file => URL.revokeObjectURL(file.preview));
  //     ;
  //     setFiles([{ preview: data }]);
  //   },
  //   [files, data]
  // );

  return (
    <Paper style={styles.dropzone_paper} elevation={11}>
      <div {...getRootProps({ className: "dropzone" })}>
        <h4>{title}</h4>
        <input {...getInputProps()} />
        <p>Drag 'n' drop some files here, or click to select files</p>
        {error.length > 0 && <b style={styles.text_red}>{error}</b>}
      </div>
      {error.length === 0 && <aside style={thumbsContainer}>{thumbs()}</aside>}
    </Paper>
  );
}
