import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { visit_list_module_columns } from "../../server/columns";
import { GetClientVisitByClientID } from "../../server";
import SearchVisitListComponent from "./SearchVisitListComponent";
import { getVitals } from "../../server/utils";
import * as Actions from "../../redux/actions/index";
import { useDispatch, useSelector } from "react-redux";
import FullScreenModal from '../VisitDetailForm/FullScreenModal';
import ProjectEditForm from './ProjectEditForm'
import { URL } from "../../BaseUrl";
import Axios from 'axios'

function VisitList(props) {
  const dispatch = useDispatch();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const { UserID } = getVitals();

  const status = useSelector(
    (StatusReducer) => StatusReducer.StatusReducer.Dcc_Status
  );
  const [currentStatus, setCurrentStatus] = useState("");

  // All FullScreen Dialog Code
  const [dialogState, setDialogState] = useState(false);
  const [rowData, setRowData] = useState(null);
  const [projectFormData, setProjectFormData] = useState(null);

  const openDailogState = (data) => {
    setRowData(data)
    setDialogState(true);
  }

  const closeDailogState = () => {
    setDialogState(false)
  }


  const setProjectDataToFullScreenModal = (data) => {
    console.log("Data From Project",data)
  }

  // FullScreen Dialog Code End

  useEffect(() => {
    dispatch(Actions.setModule("Visit List"));
  }, []);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetClientVisitByClientID(page)
      .then((res) => {
        if (res.data.result === true) {
          let data = res.data.ClientVisitArray || [];
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetClientVisitByClientID(page)
      .then((res) => {
        if (res.data.result === true) {
          let data = res.data.ClientVisitArray || [];
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }, [status]);

  useEffect(() => {
    Actions.setStatus(status[1]);
    setCurrentStatus(status[1]);
  }, []);

  const changePage = (page) => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetClientVisitByClientID(page)
      .then((res) => {
        let data = res.data.ClientVisitArray || [];
        let count = res.data.TotalCount;
        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch((error) => {
        let err = error.message;
        setError(err);
        setLoading(false);
      });
  };

  // Get Project Form Data 

  React.useEffect(() => {
      const body = {
        nProjectID: rowData && rowData.ProjectID,
        nPortalID: 2
      }
      Axios.post(URL + "/api/GetProjectsByID", body).then((response) => {
        if (response.data.Result.result) {
          setProjectFormData(response.data.Result.ProjectsData[0])
        }
        
      })
    
  }, [rowData])


  // console.log("ROW DATA VISIT LIST",rowData)

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Visit List
        </Typography>
        <SearchVisitListComponent
          setData={setData}
          setLoading={setLoading}
          setCount={setCount}
          setError={setError}
        />
        <Grid container>
          <Grid item xs={12}>
            <Table
              title="Visit List"
              columns={visit_list_module_columns}
              data={data}
              setData={setData}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              onSuccess={reloadContent}
              page={page}
              totalRecords={count}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={20}
              projects={projects}
              size={10}
              onProjectEditOpenDialog={openDailogState}
            />
          </Grid>
        </Grid>
        <FullScreenModal isOpen={dialogState} appBarTitle="Edit Project" buttonTitle="Save" handleClose={closeDailogState} >

          <div style={{
            padding : 20
          }}>
            <ProjectEditForm projectRowData={rowData} projectFormData={projectFormData} reloadContent={reloadContent} closeDailogState={closeDailogState} />
          </div>


        </FullScreenModal>
      </div>
    </Grow>
  );
}

export default withRouter(VisitList);
