import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { visit_list_module_columns } from "../../server/columns";
import { GetClientVisitByClientID } from "../../server";
// import SearchVisitListComponent from "./SearchVisitListComponent";
import { getVitals } from "../../server/utils";
import FormforVisit from "./Form";

function VisitProjectForm(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const { UserID } = getVitals();

  console.log("History State => ", props.location.state && props.location.state.Remarks)

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetClientVisitByClientID(page)
      .then(res => {
        if (res.data.result === true) {
          let data = res.data.ClientVisitArray || [];
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetClientVisitByClientID(page)
      .then(res => {
        if (res.data.result === true) {
          let data = res.data.ClientVisitArray || [];
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }, []);

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetClientVisitByClientID(page)
      .then(res => {
        let data = res.data.ClientVisitArray || [];
        let count = res.data.TotalCount;
        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch(error => {
        let err = error.message;
        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        {/* <Typography variant="h6" gutterBottom>
          My Tasks
        </Typography> */}
        <Grid container>
          <Grid item xs={12}>
            <FormforVisit match={props.match} titleAndDescription={props.location.state && props.location.state.Remarks} formState={props.location.state} />
          </Grid>
        </Grid>
        {/* <SearchVisitListComponent /> */}
      </div>
    </Grow>
  );
}

export default withRouter(VisitProjectForm);
