import * as Actions from "./actions/index";

const initialState = {
  Dcc_Status: [],
  Current_Dcc_Status: ""
};

export const StatusReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.GET_STATUS: {
      return { ...state, Dcc_Status: action.payload };
    }

    case Actions.SET_STATUS: {
      return { ...state, Current_Dcc_Status: action.payload };
    }

    default:
      return state;
  }
};
export default StatusReducer;
