import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";

export function GetAllPaymentCheques(id, page, search) {
  let link = `${api}/GetAllPaymentChecque`;
  let params = {
    nChecqueNumber: "",
    nPaymentID: localStorage.getItem("PaymentDetails"),
    nOrderBy: "ID",
    nPageIndex: 0,
    nPageSize: page || 5
  };

  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function HandlePayment(parameters) {
  const {
    clientID,
    paymentType,
    amount,
    projectID,
    id,
    isNew,
    amountPaid,
    dueDate,
    paidDate,
    status,
    paymentMethod
  } = parameters;
  const { Name } = getVitals();
  let params = {
    nPortalID: 2,
    nIsNew: isNew,
    nCurrentID: isNew ? -1 : id,
    nClientID: clientID,
    nProjectID: projectID,
    oPaymentType: paymentType,
    nAmount: amount,
    nAmountPaid: amountPaid,
    nDueDate: dueDate,
    nPaidDate: paidDate,
    nStatus: status,
    oPaymentMethod: paymentMethod,
    oCreatedBy: Name
  };

  let link = `${api}/SavePayment`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function MultiplePaymentsDelete(ids) {
  let params = {
    nPaymentID: ids.toString(),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeletePayment`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleClientVisit(parameters) {
  const {
    currentID,
    clientID,
    visitType,
    remarks,
    plot,
    block,
    area,
    clientDecision,
    isNew,
    ProjectID,
    Attachment
  } = parameters;
  const { Name } = getVitals();
  let params = {
    ID: currentID,
    ClientID: clientID,
    ProjectID,
    VisitType: visitType,
    CreatedBy: Name,
    Remarks: remarks,
    PortalID: 2,
    IsNew: isNew,
    PlotNo: plot,
    BlockNo: block,
    Area: area,
    ClientDecision: clientDecision,
    Attachment
  };

  let link = `${api}/SaveClientVisit`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
