import React, { useRef, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/ExpansionPanel";
import AccordionSummary from "@material-ui/core/ExpansionPanelSummary";
import AccordionDetails from "@material-ui/core/ExpansionPanelDetails";
import AccordionActions from "@material-ui/core/ExpansionPanelActions";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { TextFieldFormsy, SelectFormsy } from "../../components/formsy";
import Formsy from "formsy-react";
import { MenuItem } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Axios from "axios";
import Grid from "@material-ui/core/Grid";
import { URL } from "../../BaseUrl";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
}));

const ProjectSearch = ({ handleSearch, resetSearch }) => {
    const formRef = useRef(null);
    const classes = useStyles();
    const [suggestionsAssignBy, setsuggestionsAssignBy] = useState([]);
    const [statusSuggestion, setStatusSuggestions] = useState([]);
    const [isFormValid, setIsFormValid] = useState(false);
    // Disable Enable Buttons



    useEffect(()=>{
        Axios.post(URL + '/api/GetProjectStatus').then(response =>{
            if(response.data){
                setStatusSuggestions(response.data)
            }
        }).catch(err => console.error(err))
    },[])

    useEffect(() => {
        Axios.post(URL + "/api/GetAllUsers")
            .then((res) => {
                const resolvedData = res.data.Result.UsersData.map((val) => {
                    return {
                        value: val.ID,
                        label: val.Name,
                    };
                });
                setsuggestionsAssignBy(resolvedData);
            })
            .catch((err) => {
                console.error("Error => ", err);
            });
    }, []);

    function disableButton() {
        setIsFormValid(false);
    }
    // /v1/area/region
    function enableButton() {
        setIsFormValid(true);
    }

    return (
        <div className={classes.root}>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography className={classes.heading}>
                        Click here for search
                    </Typography>
                </AccordionSummary>
                <Formsy
                    className="w-full"
                    onValidSubmit={handleSearch}
                    onValid={enableButton}
                    onInvalid={disableButton}
                    ref={formRef}
                >
                    <AccordionDetails
                        style={{ width: "100%", display: "flex", flexDirection: "column" }}
                    >
                        <Grid container spacing={2}>
                            <Grid item xs={3}>
                                <TextFieldFormsy
                                    variant="outlined"
                                    fullWidth
                                    type="text"
                                    name="nKeywordTitle"
                                    label="Name"
                                />
                            </Grid>

                            <Grid item xs={3}>
                                <TextFieldFormsy
                                    variant="outlined"
                                    fullWidth
                                    type="text"
                                    name="nPlotNo"
                                    label="Plot No."
                                />
                            </Grid>

                            <Grid item xs={3}>
                                <TextFieldFormsy
                                    variant="outlined"
                                    fullWidth
                                    type="text"
                                    name="nMobileNumber"
                                    label="Mobile No."
                                />
                            </Grid>
                            <Grid item xs={3}>
                                <SelectFormsy
                                    fullWidth
                                    name="nStatus"
                                    label="Status"
                                    variant="outlined"
                                >
                                    {statusSuggestion.map((item) => {
                                        return <MenuItem key={item.ID} value={item.ID}>{item.Status}</MenuItem>;
                                    })}
                                </SelectFormsy>
                            </Grid>
                        </Grid>
                    </AccordionDetails>
                    <AccordionActions>
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => {
                                formRef.current.reset();
                                resetSearch();
                            }}
                        >
                            Reset
                        </Button>
                        <Button
                            disabled={!isFormValid}
                            type="submit"
                            variant="contained"
                            color="primary"
                        >
                            Search
                        </Button>
                    </AccordionActions>
                </Formsy>
            </Accordion>
        </div>
    );
};

export default ProjectSearch;
