import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, Button } from "@material-ui/core";
import { Table } from "../../components/Table";
import { attachments_visit_columns } from "../../server/columns";
import {api} from '../../api/api'
import {
  GetProjectTypes,
  GetFundTypes,
  GetContractType,
  HandleProject,
  GetClientVisitBySpecificClientID,
  HandleClientVisit,
} from "../../server";
import Alert from "../../components/Alert";
import PaperDropzone from "../../components/Dropzone/Dropzone";
import { makeStyles } from "@material-ui/core/styles";
// import SearchVisitListComponent from "./SearchVisitListComponent";
import { getVitals } from "../../server/utils";
import { styles } from "../../styles";
import {
  TextField,
  Switch,
  FormControlLabel,
  CircularProgress,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Checkbox,
  Divider,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  RadioGroup,
  Radio,
  FormHelperText,
  Input,
} from "@material-ui/core";
import history from "../../history";
import Axios from 'axios'

// Import New FullScreen Modal
import FullScreenModal from './FullScreenModal'

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  expansionPanel: {
    marginBottom: 16,
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

function FormVisitDetail(props) {
  const classes = useStyles();

  //   let Attachments =
  //     type === "edit" &&
  //     data &&
  //     data.length > 0 &&
  //     (data[0].AttachmentsArray || data[0].SubmittedDocs
  //       ? [
  //           {
  //             File: data[0].SubmittedDocs,
  //             FileExtenstion: "png",
  //             FileName: "File"
  //           }
  //         ]
  //       : "");

  const [projectTypes, setProjectTypes] = useState([]);
  const [projectType, setProjectType] = useState("");
  const [fundTypes, setFundTypes] = useState([]);
  const [fundType, setFundType] = useState("");
  const [contractTypes, setContractTypes] = useState([]);
  const [contractType, setContractType] = useState("");
  const [success, setSuccess] = useState("");
  const [id] = useState(props.match.id);
  // STATES USED WHILE CALLING API'S

  const [projectTitle, setProjectTitle] = useState("");
  const [projectDescription, setProjectDescription] = useState("");
  const [designFees, setDesignFees] = useState("");
  const [supervision, setSupervision] = useState("");
  const [contractor, setContractor] = useState("");

  const [attach, setAttach] = useState([]);
  const [conditions, setConditions] = useState([]);
  const [attachment, setAttachment] = useState([]);
  // ATTACHMENTS

  const [client, setClient] = useState("");
  const [ClientID, setClientID] = useState(0);
  const [visitType, setVisitType] = useState("");
  const [taskType, setTaskType] = useState("");
  const [paymentType, setPaymentType] = useState("");
  // const [fundType, setFundType] = useState("");

  const [projects, setProjects] = useState("");
  const [project, setProject] = useState("");
  const [projectID, setProjectID] = useState("");

  const [supervisionFee, setSupervisionFee] = useState("");
  const [designFee, setDesignFee] = useState("");
  const [contractorFee, setContractorFee] = useState("");
  const [role, setRole] = useState("");
  const [taskAction, setTaskAction] = useState("");
  const [plot, setPlot] = useState("");
  const [block, setBlock] = useState("");
  const [area, setArea] = useState("");
  const [clientDecision, setClientDecision] = useState("");
  const [remarks, setRemarks] = useState("");
  const [loadingActiveUsers, setLoadingActiveUsers] = useState(false);
  const [data, setData] = useState([]);
  const [AttachmentDescription, setAttachmentDescription] = useState("");
  const [error, setError] = useState("");

  // New FullScreem Dialog State
  const [dialogState, setDialogState] = useState(false);
  const [rowData, setRowData] = useState(null);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");


  console.log("From Visit Data => ", data)

  let project_types = [
    { name: "Residential", id: 1 },
    { name: "Commercial", id: 2 },
  ];
  let notificationTypes = [
    { name: "Workflow", id: 1 },
    { name: "SMS", id: 2 },
    { name: "Email", id: 3 },
  ];
  let priorities = [
    { name: "High", id: 1 },
    { name: "Medium", id: 2 },
    { name: "Low", id: 3 },
  ];
  let payment_status = [
    { name: "Pending", id: 1 },
    { name: "Satisfied", id: 2 },
  ];
  let payment_types = [
    { name: "Supervision", id: 1 },
    { name: "Contractor", id: 2 },
    { name: "Design", id: 4 },
    { name: "Other", id: 3 },
  ];
  let visit_types = [
    { name: "New Visit", id: 1 },
    { name: "Follow Up", id: 2 },
  ];
  let task_types = [
    { name: "Design", id: 1 },
    { name: "Inspection", id: 2 },
  ];

  let status_types = [
    { name: "Pending", id: 1 },
    { name: "Assigned", id: 2 },
    { name: "Completed", id: 3 },
  ];

  let clientDecisions = [
    { name: "Agree", id: 1 },
    { name: "Design modification", id: 2 },
    { name: "Disagree", id: 3 },
  ];
  let task_actions = [
    { name: "Action 1", id: 1 },
    { name: "Action 2", id: 2 },
    { name: "Action 3", id: 3 },
  ];
  let fund_types = [
    { name: "Monthly", id: 1 },
    { name: "Quarterly", id: 2 },
    { name: "Yearly", id: 3 },
  ];
  let contract_types = [
    { name: "Design", id: 1 },
    { name: "Inspection", id: 2 },
  ];
  let sex = [
    { name: "Male", id: 1 },
    { name: "Female", id: 2 },
  ];

  useEffect(() => {
    handleGettingVisitData();
    return () => {
      setRowData({})
      setName("")
      setDescription("")
    }
  }, []);


  const handleOpenDialog = (data) => {
    setRowData(data)
    setName(data.FileName)
    setDescription(data.Description)
    setDialogState(true)
  }

  const handleCloseDialog = () => {
    setDialogState(false)
  }
  
  const OnSubmitViewList = () => {
    const header = {
      headers: {
        "Content-Type": "application/json"
      }
    }
    const body = {
      ID: rowData.ID,
      File: null,
      FileName: name,
      FileExtenstion: null,
      CategoryID: rowData.CategoryID,
      CategoryTitle: rowData.Category,
      Description : description
    }
    Axios.post(`${api}/EditAttachments`, body, header).then((response) => {
      if (response.data.result) {
        handleCloseDialog();
        handleGettingVisitData();
      }
    })

  }

  const onDeleteClickViewList = (data) => {
    const header = {
      headers: {
        "Content-Type": "application/json"
      }
    }
    const body = {
      Id: data.ID,
    }
    Axios.post(`${api}/DeleteAttachments`, body, header).then((response) => {
      handleGettingVisitData();
      if (response.data === true) {
        handleGettingVisitData();
      }
    })
  }

  function handleAddingAttachment(e) {
    console.log("DATA Current Time => ", data)
    e.preventDefault();
    if (data !== null && data[0]) {
      if (!conditions.length > 0) {
        return setError("Please add an Attachment First!");
      }
      let arr = [];
      // arr.push(data[0].AttachmentsArray[0]);
      arr.push(conditions);
      setConditions(arr);
      let params = {
        currentID: data[0].ID,
        clientID: data[0].ClientID,
        ProjectID: data[0].ProjectID,
        VisitType: data[0].VisitType,
        Name: localStorage.getItem("name"),
        remarks: data[0].Remarks,
        IsNew: false,
        plot: data[0].PlotNo,
        block: data[0].BlockNo,
        area: data[0].Area,
        clientDecision: data[0].ClientDecesion,
        Attachment: conditions,
        ClientVistTaskAssigns: null,
      };
      HandleClientVisit(params).then((res) => {
        let Result = res.data.result;
        if (Result === true) {
          let message = "Visit Added successfully";
          handleGettingVisitData();
          setSuccess(message);
        }  
      });
     
    }
  }

  function handleGettingVisitData() {
    GetClientVisitBySpecificClientID(props.match.params.id).then((res) => {
      let Result = res.data.result;

      if (Result === true) {
        setData(res.data.ClientVisitArray);
        setVisitType(data.VisitType);
        setPlot(data.PlotNo);
      } else {
        let error = res.data.message;
        // setError(error);
        // setloading(false);
      }
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    let params = {
      projectTitle,
      projectType,
      projectDescription,
      fundType,
      contractType,
      designFees,
      supervision,
      contractor,
      attach,
      conditions,
      attachment,
      id,
    };

    HandleProject(params).then((res) => {
      if (res.data.Result.result) {
        let message = "Succesfully Save!";
        setSuccess(message);
      }
    });
  }

  console.log("data[0]", data[0])

  return (
    <div>
      <Alert
        open={success.length > 0}
        type="success"
        message={success}
        onClose={() => {
          setSuccess("");
          setError("");
          setProjectType("");
          setFundType("");
          setContractType("");
          setProjectTitle("");
          setProjectDescription("");
          setDesignFees("");
          setSupervision("");
          setContractor("");
          setAttach([]);
          setConditions([]);
          setAttachment([]);
        }}
      />
      <Alert
        open={error.length > 0}
        type="error"
        message={error}
        onClose={() => {
          setSuccess("");
          setError("");
          setProjectType("");
          setFundType("");
          setContractType("");
          setProjectTitle("");
          setProjectDescription("");
          setDesignFees("");
          setSupervision("");
          setContractor("");
          setAttach([]);
          setConditions([]);
          setAttachment([]);
        }}
      />
      <form onSubmit={handleSubmit}>
        <div style={{ ...styles.d_flex, ...styles.flex_column }}>
          <div style={{ display: "flex" }}>
            <div style={{ flex: 1 }}>
              <span>Visit Name : {data[0] && data[0].Remarks}</span>
              
            </div>
            <div style={{ flex: 1 }}>
              <span>Client Name : {data[0] && data[0].ClientName}</span>
            </div>
          </div>
          {data.map((value) => (
            <div>
              <TextField
                fullWidth
                label="Visit Type*"
                value={value.visitType === 1 ? "New" : "Follow Up"}
                disabled
                style={{ ...styles.mt_3 }}
              />
              <TextField
                fullWidth
                label="Plot No.*"
                value={value.PlotNo}
                disabled
                style={{ ...styles.mt_3 }}
              />
              <TextField
                fullWidth
                label="Block No."
                value={value.BlockNo}
                onChange={(e) => {
                  setBlock(e.target.value);
                }}
                disabled
                style={{ ...styles.mt_3 }}
              />
              <TextField
                fullWidth
                label="Area*"
                value={value.Area}
                disabled
                style={{ ...styles.mt_3 }}
              />
              <TextField
                fullWidth
                label="Remarks"
                value={value.Remarks}
                disabled
                style={{ ...styles.my_3 }}
              />
              <TextField
                fullWidth
                label="ClientDecision"
                value={value.ClientDecesion}
                disabled
                style={{ ...styles.my_3 }}
              />
              {/* <TextField
                fullWidth
                label="Attachment Description"
                value={AttachmentDescription}
                onChange={(e) => {
                  setAttachmentDescription(e.target.value);
                }}
                style={{ ...styles.my_3 }}
              /> */}
              <PaperDropzone
                title="Attachments"
                setImageData={setConditions}
                data={conditions}
                multiple
                type={[
                  ".dxf",
                  ".dif",
                  ".pdf",
                  ".png",
                  ".jpeg",
                  ".doc",
                  ".docx",
                ]}
              />
              <div>
                <Grid container>
                  <Grid item xs={12}>
                    <Table
                      title="Attachments"
                      columns={attachments_visit_columns}
                      data={value.AttachmentsArray}
                      // isLoading={loading}
                      // error={error}
                      // onChangePage={changePage}
                      // onSuccess={reloadContent}
                      // page={page}
                      // totalRecords={count}
                      // onRefresh={reloadContent}
                      // nullSelected={nullSelected}
                      variant={66}
                      // projects={projects}
                      // listType={"all-users"}
                      onEditButtonClickFromVisitDetails={(data) => handleOpenDialog(data)}
                      onDeleteClickViewList={(data) => onDeleteClickViewList(data)}
                    />
                  </Grid>
                </Grid>
              </div>
              <div>
                <Button
                  variant="contained"
                  color="primary"
                  style={{ margin: "5px" }}
                  onClick={(e) => {
                    handleAddingAttachment(e);
                  }}
                >
                  Upload Attachments
                </Button>
                
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    history.push(
                      {
                        pathname: `/visit-list-form/${props.match.params.id}/${value.PlotNo}/${value.Status}`,
                        state: {
                          Remarks: value.Remarks,
                          clientID: value.ClientID,
                          BlockNo: value.BlockNo,
                          VisitNumber: value.VisitNumber,
                          Area: value.Area
                        }
                      }
                    );
                  }}
                  style={{ margin: "5px" }}
                >
                  Create Project
                </Button>
              </div>
            </div>
          ))}
        </div>

        {/* <div></div> */}
      </form>
      <FullScreenModal
        isOpen={dialogState}
        handleClose={handleCloseDialog}
        appBarTitle="Hello From ViewList"
        buttonTitle="Save"
        onSubmit={() => OnSubmitViewList()}
      >
        <div>
          <Grid container style={{
            flexGrow: 1,
            padding : 12
          }}>
            <Grid item xs={6} style={{
              paddingRight :32
            }}>
              <TextField label="Name" variant="outlined" value={name} onChange={(e)=>{setName(e.target.value)}} fullWidth />
            </Grid>
            <Grid item xs={6}>
              <TextField multiline={true} label="Description" value={description} onChange={(e)=>setDescription(e.target.value)} variant="outlined" fullWidth />
            </Grid>
          </Grid>
        </div>
      </FullScreenModal>
    </div>
  );
}

export default FormVisitDetail;
