import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import {
  payments_module_columns,
  subimissions_module_columns
} from "../../server/columns";
import { GetAllClients, GetAllProjects, GetAllSubmissions } from "../../server";

function Submissions2(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [roles, setRoles] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetAllSubmissions()
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.SubmissionData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllSubmissions()
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.SubmissionData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });
  }, []);

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Submissions 2
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={subimissions_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={page => this.changePage(page)}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={17}
              projects={projects}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Submissions2);
