import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Button,
  Input,
} from "@material-ui/core";

import { DatePicker } from "@material-ui/pickers";
import { GetAllClients } from "../../server";
import { styles } from "../../styles";
import Axios from "axios";
import { URL } from "../../BaseUrl";

function SearchReassignComponent({
  match,
  projectID,
  setprojectID,
  SearchContent,
  ClientID,
  setClientID,
}) {
  const { id } = match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(false);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const [plot, setPlot] = useState("");
  const [phone, setPhone] = useState("");
  const [status, setStatus] = useState("");
  const [Clients, setClients] = useState([]);

  let status_types = [
    { ID: 1, value: true },
    { ID: 2, value: false },
  ];

  async function getAllClients() {
    let body = {
      nKeywordTitle: "",
      nEmail: "",
      nPhoneNumber: "",
      nOrderBy: "ClientName",
      nPageIndex: 0,
      nPageSize: 100,
    };
    let res = await Axios.post(URL + "/api/GetAllClients", body);
    if (res.data.Result.result) {
      setClients(res.data.Result.ClientsData);
    }
  }

  useEffect(() => {
    getAllClients();
  }, []);

  return (
    <Grow in={true}>
      <div>
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={2}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Project ID</InputLabel>
                    <Input
                      value={projectID}
                      onChange={(e) => {
                        setprojectID(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={2}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Client Name</InputLabel>
                    <Select
                      fullWidth
                      value={ClientID}
                      onChange={(e) => {
                        setClientID(e.target.value);
                      }}
                    >
                      {Clients.map((client) => (
                        <MenuItem value={client.ID} id={client.ID}>
                          {client.ClientName}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={2}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Mobile Number</InputLabel>
                    <Input
                      value={phone}
                      onChange={(e) => {
                        setPhone(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={2}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Plot Number</InputLabel>
                    <Input
                      value={plot}
                      onChange={(e) => {
                        setPlot(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={2}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Status</InputLabel>
                    <Select
                      fullWidth
                      value={status}
                      onChange={(e) => {
                        setStatus(e.target.value);
                      }}
                    >
                      {status_types.map((val) => (
                        <MenuItem value={val.ID} id={val.ID}>
                          {val.value}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>

                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <DatePicker
                      autoOk
                      label="Start Date"
                      clearable
                      disableFuture
                      value={startDate}
                      onChange={(date) => setStartDate(date)}
                    />
                  </FormControl>
                </Grid>

                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <DatePicker
                      autoOk
                      label="End Date"
                      clearable
                      disableFuture
                      value={endDate}
                      onChange={(date) => setEndDate(date)}
                    />
                  </FormControl>
                </Grid>
              </Grid>
            )}
            {!loadingProjects && (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={(e) => {
                      SearchContent(
                        e,
                        projectID,
                        ClientID,
                        plot,
                        status,
                        startDate,
                        endDate
                      );
                      setprojectID("");
                    }}
                    color="primary"
                    variant="contained"
                    disabled={
                      projectID === "" &&
                      ClientID === "" &&
                      plot === "" &&
                      status === "" &&
                      startDate === null &&
                      endDate === null
                        ? true
                        : false
                    }
                  >
                    Search
                  </Button>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(SearchReassignComponent);
