import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";
import { URL } from "../BaseUrl";

export function GetAllSentNotifications(page) {
  const { UserID } = getVitals();
  let link = `${api}/GetNotification_Pagged`;
  let params = {
    nkeywordTitle: "",
    nNotificationFrom: UserID,
    nNotificationTo: "",
    nOrderBy: "NotificationDate",
    nPageIndex: page,
    nPageSize: 10,
  };

  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

