import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Button,
  Input
} from "@material-ui/core";
import { Table } from "../../components/Table";
import {
  sent_module_columns,
  projects_module_columns,
  my_tasks_module_columns
} from "../../server/columns";
import {
  GetAllUsers,
  GetAllReceivedNotifications,
  GetAllProjects,
  GetAllProjectNotifications,
  GetMyTasksById
} from "../../server";
import { styles } from "../../styles";
import { DatePicker } from "@material-ui/pickers";
import Axios from 'axios';
import { URL } from "../../BaseUrl";

function SearchMyTaskComponent(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(false);
  const [project, setProject] = useState("");
  const [startDate, setStartDate] = useState(null);
  const [taskId, setTaskId] = useState("");
  const [location, setLocation] = useState("");
  const [status, setStatus] = useState("");
  const [endDate, setEndDate] = useState(null);
  const [projectID, setProjectID] = useState(null);
  const [clients, setClients] = useState([]);
  // const [users, setUsers] = useState([]);
  // const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [projectName, setProjectName] = useState("");
  const [assignedBy, setassignedBy] = useState(null);
  const [users, setUsers] = useState([]);
  const [projectStatus, setProjectStatus] = useState(null);

  // const reloadContent = () => {
  //   setData([]);
  //   setLoading(true);
  //   setError("");
  //   GetAllProjectNotifications(projectID)
  //     .then(res => {
  //       if (res.data.result === true) {
  //         let users = res.data.NotificationData;
  //         setLoading(false);
  //         setData(users);
  //       } else {
  //         let ErrorMessage = res.data.NotificationData;
  //         setLoading(false);
  //         setError(ErrorMessage);
  //       }
  //     })
  //     .catch(err => {
  //       let ErrorMessage = err.message;
  //       setLoading(false);
  //       setError(ErrorMessage);
  //     });
  // };

  useEffect(() => {
    Axios.post(URL + "/api/GetProjectStatus").then((res) => {
      if (res.data) {
        setProjectStatus(res.data);
      }
    })
    GetAllUsers()
      .then((res) => {
        let data = res.data.Result.UsersData || [];
        setUsers(data);
      })
      .catch((error) => {
        console.log("Error fetching users...");
      });
  },[])

  function getContent(id) {
    setError("");
    let filteredProject = [];

    if (id !== "date") {
      filteredProject = projects.filter(item => item.ID == id);
      setStartDate(null);
      setEndDate(null);
    } else {
      setProject("");
      filteredProject = projects.filter(item => {
        let projectDate = new Date(item.CreatedDate).toString();
        let start = startDate.toString();
        let end = endDate.toString();
        return projectDate >= start && projectDate <= end;
      });
    }
    setData(filteredProject);
  }

  const searchText = () => {
    props.searchPage({
      AssignBy: assignedBy, Status: status, nKeywordTitle: projectName, Location: location, nID: taskId
    })
  }

  return (
    <Grow in={true}>
      <div>
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <FormControl style={{ ...styles.my_3 }} fullWidth>
                <InputLabel htmlFor="age-simple">Task ID</InputLabel>
                <Input
                  value={taskId}
                  onChange={e => {
                    setTaskId(e.target.value);
                  }}
                />
              </FormControl>
            )}
            {!loadingProjects && (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={5}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Location</InputLabel>
                    <Input
                      value={location}
                      onChange={e => {
                        setLocation(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={5}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Status</InputLabel>
                    <Select
                      value={status}
                      onChange={(e) => {
                        setStatus(e.target.value);
                      }}
                    >
                      <MenuItem value={null}>Select Any Status</MenuItem>
                      {projectStatus &&
                        projectStatus.map((item) => {
                          return (
                            <MenuItem value={item.ID}>{item.Status}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Project Name</InputLabel>
                    <Input
                      value={projectName}
                      onChange={e => {
                        setProjectName(e.target.value);
                      }}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Assigned By</InputLabel>
                    <Select
                      value={assignedBy}
                      onChange={(e) => {
                        setassignedBy(e.target.value);
                      }}
                    >
                      <MenuItem value={null}>Select Any User</MenuItem>
                      {users &&
                        users.map((item) => {
                          return (
                            <MenuItem value={item.ID}>{item.Name}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={searchText}
                    // onClick={e => getContent("date")}
                    color="primary"
                    variant="contained"
                  >
                    Search
                  </Button>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(SearchMyTaskComponent);
