import Axios from "axios";
import { api } from "../api/api";
// import { getVitals } from "./utils";

// for dispay in add new role for
export function GetAllRoleTypes() {
  let promise = new Promise(function (resolve, reject) {
    Axios.post(`${api}/GetAllRoleTypes`)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

export function GetAllRolesPaged(page) {
  let params = {
    nOrderBy: "RoleName",
    nPageIndex: page,
    nPageSize: 10,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(`${api}/GetRoles_Pagged`, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

export function HandleRole(parameters) {
  const { RoleName, IsAdmin, RoleTypeId, id, isNew } = parameters;
  let params = {
    PortalID: 2,
    ID: isNew ? -1 : id,
    RoleName,
    IsNew: isNew,
    IsAdmin: IsAdmin,
    RoleTypeId,
    nUserType: 1,
    // CreatedBy: "Admin"
  };
  let link = `${api}/SaveRoles`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}
