const phases = [
  { phase: "New Visit", status: ["visit / follow up"], index: 1 },
  { phase: "Follow up", status: ["visit / follow up"], index: 2 },
  {
    phase: "Project",
    status: [
      "architectural design begins",
      "air conditioning",
      "owner approval for architectural design",
      "take the specifications from the owner",
      "structural design begins",
      "starting a service plan",
      "approval of the plan from the supervising engineer",
      "opening a transaction with the municipality",
      "pay fees",
      "submit the engineering plan and etisalat",
      "attach the service plan",
      "tendering and contractor selection",
      "attach contracts and pledge works",
      "fees payment and issuing a building",
    ],
    index: 3,
  },
  {
    phase: "Supervision",
    status: ["fees payment & issuing building"],
    index: 4,
  },
];

const foundPhase = (str) => {
  return phases.filter((val) => val.status.includes(str.toLowerCase().trim()));
};
function EnhancedTableHead(props) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
    columns,
    listType,
  } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead style={{ background: "#eee" }}>
      <TableRow>
        <TableCell padding="checkbox">
          {/* 
          {!listType && (
            <Checkbox
              indeterminate={numSelected > 0 && numSelected < rowCount}
              checked={numSelected === rowCount}
              onChange={onSelectAllClick}
              inputProps={{ "aria-label": "Select all desserts" }}
              style={{ Background: "red" }}
            />
          )}
        */}
        </TableCell>
        {columns.map((row) => (
          <TableCell
            key={row.id}
            align={row.numeric ? "right" : "left"}
            padding={row.disablePadding ? "none" : "default"}
            sortDirection={orderBy === row.id ? order : false}
            style={{ fontWeight: "bold", fontSize: "16px" }}
          >
            <TableSortLabel
              active={orderBy === row.id}
              direction={order}
              onClick={createSortHandler(row.id)}
            >
              {row.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const downloadAnyThing = (url, name) => {
  axios({
    url: url,
    method: "GET",
    responseType: "blob", // important
  }).then((response) => {
    const url = window.URL.createObjectURL(new Blob([response.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", name);
    document.body.appendChild(link);
    link.click();
  });
};

