import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, TextField } from "@material-ui/core";
import { Table } from "../../components/Table";
import { permissions_module_columns } from "../../server/columns";
import { GetAllProjects } from "../../server";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";
import SearchReassignComponent from "./SearchReassignComponent";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import { styles } from "../../styles";
import { reassign_module_columns } from "../../server/columns";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

function ProjectReAssign(props) {
  const dispatch = useDispatch();
  const classes = useStyles();
  const theme = useTheme();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const [value, setValue] = useState(0);
  const [projectID, setprojectID] = useState("");
  const [ClientID, setClientID] = useState("");

  useEffect(() => {
    GetProject(0);
  }, []);

  function GetProject(p, id, Cid, plot, status, startDate, endDate, SIZE) {
    setLoading(true);
    GetAllProjects(p, id, "", Cid, plot, status, startDate, endDate, SIZE)
      .then((res) => {
        let data = res.data.Result.ProjectsData || [];
        setData(data);
        setCount(res.data.Result.TotalCount);
        setLoading(false);
      })
      .catch((error) => {
        console.log("Error fetching projects...");
      });
  }

  const reloadContent = () => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetProject();
  };

  const SearchContent = (e, id, Cid, plot, status, startDate, endDate) => {
    e.preventDefault();
    setLoading(true);
    GetProject(page, id, Cid, plot, status, startDate, endDate, 1000);
  };

  const changePage = (p) => {
    setPage(p);
    setLoading(true);
    setData([]);
    GetProject(p);
  };

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Reassigning Project
        </Typography>
        <SearchReassignComponent
          projectID={projectID}
          setprojectID={setprojectID}
          SearchContent={SearchContent}
          ClientID={ClientID}
          setClientID={setClientID}
        />
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={reassign_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={(page) => changePage(page)}
              onSuccess={reloadContent}
              page={page}
              totalRecords={count}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={10}
              //   projects={projects}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(ProjectReAssign);
