import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";

export function GetAllExpenses(page) {
  let link = `${api}/GetAllExpenses`;
  let params = {
    nKeywordTitle: "",
    nOrderBy: "Amount DESC",
    nPageIndex: page,
    nPageSize: 10
  };

  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleExpense(parameters) {
  const {
    title,
    date,
    amount,
    projectID,
    id,
    isNew,
    remarks,
    attachments
  } = parameters;
  const { Name } = getVitals();
  let params = {
    nPortalID: 2,
    nIsNew: isNew,
    nCurrentID: isNew ? -1 : id,
    nExpenseTitle: title,
    nDate: date,
    nAmount: amount,
    nProjectID: projectID,
    oRemarks: remarks,
    oCreatedBy: Name,
    oAttachments: attachments
  };
  

  let link = `${api}/SaveExpense`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function MultipleExpenseDelete(ids) {
  let params = {
    nExpenseID: ids.toString(),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeleteExpense`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleClientVisit(parameters) {
  const {
    currentID,
    clientID,
    visitType,
    remarks,
    plot,
    block,
    area,
    clientDecision,
    isNew,
    ProjectID,
    Attachment
  } = parameters;
  const { Name } = getVitals();
  let params = {
    ID: currentID,
    ClientID: clientID,
    ProjectID,
    VisitType: visitType,
    CreatedBy: Name,
    Remarks: remarks,
    PortalID: 2,
    IsNew: isNew,
    PlotNo: plot,
    BlockNo: block,
    Area: area,
    ClientDecision: clientDecision,
    Attachment
  };

  let link = `${api}/SaveClientVisit`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
