import * as Actions from "./actions/index";

const initialState = {
  LoggedUser: [],
  UserRole:[],
  Module:[],
  FinalRoles:[]
};

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.GET_USER_BY_ID: {
      return { ...state, LoggedUser: action.payload };
    }
    case Actions.GET_ROLE_LOGGED_USER: {
    
      return { ...state, UserRole: action.payload };
    }
    case Actions.GET_ALL_RULES: {
      return { ...state, FinalRoles: action.payload}
    }
    case Actions.SET_MODULE: {
      return {...state, Module: action.payload}
    }

    default:
      return state;
  }
};
export default userReducer;