import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { clients_module_columns } from "../../server/columns";
import { GetAllClients, GetAllProjects } from "../../server";
import SearchClientComponent from "./SearchClientComponent";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";

function Users(props) {
  const dispatch = useDispatch();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const [size, setSize] = useState(10);

  useEffect(() => {
    dispatch(Action2.setModule("Clients"));
  }, []);

  const reloadContent = (Name, size, email, phone) => {
    console.log("Reloading Content => reached")
    setData([]);
    setLoading(true);
    setError("");
    GetAllClients(page, 10, Name, email, phone)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.ClientsData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllClients(page, size)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.ClientsData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then((res) => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch((error) => {
        console.log("Error fetching projects...");
      });
  }, []);

  const changePage = (page) => {
    setPage(page);
    setLoading(true);
    // setData([]);
    GetAllClients(page, size)
      .then((res) => {
        let data = res.data.Result.ClientsData || [];
        let count = res.data.Result.TotalCount;

        setData(data);
        setLoading(false);
        setCount(count);
        setError("");
      })
      .catch((error) => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Clients
        </Typography>
        <SearchClientComponent
          GetAllClients={GetAllClients}
          setData={setData}
          setCount={setCount}
          setLoading={setLoading}
          setError={setError}
          onSuccess={reloadContent}
          page={page}
        />
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={clients_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={(page) => changePage(page)}
              page={page}
              totalRecords={count}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={3}
              projects={projects}
              setData={setData}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Users);
