import React, {useEffect, useRef, useState} from "react";
import Axios from "axios";
import Fab from "@material-ui/core/Fab";
import ReactTable from "react-table";
import EditIcon from "@material-ui/icons/Edit";
import CircularProgress from "@material-ui/core/CircularProgress";
import {TableHeader} from "../tableHeader";
import {useDispatch} from "react-redux";
import {useParams, useHistory} from "react-router-dom";
import AddIcon from "@material-ui/icons/Add";
import {URL} from "../../BaseUrl";
import moment from "moment";
import VisitHistoryDialog from "./VisitHistoryDialog";

function VisitHistory() {
    const dispatch = useDispatch();
    const history = useHistory();
    const params = useParams();
    const [open, setOpen] = useState(false);
    const [type, setType] = useState("Add");
    const [dataForEdit, setDataForEdit] = useState({});
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    const [pages, setPages] = useState(0);
    const reactTableRef = useRef(null);
    const [pageSize, setPageSize] = useState(5);
    const [pageIndex, setPageIndex] = useState(0);
    const [orderby, setOrderBy] = useState("A.ID DESC");
    const [searchObject, setSearchObject] = useState({});

    // Get All Table Data

    useEffect(() => {
        getTableData();
    }, [pageIndex, pageSize, searchObject, orderby]);

    const getTableData = () => {
        setLoading(true);
        const params = {
            nAssignTo: searchObject.Assignby || null,
            nLocation: searchObject.location || "",
            nDescription: searchObject.name || "",
            nVisitType: 2,
            nClientDecision: searchObject.status || "",
            nVisitNumber: "",
            nClientID: null,
            nPortalID: 2,
            nOrderBy: orderby,
            nPageIndex: pageIndex,
            nPageSize: pageSize,
        };

        Axios.post(URL + "/Api/GetClientVisitByClientID", params)
            .then((res) => {
                if (res.data.result) {
                    setData(res.data.ClientVisitArray);
                    setPages(res.data.TotalPages);
                } else {
                    setData([]);
                    setPages(0);
                }
            })
            .catch((err) => {
                console.error("Error => ", err);
            })
            .finally(() => {
                setLoading(false);
            });
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);

        setTimeout(() => {
            setType("Add");
            setDataForEdit({});
        }, 100);
    };

    let visit_types = [
        {name: "Visit", id: 1},
        {name: "Project", id: 2},
    ];

    const columns = [
        {
            Header: <TableHeader name="ID"/>,
            accessor: "ID",
            className: "justify-start",
            sortable: true,
            defaultSortDesc: true,
            Cell: (row) => {
                return (
                    <div
                        style={{
                            cursor: row.original.VisitType === 1 ? "pointer" : undefined,
                            color: "#287DB2",
                        }}
                        onClick={() => {
                            if (row.original.VisitType === 1) {
                                history.push(`/visit-detail-form/${row.value}`);
                            }
                        }}
                    >
                        {row.value}
                    </div>
                );
            },
        },
        {
            Header: <TableHeader name="Name"/>,
            accessor: "ClientName",
            className: "justify-start",
            sortable: false,
        },

        {
            Header: <TableHeader name="Location"/>,
            accessor: "",
            className: "justify-start",
            sortable: false,
            Cell: (row) => {
                return (
                    <>
                        {row.original.Area} {row.original.PlotNo}
                    </>
                );
            },
        },

        {
            Header: <TableHeader name="Assign By"/>,
            accessor: "CreatedBy",
            className: "justify-start",
            sortable: false,
        },

        {
            Header: <TableHeader name="Type"/>,
            accessor: "VisitType",
            className: "justify-start",
            sortable: false,
            Cell: (row) => {
                return <>{visit_types.find((val) => val.id === row.value)?.name}</>;
            },
        },

        {
            Header: <TableHeader name="Status"/>,
            accessor: "Status",
            className: "justify-start",
            sortable: false,
            Cell: (row) => {
                return <>{row.value ? "True" : "False"}</>;
            },
        },

        {
            Header: <TableHeader name="Created Date"/>,
            accessor: "CreatedDate",
            className: "justify-start",
            sortable: false,
            Cell: (row) => {
                return <>{moment(row.value).format("l")}</>;
            },
        },

        {
            Header: <TableHeader name="Edit"/>,
            accessor: "edit",
            className: "justify-center",
            sortable: false,
            width: 200,
            Cell: (row) => {
                return (
                    <Fab
                        variant="extended"
                        size="medium"
                        color="primary"
                        aria-label="edit"
                        onClick={() => {
                            setDataForEdit(row.original);
                            setType("Save");
                            handleClickOpen();
                        }}
                    >
                        <EditIcon className="mr-8"/>
                        Edit
                    </Fab>
                );
            },
        },
    ];

    return (
        <div>
            {/* Header */}
            <div
                style={{
                    display: "flex",
                    justifyContent: "flex-end",
                    alignItems: "center",
                    marginTop: '15px'
                }}
            >
                <div>
                    <Fab
                        variant="extended"
                        size="medium"
                        color="primary"
                        aria-label="add"
                        onClick={handleClickOpen}
                    >
                        <AddIcon className="mr-8"/>
                        Add New
                    </Fab>
                </div>
            </div>
            {/* Table */}
            <div
                style={{marginTop: 20}}
                className="w-full mt-10 flex justify-between items-center"
            >
                <ReactTable
                    className="-striped w-full -highlight h-full overflow-hidden"
                    style={{marginBottom: '20px'}}
                    data={data}
                    columns={columns}
                    defaultPageSize={5}
                    loading={loading}
                    noDataText="No Data"
                    loadingText={<CircularProgress/>}
                    manual
                    multiSort={false}
                    defaultSorted={[
                        {
                            // the sorting model for the table
                            id: "ID",
                            desc: true,
                        },
                    ]}
                    pages={pages}
                    ref={reactTableRef}
                    onPageChange={(e) => {
                        setPageIndex(e);
                    }}
                    onPageSizeChange={(pagesize, pagenumber) => {
                        setPageSize(pagesize);
                        setPageIndex(pagenumber);
                    }}
                    onSortedChange={(value) => {
                        if (value.find((val) => val.id === "ID")) {
                            value.find((val) => val.id === "ID").desc
                                ? setOrderBy("A.ID DESC")
                                : setOrderBy("A.ID");
                        }
                    }}
                />
            </div>
            <VisitHistoryDialog
                open={open}
                type={type}
                handleClose={handleClose}
                dataForEdit={dataForEdit}
                getTableData={getTableData}
            />
        </div>
    );
};

export default VisitHistory;
