import React, { useState, useEffect } from 'react';
import moment from 'moment';
import axios from 'axios'
import { URL } from '../../BaseUrl';
import ReactTable from 'react-table';
import { CircularProgress } from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider ,  KeyboardDatePicker } from '@material-ui/pickers';
import './TABLE_CSS_ALL_LOADING.css'


const EmployeesPerformance = () => {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true)
    const [startDate, setStartDate] = useState(new Date('2019-07-22 13:30:21.893'))
    const [endDate, setEndDate] = useState(new Date());

    // const startDay = moment(new Date()).startOf('day').subtract(1, 'day')
    // const endDay = moment(new Date()).endOf('day');

    useEffect(() => {
        const body = {
            nDateFrom: startDate,
            nDateTo: endDate
        }
        setLoading(true)
        axios.post(`${URL}/Api/EmployeePerformanceReport`, body).then((response) => {
            setData(response.data);
            setLoading(false);
        })
    }, [startDate,endDate])


    return (
        <React.Fragment>
            <MuiPickersUtilsProvider utils={MomentUtils}>
                <div className="Second_TABLE_DATE_PICKERS_CODE_HEAD" >

                    <div>

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            label="From ( Date )"
                            format="MM-DD-YYYY"
                            inputVariant="outlined"
                            value={startDate}
                            onChange={(date) => setStartDate(date) }
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            inputVariant="outlined"
                            label="To ( Date )"
                            format="MM-DD-YYYY"
                            value={endDate}
                            onChange={(date) => setEndDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                    </div>

                </div>
                
            </MuiPickersUtilsProvider>

            {
                loading && <div id="NEW_ALL_TABLE_LOADING" >
                    <div>
                        <CircularProgress />
                        <div>
                            Loading Data...
                        </div>
                    </div>
                </div>
            }

            {data !== null && data.TasksActivityLogData === null && !loading && <div>
                
                <h4>No Data Found</h4>
                
            </div>}

            {data !== null && data.TasksActivityLogData !== null && !loading  && <div>

                <header id="NEW_ALL_TABLE_Header">
                    <span>{`Employees Performance : Total Record ( ${data.TasksActivityLogData.length} ) `}</span>
                </header>

                <ReactTable
                    className='-striped -highlight h-full overflow-hidden'
                    data={data.TasksActivityLogData || []}
                    columns={
                        [
                            {
                                Header: "id",
                                accessor: 'ID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ID}</div>
                            },
                            {
                                Header: "User ID",
                                accessor: 'UserID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.UserID}</div>
                            },
                            {
                                Header: "Task Activity ID",
                                accessor: 'TaskActivityID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.TaskActivityID}</div>
                            },
                            {
                                Header: "Task Action",
                                accessor: 'TaskAction',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "ActionDate",
                                accessor: 'ActionDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.ActionDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Remarks",
                                accessor: 'Remarks',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Created By",
                                accessor: 'CreatedBy',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Created Date",
                                accessor: 'CreatedDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.CreatedDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Modified By",
                                accessor: 'ModifiedBy',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "Modified Date",
                                accessor: 'ModifiedDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.ModifiedDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Assignee",
                                accessor: 'Assignee',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "AssigneeRole",
                                accessor: 'AssigneeRole',
                                className: "justify-start",
                                sortable: false,
                            },
                        ]
                    }
                    defaultPageSize={data.TasksActivityLogData.length}
                    noDataText={"No Data"}
                    showPageSizeOptions={false}
                    showPagination={false}
                />
            </div>}
        </React.Fragment>
    )
}


export default EmployeesPerformance;