import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, IconButton } from "@material-ui/core";
import { Table } from "../../components/Table";
import { sent_module_columns } from "../../server/columns";
import {
  GetAllSentNotifications,
  GetAllUsers,
  GetAllProjects
} from "../../server";
import { ArrowBackTwoTone } from "@material-ui/icons";
import { styles } from "../../styles";

function SentNotifications(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [clients, setClients] = useState([]);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetAllSentNotifications(page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.NotificationData;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllSentNotifications(page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.NotificationData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });

    GetAllUsers()
      .then(res => {
        let data = res.data.Result.UsersData || [];
        setUsers(data);
      })
      .catch(error => {
        console.log("Error fetching users...");
      });
  }, []);

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetAllSentNotifications(page)
      .then(res => {
        let data = res.data.NotificationData || [];
        let count = res.data.TotalCount;
        setCount(count);
        setData(data);
        setLoading(false);
      })
      .catch(error => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <IconButton
            style={{ ...styles.mr_2 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone />
          </IconButton>
          Sent Notifications
        </Typography>
        <Typography variant="h6" gutterBottom />
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={sent_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              page={page}
              totalRecords={count}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={16}
              projects={projects}
              clients={clients}
              users={users}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(SentNotifications);
