import React, { useState, useEffect } from "react";
import { Grid, Paper, Typography, Grow } from "@material-ui/core";
import NestedList from "../../components/NestedList/NextedList";
// import { GetCategories, GetGenericContentListing } from "../../server";
import { Table } from "../../components/Table";
// import { styles } from "../../styles";
import { roles_module_columns } from "../../server/columns";
import { GetAllRolesPaged, GetAllRoleTypes } from "../../server";
import RoleDailog from "./RolesDailog";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";
import Alert from "../../components/Alert";
import axios from "axios";
import { URL } from "../../BaseUrl";

export default function Security(props) {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [data, setDate] = useState([]);
  const [roles, setRoles] = useState([]);
  const [loadingData, setLoading] = useState(true);
  const [errorData, setErrorData] = useState("");
  const [id, setID] = useState("");
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const [nullSelected, setNullSelected] = useState(false);
  const [roleID, setRoleID] = useState(0);
  const [success, setSuccess] = useState("");
  const [error, setError] = useState("");
  const [rulesOnId, setRulesOnId] = useState([]);
  const [RoleLoading, setRoleLoading] = useState(false);

  useEffect(() => {
    GetAllRolesPaged(page)
      .then((res) => {
        let datas = res.data.RolesArray || [];
        let counts = res.data.TotalCount;
        // ExtractAdmin(datas, counts);
        setDate(datas);
        setLoading(false);
        setCount(counts);
      })
      .catch((error) => {
        let err = error.message;
        setErrorData(err);
        setLoading(false);
      });

    GetAllRoleTypes()
      .then((res) => {
        let Roles = res.data.data;
        setRoles(Roles);
      })
      .catch((err) => console.log("Error fetching Roles"));
  }, [page]);

  useEffect(() => {
    dispatch(Action2.setModule("Roles"));
  }, []);

  const GetAssignmentById = async (id) => {
    let body = {
      Id: id,
    };
    let res = await axios.post(URL + "/api/GetRuleByRoleId", body);
    if (res.status === 200) {
      setRulesOnId(res.data);
    }
  };

  const AssignRole = async (body) => {
    let res = await axios.post(URL + "/Api/AddRuleRange", body);
    let rol = res.data;
    if (rol.length > 0) {
      setSuccess("Roles Assigned");
      setOpen(false);
    } else {
      setError("There May be a problem while Assigning Data!");
      setOpen(false);
    }
  };

  const reloadContent = (param) => {
    setDate([]);
    setLoading(true);
    setErrorData("");
    GetAllRolesPaged(page)
      .then((res) => {
        let datas = res.data.RolesArray || [];
        let counts = res.data.TotalCount;
        // ExtractAdmin(datas, counts);
        setDate(datas);
        setLoading(false);
        setCount(counts);
      })
      .catch((error) => {
        let err = error.message;
        setErrorData(err);
        setLoading(false);
      });
  };

  const changePage = (page) => {
    setPage(page);
    setDate([]);
    setLoading(true);
    setErrorData("");

    GetAllRolesPaged(page)
      .then((res) => {
        let datas = res.data.RolesArray || [];
        let counts = res.data.TotalCount;
        // ExtractAdmin(datas, counts);
        setDate(datas);
        setLoading(false);
        setCount(counts);
        setErrorData("");
      })
      .catch((error) => {
        let err = error.message;
        setErrorData(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Alert
          open={error.length > 0}
          type="error"
          message={error}
          onClose={() => {
            setError("");
          }}
        />
        <Alert
          open={success.length > 0}
          type="success"
          message={success}
          onClose={() => {
            setSuccess("");
          }}
        />
        <Typography variant="h6" gutterBottom>
          Roles
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={roles_module_columns}
              data={data}
              isLoading={loadingData}
              page={page}
              totalRecords={count}
              error={errorData}
              onChangePage={changePage}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={18}
              roles={roles}
              closer={setOpen}
              setRoleID={setRoleID}
              GetAssignmentById={GetAssignmentById}
              setRoleLoading={setRoleLoading}
              setData={setDate}
              // size={data.length}
            />
          </Grid>
        </Grid>
        <RoleDailog
          open={open}
          closer={setOpen}
          roleID={roleID}
          AssignRole={AssignRole}
          rulesOnId={rulesOnId}
          setRoleLoading={setRoleLoading}
          RoleLoading={RoleLoading}
        />
      </div>
    </Grow>
  );
}
