import React, { Component } from "react";
import { Grid, Paper, Typography, Grow } from "@material-ui/core";
import NestedList from "../../components/NestedList/NextedList";
import { GetCategories, GetGenericContentListing } from "../../server";
import { Table } from "../../components/Table";
import { styles } from "../../styles";
import { security_module_columns } from "../../server/columns";
import { GetAllGroups, GetAllRoleTypes } from "../../server";

export default class Security extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      roles: [],
      loadingData: true,
      errorData: "",
      id: "",
      page: 0,
      count: 0,
      nullSelected: false
    };
  }

  componentDidMount() {
    GetAllGroups(this.state.page)
      .then(res => {
        let data = res.data.Result.GroupsData || [];
        let count = res.data.Result.TotalCount;

        this.setState({
          data,
          loadingData: false,
          count
        });
      })
      .catch(error => {
        let err = error.message;
        this.setState({ errorData: err, loadingData: false });
      });

    GetAllRoleTypes()
      .then(res => {
        let Roles = res.data.RolesArray;
        this.setState({ roles: Roles });
      })
      .catch(err => console.log("Error fetching Roles"));
  }

  reloadContent = param => {
    this.setState({
      data: [],
      errorData: "",
      loadingData: true
    });
    GetAllGroups(this.state.page)
      .then(res => {
        let data = res.data.Result.GroupsData || [];
        let count = res.data.Result.TotalCount;
        this.setState({
          data,
          loadingData: false,
          count
        });
      })
      .catch(error => {
        let err = error.message;
        this.setState({ errorData: err, loadingData: false });
      });
  };

  getLatestId = id => {
    if (id !== this.state.id) {
      this.setState({
        id,
        errorData: "",
        data: [],
        loadingData: true,
        nullSelected: true
      });
      const { page } = this.state;

      GetGenericContentListing(id)
        .then(res => {
          let data = res.data.GetGenericContentByCategoryIDListingResult
            ? res.data.GetGenericContentByCategoryIDListingResult
                .GenericContents
            : res.data.ForTest_GenericContentResult
            ? res.data.ForTest_GenericContentResult.GenericContents
            : [];

          let count = res.data.GetGenericContentByCategoryIDListingResult
            ? res.data.GetGenericContentByCategoryIDListingResult.TotalPages *
              10
            : res.data.ForTest_GenericContentResult &&
              res.data.ForTest_GenericContentResult.TotalPages * 10;

          this.setState({
            data,
            loadingData: false,
            count,
            nullSelected: false
          });
        })
        .catch(error => {
          let err = error.message;
          this.setState({
            errorData: err,
            loadingData: false,
            nullSelected: false
          });
        });
    }
  };

  changePage = page => {
    this.setState({ page: page });
    this.setState({
      data: [],
      errorData: "",
      loadingData: true
    });

    GetAllGroups(page)
      .then(res => {
        let data = res.data.Result.GroupsData || [];
        let count = res.data.Result.TotalCount;

        this.setState({
          data: data,
          count: count,
          errorData: "",
          loadingData: false
        });
      })
      .catch(error => {
        let err = error.message;
        this.setState({
          errorData: "",
          loadingData: false
        });
      });
  };

  static  getDerivedStateFromProps() {
    return true;
  }

  render() {
    const {
      errorData,
      data,
      roles,
      page,
      count,
      nullSelected,
      loadingData
    } = this.state;
    return (
      <Grow in={true}>
        <div>
          <Typography variant="h6" gutterBottom>
            Security
          </Typography>
          <Grid container>
            <Grid item xs={12}>
              <Table
                title="Groups"
                columns={security_module_columns}
                data={data}
                isLoading={loadingData}
                page={page}
                totalRecords={count}
                error={errorData}
                onChangePage={page => this.changePage(page)}
                onSuccess={this.reloadContent}
                onRefresh={this.reloadContent}
                nullSelected={nullSelected}
                variant={1}
                roles={roles}
              />
            </Grid>
          </Grid>
        </div>
      </Grow>
    );
  }
}
