import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { my_tasks_module_columns } from "../../server/columns";
import { GetMyTaskPaged } from "../../server";
import SearchMyTaskComponent from "./SearchMyTaskComponent";
import { getVitals } from "../../server/utils";
import * as Actions from "../../redux/actions/index";
import { useDispatch, useSelector } from "react-redux";

function MyTasks(props) {
  const dispatch = useDispatch();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const [AssignBy, setAssignBy] = useState("");
  const [Status, setStatus] = useState("");
  const [nKeywordTitle, setnKeywordTitle] = useState("");
  const [Location, setLocation] = useState("");
  const [nID, setnID] = useState("");
  const { UserID } = getVitals();
  const status = useSelector(
    (StatusReducer) => StatusReducer.StatusReducer.Dcc_Status
  );
  const [currentStatus, setCurrentStatus] = useState("");

  useEffect(() => {
    dispatch(Actions.setModule("Indox"));
  }, []);

  useEffect(() => {
    GetMyTaskPaged(page, UserID)
      .then((res) => {
        if (res.data.result === true) {
          let data = res.data.UserTaskData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
    Actions.setStatus(status[1]);
    setCurrentStatus(status[1]);
  }, []);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetMyTaskPaged(page, UserID)
      .then((res) => {
        if (res.data.result === true) {
          let data = res.data.UserTaskData;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };
  const changePage = (page) => {
    setPage(page);
    setLoading(true);
    setData([]);
    setError("");
    GetMyTaskPaged(page, UserID, null, null, {
      AssignBy, Status, nKeywordTitle, Location, nID
    })
      .then((res) => {
        let data = res.data.UserTaskData;
        let count = res.data.TotalCount;
        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch((error) => {
        let err = error.message;
        setError(err);
        setLoading(false);
      });
  };


  const searchPage = (obj) => {
    const { AssignBy, Status, nKeywordTitle, Location, nID } = obj;
    setAssignBy(AssignBy);
    setStatus(Status);
    setnKeywordTitle(nKeywordTitle);
    setLocation(Location);
    setnID(nID);
    GetMyTaskPaged(0, UserID, null, null, obj).then((res) => {
      let data = res.data.UserTaskData;
      let count = res.data.TotalCount;
      setData(data);
      setLoading(false);
      setCount(count);
      setPage(0);
    })
      .catch((error) => {
        let err = error.message;
        setError(err);
        setLoading(false);
      });
  }

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          My Tasks
        </Typography>
        <SearchMyTaskComponent
          setData={setData}
          setLoading={setLoading}
          setCount={setCount}
          setError={setError}
          searchPage={searchPage}
        />
        <Grid container>
          <Grid item xs={12}>
            <Table
              title="My Tasks"
              columns={my_tasks_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              onSuccess={reloadContent}
              page={page}
              totalRecords={count}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={19}
              projects={projects}
              setData={setData}
              // listType={"all-users"}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(MyTasks);
