import React, { useState } from "react";
import Axios from "axios";
import Alert from "../../components/Alert";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { Redirect } from "react-router-dom";
import BackgroundImage from "../../assets/images/sign-bg.jpg";
import Logo from "../../assets/images/logo.png";
import history from "../../history";
import * as Actions from "../../redux/actions/user.actions";
import { useDispatch } from "react-redux";

import {
  Grid,
  Paper,
  TextField,
  Button,
  FormControlLabel,
  Checkbox,
  InputAdornment,
  IconButton,
  LinearProgress,
  Slide
} from "@material-ui/core";
import { Signin } from "../../server";
import { styles } from "../../styles";
import { isValidEmail } from "../../server/utils";

function Login() {
  const dispatch = useDispatch();
  let email = window.localStorage.getItem("username");
  let token = window.localStorage.getItem("accessToken");
  let isValid = token && token.length > 5;

  const [username, setUsername] = useState(email);
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [showPassword, setshowPassword] = useState(false);
  const [error, setError] = useState("");
  const [checked, setCheckbox] = useState(email ? email.length !== 0 : false);

  function handleCheck(e) {
    setCheckbox(e.target.checked);
  }

  function handleChange(name, e) {
    setError("");
    let value = e.target.value;
    if (name === "username") {
      setUsername(value);
    } else {
      setPassword(value);
    }
  }

  function handleShowPassword() {
    setshowPassword(!showPassword);
  }

  function handleSubmit() {
    if (checked) {
      window.localStorage.setItem("username", username);
    } else if (!checked) {
      window.localStorage.removeItem("username");
    }
    let isEmailValid = isValidEmail(username);
    // if (!isEmailValid) {
    //   setError("Invalid Email provided!");
    // } else
    if (password.length < 8) {
      setError("Password cannot be less than 8 characters!");
    } else if (
      password.length > 7
      // && isEmailValid
    ) {
      setLoading(true);
      Signin(username, password)
        .then(res => {
          setLoading(false);
          if (res.data.Result.result === false) {
            setError(res.data.Result.message);
          } else {
            history.go();
            dispatch(Actions.getAllRoles());
            history.push("/my-tasks");
            let token = res.data.Result.Token;
            let username = res.data.Result.UsersData[0].Username;
            let id = res.data.Result.UsersData[0].ID;
            let name =
              res.data.Result.UsersData[0].Name +
              ` ${res.data.Result.UsersData[0].FamilyName}`;
            let RoleID = res.data.Result.UsersData[0].RoleId;
            Axios.defaults.headers["Authorization"] = token;
            window.localStorage.setItem("accessToken", token);
            window.localStorage.setItem("name", name);
            window.localStorage.setItem("email", username);
            window.localStorage.setItem("language", "1");
            window.localStorage.setItem("userID", id);
            window.localStorage.setItem("userID", id);
            window.localStorage.setItem("RoleID", RoleID);
            setSuccess(true);
          }
        })
        .catch(err => {
          setLoading(false);
          let error = err.message;
          setError(error);
        });
    }
  }

  return isValid ? (
    <Redirect to="/dashboard" />
  ) : !success ? (
    <Grid container style={styles.login_container} justify="flex-end">
      {loading && (
        <div style={styles.progress_container}>
          <LinearProgress />
        </div>
      )}
      <Alert
        message={error}
        type="error"
        open={error.length > 0}
        onClose={() => setError("")}
      />
      <Slide
        in
        direction="right"
        timeout={{
          appear: 100,
          enter: 500,
          exit: 2000
        }}
      >
        <Grid item md={9} xs={12}>
          <img
            style={styles.responsive_image}
            src={BackgroundImage}
            alt="Backgound"
          />
        </Grid>
      </Slide>
      <Slide
        in
        direction="left"
        timeout={{
          appear: 100,
          enter: 500,
          exit: 2000
        }}
      >
        <Grid item md={3} xs={12}>
          <Paper style={styles.login_form_container} square elevation={11}>
            <form
              style={{
                ...styles.d_flex,
                ...styles.flex_column,
                ...styles.flex_1
              }}
              onSubmit={e => {
                e.preventDefault();
                handleSubmit();
              }}
            >
              <div style={styles.logo_holder_style} className="logo-holder">
                <a href="#">
                  <img style={styles.logo_style} src={Logo} alt="Logo" />
                </a>
              </div>
              <TextField
                type="username"
                placeholder=""
                value={username}
                label="Username"
                fullWidth
                onChange={e => handleChange("username", e)}
              />
              <TextField
                type={showPassword ? "text" : "password"}
                placeholder="Password"
                onChange={e => handleChange("password", e)}
                label="Password"
                fullWidth
                style={styles.mt_3}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="Toggle password visibility"
                        onClick={() => handleShowPassword()}
                      >
                        {showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
              <Button
                onClick={() => handleSubmit()}
                disabled={loading}
                style={styles.login_button}
                color="primary"
                variant="contained"
                type="submit"
              >
                Login
              </Button>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={checked}
                    onChange={e => handleCheck(e)}
                    value="checkedB"
                    color="primary"
                  />
                }
                label="Remember me"
              />
            </form>
          </Paper>
        </Grid>
      </Slide>
    </Grid>
  ) : (
    <Redirect to="/dashboard" />
  );
}

export { Login };
