import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router-dom";
import {
  Grow,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Button,
  Input,
} from "@material-ui/core";
import { Table } from "../../components/Table";
import {
  sent_module_columns,
  projects_module_columns,
  my_tasks_module_columns,
} from "../../server/columns";
import {
  GetAllUsers,
  GetAllReceivedNotifications,
  GetAllProjects,
  GetAllProjectNotifications,
  GetMyTasksById,
  GetClientVisitByClientID,
} from "../../server";
import { styles } from "../../styles";
import { DatePicker } from "@material-ui/pickers";

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  expansionPanel: {
    marginBottom: 16,
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

function SearchVisitListComponent({
  setData,
  page,
  setError,
  setLoading,
  setCount,
}) {
  // const { id } = props.match.params;

  const [projects, setProjects] = useState([]);
  const [loadingProjects, setLoadingProjects] = useState(false);
  const [project, setProject] = useState("");
  const [startDate, setStartDate] = useState(null);
  const [taskId, setTaskId] = useState("");

  const [description, setDescription] = useState("");
  const [type, setType] = useState("");
  const [location, setLocation] = useState("");
  const [status, setStatus] = useState("");
  const [assign, setAssign] = useState("");

  const [endDate, setEndDate] = useState(null);
  const [projectID, setProjectID] = useState(null);
  const [clients, setClients] = useState([]);
  const [users, setUsers] = useState([]);
  // const [loading, setLoading] = useState(false);
  // const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);

  const [assignUser, setAssignUser] = useState([]);

  let visit_types = [
    { name: "Visit", id: 1 },
    { name: "Project", id: 2 },
  ];

  let status_types = [
    { name: "Agree", id: 1 },
    { name: "Agree With Modification", id: 2 },
    { name: "Disagree", id: 3 },
  ];

  const classes = useStyles();
  useEffect(() => {
    GetAllUsers()
      .then((res) => {
        let data = res.data.Result.UsersData || [];
        setUsers(data);
      })
      .catch((error) => {
        console.log("Error fetching users...");
      });
  }, []);

  function searchData() {
    GetClientVisitByClientID(page, description, type, location, status)
      .then((res) => {
        if (res.data.result === true) {
          let data = res.data.ClientVisitArray || [];
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(data);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }

  return (
    <Grow in={true}>
      <div>
        {/* <Typography variant="h6" gutterBottom>
          Inquiry
        </Typography> */}
        <Grid container>
          <Grid item xs={12}>
            {loadingProjects ? (
              <div style={{ ...styles.d_flex, ...styles.justify_center }}>
                <CircularProgress />
              </div>
            ) : (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Name</InputLabel>
                    <Input
                      value={description}
                      onChange={(e) => {
                        setDescription(e.target.value);
                      }}
                    />
                  </FormControl>
                  {/* <DatePicker
                  autoOk
                  label="Start Date"
                  clearable
                  disableFuture
                  value={startDate}
                  onChange={date => setStartDate(date)}
                /> */}
                </Grid>
                <Grid item xs={3}>
                  <FormControl
                    className={classes.formControl}
                    style={{ ...styles.my_3 }}
                    fullWidth
                  >
                    <InputLabel htmlFor="age-simple">Type*</InputLabel>
                    <Select
                      value={type}
                      onChange={(e) => {
                        setType(e.target.value);
                      }}
                    >
                      {visit_types &&
                        visit_types.map((item) => {
                          return (
                            <MenuItem value={item.id}>{item.name}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>
                  {/* <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Type</InputLabel>
                    <Input
                      value={type}
                      onChange={e => {
                        setType(e.target.value);
                      }}
                    />
                  </FormControl> */}

                  {/* <DatePicker
                  autoOk
                  label="End Date"
                  clearable
                  disableFuture
                  value={endDate}
                  onChange={date => setEndDate(date)}
                /> */}
                </Grid>
                <Grid item xs={3}>
                  <FormControl
                    className={classes.formControl}
                    style={{ ...styles.my_3 }}
                    fullWidth
                  >
                    <InputLabel htmlFor="age-simple">Assign By*</InputLabel>
                    <Select
                      value={assign}
                      onChange={(e) => {
                        setAssign(e.target.value);
                      }}
                    >
                      {users &&
                        users.map((item) => {
                          return (
                            <MenuItem value={item.ID}>{item.Name}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>

                  {/* <DatePicker
                  autoOk
                  label="End Date"
                  clearable
                  disableFuture
                  value={endDate}
                  onChange={date => setEndDate(date)}
                /> */}
                </Grid>
                {/* <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={getTaskById}
                    // onClick={e => getContent("date")}
                    color="primary"
                    variant="contained"
                    disabled={taskId === ""}
                    // disabled={(startDate === null) && (endDate === null)}
                  >
                    Search
                  </Button>
                </Grid> */}
              </Grid>
            )}
            {!loadingProjects && (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                style={styles.mb_3}
              >
                <Grid item xs={3}>
                  <FormControl style={{ ...styles.my_3 }} fullWidth>
                    <InputLabel htmlFor="age-simple">Location</InputLabel>
                    <Input
                      value={location}
                      onChange={(e) => {
                        setLocation(e.target.value);
                      }}
                    />
                  </FormControl>
                  {/* <DatePicker
                    autoOk
                    label="Start Date"
                    clearable
                    disableFuture
                    value={startDate}
                    onChange={date => setStartDate(date)}
                  /> */}
                </Grid>
                <Grid item xs={3}>
                  <FormControl
                    className={classes.formControl}
                    style={{ ...styles.my_3 }}
                    fullWidth
                  >
                    <InputLabel htmlFor="age-simple">Status*</InputLabel>
                    <Select
                      value={status}
                      onChange={(e) => {
                        setStatus(e.target.value);
                      }}
                    >
                      {status_types &&
                        status_types.map((item) => {
                          return (
                            <MenuItem value={item.name}>{item.name}</MenuItem>
                          );
                        })}
                    </Select>
                  </FormControl>

                  {/* <DatePicker
                    autoOk
                    label="End Date"
                    clearable
                    disableFuture
                    value={endDate}
                    onChange={date => setEndDate(date)}
                  /> */}
                </Grid>
                <Grid item xs={3}>
                  <Button
                    fullWidth
                    onClick={searchData}
                    // onClick={e => getContent("date")}
                    color="primary"
                    variant="contained"
                    disabled={
                      status === "" &&
                      location === "" &&
                      assign === "" &&
                      description === "" &&
                      type === ""
                        ? true
                        : false
                    }
                    // disabled={(startDate === null) && (endDate === null)}
                  >
                    Search
                  </Button>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(SearchVisitListComponent);
