import React, { useEffect, useState } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { CircularProgress } from "@material-ui/core";
import { styles } from "../../styles";
import { ThumbUp, Close } from "@material-ui/icons";
import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  IconButton,
  Typography,
  FormControlLabel,
  Checkbox,
  TableCell,
  Table,
  TableRow,
  TableBody,
  TableHead,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { URL } from "../../BaseUrl";

const useStyles = makeStyles((theme) => ({
  formControl: {
    // margin: theme.spacing(1),
    width: "100%",
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function RoleDialog({
  open,
  closer,
  roleID,
  AssignRole,
  rulesOnId,
  setRoleLoading,
  RoleLoading,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [Modules, setModules] = useState(null);
  const [RuleSet, setRules] = useState([]);
  const [ModuleID, setModuleID] = useState(0);
  const [add, setAdd] = useState(0);

  const GetAllModules = async () => {
    let res = await axios.post(URL + "/api/GetAllModules");
    if (res.data) {
      setModules(res.data);
    } else {
      console.log("No roles");
    }
  };

  useEffect(() => {
    GetAllModules();
  }, []);

  const [baseArr, setBaseArr] = useState([]);
  const [ModulesArr, setModulesArr] = useState([]);


  useEffect(() => {
    let myNewArr = [];
    if (rulesOnId.length > 0) {
 

      rulesOnId.map((value) => {
        let myArr23 = [
          {
            CurrentId: value.Id || -1,
            ModuleId: value.ModuleId,
            RoleTypeId: roleID,
            IsAllow: value.IsAllow,
            Action: value.Action,
          },
        ];
        myNewArr = [...myNewArr, ...myArr23];
        setBaseArr(myNewArr);
      });
    } else {

      if (Modules) {
        Modules.map((val) => {
          let myArr43 = [
            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "VIEW",
            },
            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "ADD",
            },

            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "EDIT",
            },
            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "DELETE",
            },
            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "ASSIGN IT",
            },
            {
              CurrentId: -1,
              ModuleId: val.Id,
              RoleTypeId: roleID,
              IsAllow: false,
              Action: "CLOSE",
            },
          ];
          myNewArr = [...myNewArr, ...myArr43];
        });
        setBaseArr(myNewArr);
      }
    }
    if (baseArr.length > 0) {
      setRoleLoading(false);
    }
  }, [rulesOnId, roleID]);

  return (
    <Dialog
      style={{ margin: "5% 15%" }}
      open={open}
      fullWidth
      maxWidth="xl"
      onClose={() => {
        closer(false);
        setBaseArr([]);
      }}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            align: "center",
            width: "100%",
          }}
          // className="flex flex-row justify-between items-center"
        >
          <span>Permission</span>

          <IconButton
            onClick={(e) => {
              e.stopPropagation();
              closer(false);
              //   setMessage("");
            }}
          >
            <Close />
          </IconButton>
        </div>
      </DialogTitle>

      {RoleLoading ? (
        <div style={styles.loading_container}>
          <CircularProgress />
        </div>
      ) : (
        <>
          <DialogContent dividers>
            <>
              <div
                //   className="flex flex-col "
                style={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                {Modules && (
                  <Table
                    className={classes.table}
                    style={{ width: "100%" }}
                    aria-label="simple table"
                  >
                    <TableHead>
                      <TableRow>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Modules</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>View</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Add</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Edit</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Delete</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Can Assign</h2>
                        </TableCell>
                        <TableCell className={classes.CellHead} align="left">
                          <h2>Close</h2>
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {Modules &&
                        Modules.map((val, i) => {
                          return (
                            <TableRow>
                              <TableCell key={val.Id} align="left">
                                <h3>{val.Name}</h3>
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="VIEW"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "VIEW"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "VIEW"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="ADD"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "ADD"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "ADD"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="EDIT"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "EDIT"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "EDIT"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="DELETE"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "DELETE"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "DELETE"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="ASSIGN IT"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "ASSIGN IT"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "ASSIGN IT"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                              <TableCell>
                                <Checkbox
                                  name="CLOSE"
                                  checked={
                                    baseArr &&
                                    baseArr.length > 0 &&
                                    baseArr.filter(
                                      (we) =>
                                        we.ModuleId == val.Id &&
                                        we.Action === "CLOSE"
                                    )[0].IsAllow
                                  }
                                  onChange={(e) => {
                                    let filterdBases = baseArr.filter(
                                      (x) => x.ModuleId == val.Id
                                    );
                                    let filteredToChange = filterdBases.filter(
                                      (x) => x.Action == "CLOSE"
                                    )[0];

                                    filteredToChange.IsAllow = !filteredToChange.IsAllow;

                                    let index = baseArr.findIndex(
                                      (x) => x == filteredToChange
                                    );

                                    let newBase = baseArr;

                                    newBase[index] = filteredToChange;

                                    setBaseArr(newBase);
                                    setAdd(add + 1);
                                  }}
                                />
                              </TableCell>
                            </TableRow>
                          );
                        })}
                    </TableBody>
                  </Table>
                )}
              </div>
            </>
          </DialogContent>
        </>
      )}
      <DialogActions>
        <Button
          variant="contained"
          onClick={(e) => {
            AssignRole(baseArr);
            setBaseArr([]);
          }}
          color="primary"
        >
          Assign Roles
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default RoleDialog;
