export function MultiplePaymentsDelete(ids) {
  let params = {
    nPaymentID: ids.toString(),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeletePayment`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleClientVisit(parameters) {
  const {
    currentID,
    clientID,
    visitType,
    remarks,
    plot,
    block,
    area,
    clientDecision,
    isNew,
    ProjectID,
    Attachment
  } = parameters;
  const { Name } = getVitals();
  let params = {
    ID: currentID,
    ClientID: clientID,
    ProjectID,
    VisitType: visitType,
    CreatedBy: Name,
    Remarks: remarks,
    PortalID: 2,
    IsNew: isNew,
    PlotNo: plot,
    BlockNo: block,
    Area: area,
    ClientDecision: clientDecision,
    Attachment
  };

  let link = `${api}/SaveClientVisit`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
