import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";

export function GetAllVouchers(page) {
  let link = `${api}/GetAllVouchers`;

  let params = {
    nOwnerName: "",
    nVoucherType: "",
    nVoucherNumber: "",
    nPaymentMethod: "",
    nOrderBy: "ID",
    nPageIndex: page,
    nPageSize: 10
  };

  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

export function HandleVoucher(parameters) {
  const {
    type,
    amount,
    projectID,
    id,
    isNew,
    owner,
    payee,
    voucherNo,
    date,
    paymentMethod,
    notes,
    attachments
  } = parameters;
  const { Name } = getVitals();
  let params = {
    oOwnerName: owner,
    oPayee: payee,
    nVoucherType: type,
    nVoucherNumber: voucherNo,
    nDate: date,
    nPaymentMethod: paymentMethod,
    oNotes: notes,
    nAmount: amount,
    nProjectID: projectID,
    nCreatedBy: Name,
    nPortalID: 2,
    nisNew: isNew,
    nCurrentID: isNew ? -1 : id,
    oVoucherCopy: attachments
  };

  let link = `${api}/SaveVoucher`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}

export function MultipleVouchersDelete(ids) {
  let params = {
    nVoucherID: ids.toString(),
    nPortalID: 2
  };
  let promise = new Promise(function(resolve, reject) {
    Axios.post(`${api}/DeleteVoucher`, params)
      .then(res => resolve(res))
      .catch(err => reject(err));
  });
  return promise;
}
