import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { Grow, Typography, Grid ,IconButton} from '@material-ui/core';
import { Table } from '../../components/Table';
import { payments_module_columns } from '../../server/columns';
import { GetAllClients, GetAllProjects, GetAllPayments } from '../../server';
import RecipeReviewCard from '../../components/Card';
import history from "../../history";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";

function Notifications(props) {
  const { id } = props.match.params;
  const dispatch = useDispatch();
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [clients, setClients] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [nullSelected, setNullSelected] = useState(false);

  useEffect(() =>{
    dispatch(Action2.setModule("Notifications"))
  },[])

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError('');
    GetAllPayments()
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.PaymentData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllPayments()
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.PaymentData;
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log('Error fetching projects...');
      });
    GetAllClients()
      .then(res => {
        let data = res.data.Result.ClientsData || [];
        setClients(data);
      })
      .catch(error => {
        console.log('Error fetching clients...');
      });
  }, []);

  return (
    <Grow in={true}>
      <div>
      <IconButton onClick={(ev) =>{
         ev.preventDefault();
         history.goBack();
      }}>
      <ArrowBackIcon />
      </IconButton>
        <Typography variant="h6" gutterBottom>
          Notifications
        </Typography>
        <Grid container spacing={3}>
          <Grid item xs={12} md={4}>
            <RecipeReviewCard variant="sent" />
          </Grid>
          <Grid item xs={12} md={4}>
            <RecipeReviewCard variant="received" />
          </Grid>
          <Grid item xs={12} md={4}>
            <RecipeReviewCard variant="project" />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Notifications);
