import { connect } from "react-redux";
import { toggleLanguage } from "./actions";
import { getCheque } from "./actions";

const mapStateToProps = ({ internationalization: { appLanguage } }) => ({
  appLanguage
});

const mapDispatchToProps = dispatch => ({
  toggleLanguage: id => dispatch(toggleLanguage(id))
});

export const withInternationalization = Component =>
  connect(mapStateToProps, mapDispatchToProps)(Component);
