import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog(props) {
    const classes = useStyles();

    React.useEffect(() => {
        return () => {
            props.resetModalData()
        }
    },[])

    return (
        <div>
            <Dialog fullScreen open={props.imageModalState && props.ModalData} onClose={props.closeImageModal} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={props.closeImageModal} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                        {props.title}
            </Typography>
                        
                    </Toolbar>
                </AppBar>
                {
                    props.ModalData && <List>
                        <ListItem button>
                            <ListItemText primary="Remarks" secondary={props.ModalData.remarks} />
                        </ListItem>
                        <Divider />
                        <ListItem button>
                            <img src={props.ModalData.image} alt={props.ModalData.name} />
                        </ListItem>

                    </List>
                }
            </Dialog>
        </div>
    );
}