import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, IconButton, Paper } from "@material-ui/core";
import { Table } from "../../components/Table";
import { ArrowBackTwoTone } from "@material-ui/icons";
import { visits_module_columns } from "../../server/columns";
import { styles } from "../../styles";
import {
  GetAllVisits,
  GetVisitTasks,
  GetAllProjects,
  GetAllRoleTypes
} from "../../server";
import { VisitDetail } from "../../components/VisitDetails";


function Visits(props) {

  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [roles, setRoles] = useState([]);
  const [selectedVisitID, setSelectedVisitID] = useState(0);
  const [projects, setProjects] = useState([]);
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loadingTasks, setLoadingTasks] = useState(false);
  const [error, setError] = useState("");
  const [errorTasks, setErrorTasks] = useState("");
  const [visitType, setVisitType] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const getVisitTasks = id => {
    setLoadingTasks(true);
    setTasks([]);
    if (id) {
      let visit = data.filter(item => item.ID === id)[0].VisitType;
      setVisitType(visit);
      setSelectedVisitID(id);
      GetVisitTasks(id)
        .then(res => {
          if (res.data.result === true) {
            let tasks = res.data.ClientVisitDataArray;
            setLoadingTasks(false);
            setTasks(tasks);
          } else {
            let ErrorMessage = res.data.message;
            setLoadingTasks(false);
            setErrorTasks(ErrorMessage);
          }
        })
        .catch(err => {
          let ErrorMessage = err.message;
          setLoadingTasks(false);
          setErrorTasks(ErrorMessage);
        });
    } else {
      setLoadingTasks(false);
      setErrorTasks("");
    }
  };

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");

    GetAllVisits(id, page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.ClientVisitArray;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllVisits(id, page)
      .then(res => {
        if (res.data.result === true) {
          let users = res.data.ClientVisitArray;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });

    GetAllRoleTypes()
      .then(res => {
        let Roles = res.data.RolesArray;
        setRoles(Roles);
      })
      .catch(err => console.log("Error fetching Roles"));

     
  }, []);

  const reloadTasks = id => {
    setLoadingTasks(true);
    setErrorTasks("");
    setTasks([]);
    GetVisitTasks(id)
      .then(res => {
        if (res.data.result === true) {
          let tasks = res.data.ClientVisitDataArray;
          setLoadingTasks(false);
          setTasks(tasks);
        } else {
          let ErrorMessage = res.data.message;
          setLoadingTasks(false);
          setErrorTasks(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoadingTasks(false);
        setErrorTasks(ErrorMessage);
      });
  };

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetAllVisits(id, page)
      .then(res => {
        let data = res.data.ClientVisitArray || [];
        let count = res.data.TotalCount;

        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch(error => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <IconButton
            style={{ ...styles.mr_2 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone />
          </IconButton>
          Visits
        </Typography>
        <Grid container component={Paper} elevation={11} spacing={2}>
          <Grid
            item
            xs={12}
            md={6}
            style={{ borderRight: "1px solid lightgrey" }}
          >
            <Table
              title="Visits list"
              columns={visits_module_columns}
              data={data}
              projects={projects}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              page={page}
              totalRecords={count}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              onSelectRow={id => getVisitTasks(id)}
              variant={5}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <VisitDetail
              tasks={tasks}
              loading={loadingTasks}
              error={errorTasks}
              id={selectedVisitID}
              reloadTasks={reloadTasks}
              roles={roles}
              visitType={visitType}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Visits);
