import React, { useEffect } from "react";
import ProtectedRoute from "./components/ProtectedRoute";
import "./App.css";
import { addLocaleData, IntlProvider } from "react-intl";
import en from "react-intl/locale-data/en";
import ar from "react-intl/locale-data/ar";
import english from "./locales/en.json";
import arabic from "./locales/ar.json";
import { withInternationalization } from "./redux/Internationalization/withInternationalization";
import * as Actions from "./redux/actions/index";
import { useDispatch, useSelector } from "react-redux";

addLocaleData([...en, ...ar]);
const messages = {
  en: english,
  ar: arabic
};
function IntlApp(props) {
  const dispatch = useDispatch();
  const { appLanguage } = props;
  const localeMessages = messages[appLanguage || "en"];
  const UserID = useSelector(user => user.userReducer.LoggedUser);

  useEffect(() => {
    dispatch(Actions.getStatus());
    dispatch(Actions.getUserID());
    dispatch(Actions.getAllRoles());
  }, []);

  useEffect(() => {
    if (UserID && UserID[0] && localStorage.getItem("userID")) {
      dispatch(Actions.getUsersRole(UserID[0].RoleId));
    }
  }, [UserID]);


  return (
    <IntlProvider locale={appLanguage || "en"} messages={localeMessages}>
      <ProtectedRoute />
    </IntlProvider>
  );
}

export default withInternationalization(IntlApp);
