import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, IconButton } from "@material-ui/core";
import { Table } from "../../components/Table";
import { ArrowBackTwoTone } from "@material-ui/icons";
import { projects_module_columns } from "../../server/columns";
import { GetClientVisitByClientID } from "../../server";
import { styles } from "../../styles";

function Projects(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetClientVisitByClientID(page, "", 2, "", "", "", id)
      .then((res) => {
        if (res.data.result) {
          let users = res.data.ClientVisitArray;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetClientVisitByClientID(page, "", 2, "", "", "", id)
      .then((res) => {
        if (res.data.result) {
          let users = res.data.ClientVisitArray;
          let count = res.data.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.Result.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  }, []);

  const changePage = (page) => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetClientVisitByClientID(page, "", 2, "", "", "")
      .then((res) => {
        let data = res.data.ClientVisitArray;
        let count = res.data.TotalCount;
        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch((error) => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        >
          <IconButton
            style={{ ...styles.mr_2 }}
            onClick={() => props.history.goBack()}
          >
            <ArrowBackTwoTone />
          </IconButton>
          Projects
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title="Projects list"
              columns={projects_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              page={page}
              totalRecords={count}
              onChangePage={changePage}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={6}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Projects);
