import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Badge from "@material-ui/core/Badge";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import SecurityIcon from "@material-ui/icons/SecurityOutlined";
import PersonIcon from "@material-ui/icons/PersonOutline";
import BusinessIcon from "@material-ui/icons/BusinessOutlined";
import NewReleasesIcon from "@material-ui/icons/NewReleasesOutlined";
import MediaIcon from "@material-ui/icons/ImageOutlined";
import FaqIcon from "@material-ui/icons/HelpOutline";
import AttachMoney from "@material-ui/icons/AttachMoney";
import BuildIcon from "@material-ui/icons/Build";
import LockIcon from "@material-ui/icons/Lock";
import { AppbarMenu } from "./AppbarMenu";
import { Link, Switch, Route, withRouter } from "react-router-dom";
import { Users } from "../../pages/Users";
import { Security } from "../../pages/Security";
import { FormattedMessage } from "react-intl";
import { InternationalizationMenu } from "./InternationalizationMenu";
import { ReportsApp } from "../../pages/Reports";
import { version } from "../../server/utils";
import { styles } from "../../styles";
import { Clients } from "../../pages/Clients";
import { Expenses } from "../../pages/Expenses";
import { Projects } from "../../pages/Projects";
import { Visits } from "../../pages/Visits";
import { Tasks } from "../../pages/Tasks";
import { AllUsers } from "../../pages/AllUsers";
import { MyTasks } from "../../pages/MyTasks";
import { ProjectDetails } from "../../components/ProjectDetails";
import { VisitList } from "../../pages/VisitList";
import { AddNewVisit } from "../../components/AddNewVisit";
import ActivityLogs from "../../pages/ActivityLogs/ActivityLogs";
import { SupervisionDetails } from "../../pages/SupervisionDetails";
import {
  Money,
  CardGiftcard,
  Payment,
  Subject,
  Notifications,
  QuestionAnswer,
} from "@material-ui/icons";
import { Payments } from "../../pages/Payments";
import { Vouchers } from "../../pages/Vouchers";
import { PaymentCheques } from "../../pages/Submissions";
import Submissions from "../../pages/Submissions/Submissions";
import { Notification } from "../../pages/Notifications";
import { SentNotifications } from "../../pages/SentNotifications";
import { ReceivedNotifications } from "../../pages/ReceivedNotifications";
import { ProjectNotifications } from "../../pages/ProjectNotifications";
import Inquiry from "../../pages/Inquiry/Inquiry";
import Submissions2 from "../../pages/Submissions/Submissions2";
import Roles from "../../pages/Roles/Roles";
import { VisitProjectForm } from "../../pages/VisitProjectForm";
import { VisitDetailForm } from "../../pages/VisitDetailForm";
import { Tools } from "../../pages/Tools";
import { Permissions } from "../../pages/Permissions";
import { ProjectFollowUp } from "../../pages/ProjectFollowUp";
import { ProjectReAssign } from "../../pages/ReAssigningProjects";
import AccountTreeIcon from "@material-ui/icons/AccountTree";
import AssignmentIcon from "@material-ui/icons/Assignment";
import EqualizerIcon from '@material-ui/icons/Equalizer';
import NewInbox from "../../pages/New Inbox";
import ProjectStatus from "../../pages/projectStatus";

const drawerWidth = 250;
const drawerColor = "#2d323e";

const IconsWidth = {
  minWidth: "36px",
  marginLeft: "3px",
};

const IconsColor = {
  color: "#fff",
  fontSize: "18px",
};

const DividerColor = {
  background: "rgba(255,255,255,0.2)",
};

const drawerText = {
  fontSize: "14px",
  color: "#fff",
  ...styles.text_capitalize,
};

const versionStyles = {
  ...styles.flex_1,
  ...styles.d_flex,
  ...styles.justify_center,
  ...styles.alignItems_end,
  ...styles.p_4,
  color: "#bcbdbf",
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    background: "#272b35",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
    background: "rgba(0,0,0,0.1)",
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    background: drawerColor,
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    background: drawerColor,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(7) + 1,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function MiniDrawer(props) {
  const { match } = props;
  let path = match.path;
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const UserRole = useSelector((user) => user.userReducer.UserRole);
  const FinalRoles = useSelector((user) => user.userReducer.FinalRoles);
  const [access, setAccess] = useState([]);

  useEffect(() => {
    setNewAccess();
  }, [UserRole, FinalRoles]);

  function setNewAccess() {
    let arr = {};
    FinalRoles.map((val) => {
      if (parseInt(localStorage.getItem("RoleID")) === val.RoleTypeId) {
        if (val.Action === "VIEW") {
          arr[val.ModuleName] = val.IsAllow;
        }
      }
    });
    setAccess([arr]);
  }

  const { signout } = props;
  function handleDrawerOpen() {
    setOpen(true);
  }

  function handleDrawerClose() {
    setOpen(false);
  }

  return (
    <div className={classes.root} id="app-main">
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="Open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            noWrap
            style={styles.appbar_heading}
            component={Link}
            to="/dashboard"
          >
            <FormattedMessage id="common.dashboard" />
          </Typography>
          <InternationalizationMenu />
          <AppbarMenu signout={signout} />
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
        open={open}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon style={styles.text_white} />
            ) : (
              <ChevronLeftIcon style={styles.text_white} />
            )}
          </IconButton>
        </div>

        {access.map((val) => {
          return (
            <List>
              {/* <ListItem
            button
            component={Link}
            to={`/generic-content`}
            selected={path === '/generic-content'}
          >
            <ListItemIcon>
              <InboxIcon />
            </ListItemIcon>
            <ListItemText
              primary={<FormattedMessage id={`common.genericContent`} />}
              primaryTypographyProps={{
                style: styles.text_capitalize
              }}
            />
          </ListItem> */}

              <ListItem
                button
                component={Link}
                to={`/inbox`}
                selected={path === "/inbox"}
                className={classes.selected}
                style={{ display: val.Indox ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <QuestionAnswer style={IconsColor} />
                </ListItemIcon>
                <ListItemText style={drawerText} primary={"Inbox"} />
              </ListItem>
              <ListItem
                button
                component={Link}
                to={`/my-tasks`}
                selected={path === "/my-tasks"}
                className={classes.selected}
                style={{ display: val.Indox ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <QuestionAnswer style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.my-tasks`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/visit-list`}
                selected={path === "/visit-list"}
                className={classes.selected}
                style={{ display: val["Visit List"] ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <Subject style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.visit-list`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/clients`}
                selected={path === "/clients"}
                className={classes.selected}
                style={{ display: val.Clients ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <PersonIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.clients`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/inquiry`}
                selected={path === "/inquiry"}
                className={classes.selected}
                style={{ display: val.Inquiry ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <QuestionAnswer style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.inquiry`} />}
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to={`/notifications`}
                selected={path === "/notifications"}
                className={classes.selected}
                style={{ display: val.Notifications ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <Notifications style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.notifications`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/expenses`}
                selected={path === "/expenses"}
                className={classes.selected}
                style={{ display: val.Expenses ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <AttachMoney style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.expenses`} />}
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to={`/reports`}
                selected={path === "/reports"}
                className={classes.selected}
                style={{ display: val.Reports ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <Subject style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.reports`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/roles`}
                selected={path === "/roles"}
                className={classes.selected}
                style={{ display: val.Roles ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <PersonIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.roles`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/all-users`}
                selected={path === "/all-users"}
                className={classes.selected}
                style={{ display: val.Users ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <PersonIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`common.all-users`} />}
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to={`/Tools`}
                selected={path === "/Tools"}
                className={classes.selected}
                style={{ display: val.Tools ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <BuildIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`tools`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/ProjectFollowUp`}
                selected={path === "/ProjectFollowUp"}
                className={classes.selected}
                style={{ display: val.ProjectFollowUp ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <AccountTreeIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`Project Follow Up`} />}
                />
              </ListItem>

              <ListItem
                button
                component={Link}
                to={`/ProjectReAssign`}
                selected={path === "/ProjectReAssign"}
                className={classes.selected}
                style={{ display: val.ProjectReAssign ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <AssignmentIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                  style={drawerText}
                  primary={<FormattedMessage id={`Project Reassign`} />}
                />
              </ListItem>
              <ListItem
                  button
                  component={Link}
                  to={`/projectRank`}
                  selected={path === "/projectRank"}
                  className={classes.selected}
                  style={{ display: val.Tools ? "flex" : "none" }}
              >
                <ListItemIcon style={IconsWidth}>
                  <EqualizerIcon style={IconsColor} />
                </ListItemIcon>
                <ListItemText
                    style={drawerText}
                    primary={<FormattedMessage id={`Project Status`} />}
                />
              </ListItem>

              {/* <ListItem
            button
            component={Link}
            to={`/content`}
            selected={path === '/content'}
            className={classes.selected}
          >
            <ListItemIcon style={IconsWidth}>
              <InboxIcon style={IconsColor} />
            </ListItemIcon>
            <ListItemText
              style={drawerText}
              primary={<FormattedMessage id={`common.content`} />}
            />
          </ListItem>
          <ListItem
            button
            component={Link}
            to={`/banner`}
            selected={path === '/banner'}
            className={classes.selected}
          >
            <ListItemIcon style={IconsWidth}>
              <BannerIcon style={IconsColor} />
            </ListItemIcon>
            <ListItemText
              style={drawerText}
              primary={<FormattedMessage id={`common.banner`} />}
            />
          </ListItem>
          <ListItem
            button
            component={Link}
            to={`/news`}
            selected={path === '/news'}
            className={classes.selected}
          >
            <ListItemIcon style={IconsWidth}>
              <NewReleasesIcon style={IconsColor} />
            </ListItemIcon>
            <ListItemText
              style={drawerText}
              primary={<FormattedMessage id={`common.news`} />}
            />
          </ListItem>
          <ListItem
            button
            component={Link}
            to={`/media`}
            selected={path === '/media'}
            className={classes.selected}
          >
            <ListItemIcon style={IconsWidth}>
              <MediaIcon style={IconsColor} />
            </ListItemIcon>
            <ListItemText
              style={drawerText}
              primary={<FormattedMessage id={`common.media`} />}
            />
          </ListItem>
          <ListItem
            button
            component={Link}
            to={`/faqs`}
            selected={path === '/faqs'}
            className={classes.selected}
          >
            <ListItemIcon style={IconsWidth}>
              <FaqIcon style={IconsColor} />
            </ListItemIcon>
            <ListItemText
              style={drawerText}
              primary={<FormattedMessage id={`common.faqs`} />}
            />
          </ListItem> */}
            </List>
          );
        })}

        {open && <div style={versionStyles}>Version {version}</div>}
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Switch>
          {/* <Route path="/generic-content" component={GenericContent} /> */}
          <Route exact path="/users/:id" component={Clients} />
          <Route path="/expenses" component={Expenses} />
          <Route path="/reports" component={ReportsApp} />
          <Route path="/payments" component={Payments} />
          <Route path="/submissions" component={Submissions} />
          <Route path="/submissions2" exact={true} component={Submissions2} />
          <Route path="/vouchers" component={Vouchers} />
          <Route path="/projects/:id" component={Projects} />
          <Route path="/visits/:id" component={Visits} />
          <Route path="/inbox" component={NewInbox} />
          <Route
            exact
            path="/notifications/sent"
            component={SentNotifications}
          />
          <Route
            exact
            path="/notifications/received"
            component={ReceivedNotifications}
          />
          <Route
            exact
            path="/notifications/project"
            component={ProjectNotifications}
          />
          <Route exact path="/inquiry" component={Inquiry} />
          <Route exact path="/Tools" component={Tools} />
          {/* <Route
            exact
            path="/notifications/received"
            component={DashboardPage}
          />
          <Route
            exact
            path="/notifications/project"
            component={DashboardPage}
          /> */}
          <Route exact path="/activity-logs/:id" component={ActivityLogs} />
          <Route
            exact
            path="/supervision-details/:id"
            component={SupervisionDetails}
          />
          <Route exact path="/notifications" component={Notification} />
          <Route path="/project-tasks/:id" component={Tasks} />
          <Route path="/dashboard" component={ReportsApp} />
          <Route path="/clients" component={Clients} />
          <Route path="/all-users" component={AllUsers} />
          <Route exact path="/my-tasks" component={MyTasks} />
          <Route path="/my-tasks/:id" component={ProjectDetails} />
          <Route path="/visit-list" component={VisitList} />
          <Route path="/add-new-visit/:id/:new" component={AddNewVisit} />
          <Route path="/roles" component={Roles} />
          <Route path="/trade" component={Users} />
          <Route path="/security" component={Security} />
          <Route path="/content" component={Users} />
          <Route path="/banner" component={Users} />
          <Route path="/faqs" component={Users} />
          <Route path="/media" component={Users} />
          <Route path="/news" component={Users} />
          <Route
            path="/visit-list-form/:id/:plot/:status"
            component={VisitProjectForm}
          />
          <Route path="/visit-detail-form/:id" component={VisitDetailForm} />
          <Route path="/permissions" component={Permissions} />
          <Route path="/ProjectFollowUp" component={ProjectFollowUp} />
          <Route path="/ProjectReassign" component={ProjectReAssign} />
          <Route path="/projectRank" component={ProjectStatus} />
        </Switch>
      </main>
    </div>
  );
}

export default withRouter(MiniDrawer);
