import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { vouchers_module_columns } from "../../server/columns";
import { GetAllClients, GetAllProjects, GetAllVouchers } from "../../server";

function Vouchers(props) {
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setError("");
    GetAllVouchers(page)
      .then(res => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.VouchersData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllVouchers(page)
      .then(res => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.VouchersData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch(err => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllProjects()
      .then(res => {
        let data = res.data.Result.ProjectsData || [];
        setProjects(data);
      })
      .catch(error => {
        console.log("Error fetching projects...");
      });
  }, []);

  const changePage = page => {
    setPage(page);
    setLoading(true);
    setData([]);
    GetAllVouchers(page)
      .then(res => {
        let data = res.data.Result.VouchersData || [];
        let count = res.data.Result.TotalCount;

        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch(error => {
        let err = error.message;

        setError(err);
        setLoading(false);
      });
  };

  return (
    <Grow in={true}>
      <div>
        <Typography variant="h6" gutterBottom>
          Vouchers
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={vouchers_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              page={page}
              totalRecords={count}
              onSuccess={reloadContent}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={14}
              projects={projects}
            />
          </Grid>
        </Grid>
      </div>
    </Grow>
  );
}

export default withRouter(Vouchers);
