import Axios from "axios";
import { api } from "../api/api";
// import { getVitals } from "./utils";

export function GetAllProjectsPagged(page, projectID) {

  const params = {
    nKeywordTitle: "",
    nProjectType: "",
    nProjectID: localStorage.getItem("ProjectIDssss"),
    nClientID: null,
    nDateFrom: "",
    nDateTo: "",
    nContractType: "",
    nMobileNumber: "",
    nPlotNo: "",
    nStatus: null,
    nOrderBy: "A.ProjectTitle",
    nPageIndex: page,
    nPageSize: 10
  };

  let link = `${api}/GetAllProjects`;
  let promise = new Promise(function(resolve, reject) {
    Axios.post(link, params)
      .then(res => {
        resolve(res);
      })
      .catch(err => reject(err));
  });
  return promise;
}

