import Axios from "axios";
import { api } from "../api/api";
import { getVitals } from "./utils";

export function GetAllClients(page, size, Name, phone, email) {
  let link = `${api}/GetAllClients`;
  let params = {
    nKeywordTitle: Name || "",
    nEmail: email || "",
    nPhoneNumber: phone || "",
    nOrderBy: "ClientName",
    nPageIndex: page,
    nPageSize: size || 1000000,
  };

  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => reject(err));
  });
  return promise;
}

export function HandleClient(parameters) {
  const {
    nClientName,
    nMobileNumber,
    nEmail,
    nWebsite,
    id,
    isNew,
  } = parameters;
  const { Name } = getVitals();
  let params = {
    nClientName,
    nMobileNumber,
    nEmail,
    nCreatedBy: Name,
    nIsDeleted: false,
    nWebsite,
    nCurrentID: isNew ? -1 : id,
    PortalID: 2,
    isNew: isNew,
  };

  let link = `${api}/SaveClient`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

export function MultipleClientsDelete(ids) {
  let params = {
    nClientID: ids.toString(),
    PortalID: 2,
  };
  let promise = new Promise(function (resolve, reject) {
    Axios.post(`${api}/DeleteClient`, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}

export function HandleClientVisit(parameters) {
  const {
    currentID,
    clientID,
    VisitType,
    remarks,
    plot,
    block,
    area,
    clientDecision,
    isNew,
    ProjectID,
    Attachment,
  } = parameters;
  const { Name } = getVitals();
  let params = {
    ID: currentID,
    ClientID: clientID,
    ProjectID,
    VisitType: VisitType,
    CreatedBy: Name,
    Remarks: remarks,
    PortalID: 2,
    IsNew: isNew,
    PlotNo: plot,
    BlockNo: block,
    Area: area,
    ClientDecision: clientDecision,
    Attachment,
  };

  let link = `${api}/SaveClientVisit`;
  let promise = new Promise(function (resolve, reject) {
    Axios.post(link, params)
      .then((res) => resolve(res))
      .catch((err) => reject(err));
  });
  return promise;
}


