import React, { Component } from 'react';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import PlusIcon from '@material-ui/icons/Add';
import AddIcon from '@material-ui/icons/AddCircleOutline';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import CategoryIcon from '@material-ui/icons/Category';
import RemoveIcon from '@material-ui/icons/Remove';
import {
  IconButton,
  Paper,
  MenuList,
  MenuItem,
  ClickAwayListener,
  Button,
  Divider
} from '@material-ui/core';
import FullScreenDialog from '../FullScreenDialog/FullScreenDialog';
import Skeleton from 'react-loading-skeleton';
import AlertDialog from '../Table/ActionAlert';
import { styles } from '../../styles';

var iteration = 1;
export default class NestedList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: [],
      currentID: '',
      uniqueName: '',
      id: '',
      openAddNew: false,
      openEdit: false,
      openDelete: false,
      content: false
    };
  }

  openNew = (id, content) => {
    this.setState({ openAddNew: true, currentID: '', id: id, content });
  };

  openDelete = (id, uniqueName) => {
    this.setState({ openDelete: true, currentID: '', id: id, uniqueName });
  };

  openEdit = id => {
    this.setState({ openEdit: true, currentID: '', id: id });
  };

  handleClick = i => {
    const { open } = this.state;
    let opened = open;
    opened.push(i);
    iteration += 1;
    this.setState({ open: opened });
  };

  handleContextMenu = (id, e) => {
    e.preventDefault();
    if (id === 'main') {
      this.setState({ currentID: id, id: id });
    } else {
      this.setState({ currentID: id, id: id });
    }
  };

  handleClose = item => {
    const { open } = this.state;
    var arr = open;
    iteration -= 1;
    for (var i = 0; i < arr.length; i++) {
      if (arr[i] === item) {
        arr.splice(i, 1);
      }
    }
    let closed = arr;
    this.setState({ open: closed });
  };

  ExpandRecursively = data => {
    const { open, currentID, id } = this.state;
    const { onChangeItem } = this.props;
    return data !== null
      ? data.map((item, i) => (
          <Collapse
            in={open.includes(item.ParentID)}
            timeout="auto"
            unmountOnExit
            key={item.ID}
          >
            <List component="div" disablePadding>
              <ListItem
                style={
                  item.SubCategories !== null
                    ? { paddingLeft: iteration * 10 }
                    : { paddingLeft: (iteration + 1) * 10 }
                }
                button
                onClick={() => {
                  onChangeItem(item.ID);
                  this.setState({ id: item.ID });
                }}
                selected={id === item.ID}
                onContextMenu={e => this.handleContextMenu(item.ID, e)}
              >
                {currentID === item.ID && (
                  <ClickAwayListener
                    onClickAway={() => this.setState({ currentID: '' })}
                  >
                    <Paper
                      style={{
                        position: 'absolute',
                        left: 117,
                        zIndex: 2,
                        top: 21
                      }}
                      elevation={11}
                    >
                      <MenuList>
                        <MenuItem onClick={() => this.openNew(item.ID)} divider>
                          <PlusIcon
                            style={{ ...styles.listIcons, ...styles.mr_2 }}
                          />
                          New
                        </MenuItem>
                        {currentID !== 'main' && (
                          <>
                            <MenuItem
                              onClick={() => this.openEdit(item.ID)}
                              divider
                            >
                              <EditIcon
                                style={{
                                  ...styles.listIcons,
                                  ...styles.mr_2
                                }}
                              />
                              Edit
                            </MenuItem>
                            <MenuItem
                              onClick={() => this.openNew(item.ID, true)}
                              divider
                            >
                              <AddIcon
                                style={{
                                  ...styles.listIcons,
                                  ...styles.mr_2
                                }}
                              />
                              Add Item
                            </MenuItem>
                            <MenuItem
                              onClick={() =>
                                this.openDelete(item.ID, item.UniqueName)
                              }
                              divider
                            >
                              <DeleteIcon
                                style={{
                                  ...styles.listIcons,
                                  ...styles.mr_2
                                }}
                              />
                              Delete
                            </MenuItem>
                          </>
                        )}
                      </MenuList>
                    </Paper>
                  </ClickAwayListener>
                )}
                <ListItemIcon>
                  {item.SubCategories !== null && open.includes(item.ID) ? (
                    <IconButton onClick={() => this.handleClose(item.ID)}>
                      <RemoveIcon
                        style={{
                          ...styles.listIcons
                        }}
                      />
                    </IconButton>
                  ) : (
                    item.SubCategories !== null &&
                    item.SubCategories.length > 0 && (
                      <IconButton onClick={() => this.handleClick(item.ID)}>
                        <PlusIcon
                          style={{
                            ...styles.listIcons
                          }}
                        />
                      </IconButton>
                    )
                  )}
                </ListItemIcon>
                <ListItemText primary={item.Name} />
              </ListItem>

              {this.ExpandRecursively(item.SubCategories)}
            </List>
          </Collapse>
        ))
      : null;
  };

  findById = data => {
    const { id } = this.state;
    var i, temp;
    for (i = 0; i < data.length; i++) {
      if (data[i].ID == id) {
        return [data[i]];
      }
      if (data[i].SubCategories !== null) {
        temp = this.findById(data[i].SubCategories); // store result
        if (temp) {
          // check
          return temp; // return result
        }
      }
    }
    return null;
  };

  render() {
    const {
      loading,
      error,
      data,
      onChangeItem,
      reloadCategories,
      reloadContent,
      variant
    } = this.props;
    const {
      open,
      currentID,
      openAddNew,
      id,
      openEdit,
      content,
      openDelete,
      uniqueName
    } = this.state;
    return loading ? (
      <Skeleton count={6} height={40} />
    ) : error.length > 0 ? (
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          minHeight: 150
        }}
      >
        {error}
      </div>
    ) : (
      <>
        <FullScreenDialog
          title={content ? 'Add New Item' : 'Add New Category'}
          setOpen={() => {
            this.setState({ openAddNew: false });
          }}
          open={openAddNew}
          type="new"
          id={id}
          data={this.findById(data)}
          onSuccess={() => {
            reloadCategories();
            reloadContent();
          }}
          content={content}
          variant={variant}
        />
        <FullScreenDialog
          title="Edit Category"
          setOpen={() => {
            this.setState({ openEdit: false });
          }}
          open={openEdit}
          type="edit"
          id={id}
          data={this.findById(data)}
          onSuccess={reloadCategories}
          variant={variant}
        />
        <AlertDialog
          onClose={[
            () => this.setState({ openDelete: false }),
            () => {
              reloadCategories();
              reloadContent(null);
            }
          ]}
          open={openDelete}
          type={'deleteCat'}
          uniqueName={uniqueName}
        />

        <List
          component="nav"
          aria-labelledby="nested-list-subheader"
          subheader={
            <>
              <ListSubheader
                component={Button}
                onContextMenu={e => this.handleContextMenu('main', e)}
                onClick={() => {
                  onChangeItem('');
                  this.setState({ id: 'main' });
                }}
                style={{
                  display: 'flex',
                  justifyContent: 'flex-start',
                  borderRadius: 0
                }}
                fullWidth
              >
                <CategoryIcon style={{ ...styles.listIcons, ...styles.mr_2 }} />{' '}
                Categories
              </ListSubheader>
              <Divider />
              {currentID === 'main' && (
                <ClickAwayListener
                  onClickAway={() => this.setState({ currentID: '' })}
                >
                  <Paper
                    style={{
                      position: 'absolute',
                      left: 86,
                      zIndex: 2,
                      top: 21
                    }}
                    elevation={11}
                  >
                    <MenuList>
                      <MenuItem onClick={() => this.openNew('main')}>
                        <PlusIcon
                          style={{ ...styles.listIcons, ...styles.mr_2 }}
                        />
                        New
                      </MenuItem>
                    </MenuList>
                  </Paper>
                </ClickAwayListener>
              )}
            </>
          }
        >
          {data.map((item, index) => {
            return (
              <>
                <ListItem
                  onContextMenu={e => this.handleContextMenu(item.ID, e)}
                  style={{ position: 'relative' }}
                  button
                  onClick={() => {
                    onChangeItem(item.ID);
                    this.setState({ id: item.ID });
                  }}
                  selected={item.ID === id}
                >
                  {currentID === item.ID && (
                    <ClickAwayListener
                      onClickAway={() => this.setState({ currentID: '' })}
                    >
                      <Paper
                        style={{
                          position: 'absolute',
                          left: 117,
                          zIndex: 2,
                          top: 21
                        }}
                        elevation={11}
                      >
                        <MenuList>
                          <MenuItem
                            onClick={() => this.openNew(item.ID)}
                            divider
                          >
                            <PlusIcon
                              style={{ ...styles.listIcons, ...styles.mr_2 }}
                            />
                            New
                          </MenuItem>
                          {currentID !== 'main' && (
                            <>
                              <MenuItem
                                onClick={() => this.openEdit(item.ID)}
                                divider
                              >
                                <EditIcon
                                  style={{
                                    ...styles.listIcons,
                                    ...styles.mr_2
                                  }}
                                />
                                Edit
                              </MenuItem>
                              <MenuItem
                                onClick={() => this.openNew(item.ID, true)}
                                divider
                              >
                                <AddIcon
                                  style={{
                                    ...styles.listIcons,
                                    ...styles.mr_2
                                  }}
                                />
                                Add Item
                              </MenuItem>
                              <MenuItem
                                onClick={() =>
                                  this.openDelete(item.ID, item.UniqueName)
                                }
                                divider
                              >
                                <DeleteIcon
                                  style={{
                                    ...styles.listIcons,
                                    ...styles.mr_2
                                  }}
                                />
                                Delete
                              </MenuItem>
                            </>
                          )}
                        </MenuList>
                      </Paper>
                    </ClickAwayListener>
                  )}
                  <ListItemIcon>
                    {item.SubCategories !== null && open.includes(item.ID) ? (
                      <IconButton onClick={() => this.handleClose(item.ID)}>
                        <RemoveIcon
                          style={{
                            ...styles.listIcons
                          }}
                        />
                      </IconButton>
                    ) : (
                      item.SubCategories !== null &&
                      item.SubCategories.length > 0 && (
                        <IconButton onClick={() => this.handleClick(item.ID)}>
                          <PlusIcon
                            style={{
                              ...styles.listIcons
                            }}
                          />
                        </IconButton>
                      )
                    )}
                  </ListItemIcon>
                  <ListItemText primary={item.Name} />
                </ListItem>
                {this.ExpandRecursively(item.SubCategories, iteration)}
              </>
            );
          })}
        </List>
      </>
    );
  }
}
