import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { Avatar, Typography } from "@material-ui/core";
import Profile from "../../../assets/images/profile.jpg";
import { styles } from "../../../styles";
import PersonIcon from "@material-ui/icons/PersonOutline";
import MailIcon from "@material-ui/icons/MailOutline";
import Divider from "@material-ui/core/Divider";
import LogoutIcon from "@material-ui/icons/ExitToApp";

export default function LongMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const name = window.localStorage.getItem("name");

  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  const IconsStyle = {
    color: "#666",
    fontSize: "20px",
    marginRight: "7px"
  };
  const TextStyle = {
    color: "#666",
    fontSize: "16px"
  };

  const { type } = props;

  return (
    <div>
      <IconButton
        aria-label="More"
        aria-controls="long-menu"
        aria-haspopup="true"
        onClick={handleClick}
        disabled={type === "generic"}
      >
        <Avatar src={Profile} style={styles.mr_2} />
      </IconButton>
      <Menu
        PaperProps={{
          style: { width: 200 }
        }}
        id="long-menu"
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center"
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center"
        }}
      >
        {type !== "generic" && (
          <>
            <MenuItem style={styles.d_flex}>
              <span style={TextStyle}>{name}</span>
            </MenuItem>
            <MenuItem style={styles.d_flex}>
              <PersonIcon style={IconsStyle} />
              <span style={TextStyle}>&nbsp;My Profile</span>
            </MenuItem>
            <MenuItem style={styles.d_flex}>
              <MailIcon style={IconsStyle} />
              <span style={TextStyle}>&nbsp;Inbox</span>
            </MenuItem>
            <Divider />
            <MenuItem onClick={e => props.signout(e)}>
              <LogoutIcon style={IconsStyle} />
              <span style={TextStyle}>&nbsp;Logout</span>
            </MenuItem>
          </>
        )}
        {type === "generic" && (
          <>
            <MenuItem style={styles.d_flex}>
              <Avatar src={Profile} style={styles.mr_2} />
              <Typography variant="overline">{name}</Typography>
            </MenuItem>
            <MenuItem onClick={e => {}}>Logout</MenuItem>
          </>
        )}
      </Menu>
    </div>
  );
}
