import React, { useState, useEffect } from 'react';
import moment from 'moment';
import axios from 'axios'
import { URL } from '../../BaseUrl';
import ReactTable from 'react-table';
import { CircularProgress } from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers';
import './TABLE_CSS_ALL_LOADING.css'


const PaymentDetails = () => {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true)
    const [startDate, setStartDate] = useState(new Date('2020-02-03 11:20:16.370'))
    const [endDate, setEndDate] = useState(new Date());

    // const startDay = moment(new Date()).startOf('day').subtract(1, 'day')
    // const endDay = moment(new Date()).endOf('day');

    useEffect(() => {
        const body = {
            nDateFrom: startDate,
            nDateTo: endDate
        }
        setLoading(true)
        axios.post(`${URL}/Api/PaymentReport`, body).then((response) => {
            setData(response.data);
            setLoading(false);
        })
    }, [startDate, endDate])


    return (
        <React.Fragment>
            <MuiPickersUtilsProvider utils={MomentUtils}>
                <div className="Second_TABLE_DATE_PICKERS_CODE_HEAD" >

                    <div>

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            label="From ( Date )"
                            format="MM-DD-YYYY"
                            inputVariant="outlined"
                            value={startDate}
                            onChange={(date) => setStartDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                        <KeyboardDatePicker
                            margin="normal"
                            id="date-picker-dialog"
                            inputVariant="outlined"
                            label="To ( Date )"
                            format="MM-DD-YYYY"
                            value={endDate}
                            onChange={(date) => setEndDate(date)}
                            KeyboardButtonProps={{
                                'aria-label': 'change date',
                            }}
                        />

                    </div>

                </div>

            </MuiPickersUtilsProvider>

            {
                loading && <div id="NEW_ALL_TABLE_LOADING" >
                    <div>
                        <CircularProgress />
                        <div>
                            Loading Data...
                        </div>
                    </div>
                </div>
            }

            {data !== null && data.PaymentData === null && !loading && <div>

                <h4>No Data Found</h4>

            </div>}

            {data !== null && data.PaymentData !== null && !loading && <div>

                <header id="NEW_ALL_TABLE_Header">
                    <span>{`Payment Details : Total Record ( ${data.PaymentData.length} ) `}</span>
                </header>

                <ReactTable
                    className='-striped -highlight h-full overflow-hidden'
                    data={data.PaymentData || []}
                    columns={
                        [
                            {
                                Header: "id",
                                accessor: 'ID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ID}</div>
                            },
                            {
                                Header: "Client ID",
                                accessor: 'ClientID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ClientID}</div>
                            },
                            {
                                Header: "Project ID",
                                accessor: 'ProjectID',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.ProjectID}</div>
                            },
                            {
                                Header: "Payment Type",
                                accessor: 'PaymentType',
                                className: "justify-start",
                                sortable: false
                            },
                            {
                                Header: "Amount",
                                accessor: 'Amount',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.Amount}</div>
                            },
                            {
                                Header: "Amount Paid",
                                accessor: 'AmountPaid',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.AmountPaid}</div>
                            },
                            {
                                Header: "Due Amount",
                                accessor: 'DueAmount',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => <div style={{ textAlign: 'center' }} > {row.original.DueAmount}</div>
                            },
                            {
                                Header: "Due Date",
                                accessor: 'DueDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.DueDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "PaidDate",
                                accessor: 'PaidDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.PaidDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "Status",
                                accessor: 'Status',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "PaymentMethod",
                                accessor: 'PaymentMethod',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "CreatedBy",
                                accessor: 'CreatedBy',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "Created Date",
                                accessor: 'CreatedDate',
                                className: "justify-start",
                                sortable: false,
                                Cell: (row) => moment(row.original.CreatedDate).format("MM-DD-YYYY")
                            },
                            {
                                Header: "ModifiedBy",
                                accessor: 'ModifiedBy',
                                className: "justify-start",
                                sortable: false,
                            },
                            {
                                Header: "Modified Date",
                                accessor: 'ModifiedDate',
                                className: "justify-start",
                                sortable: false,
                                ell: (row) => moment(row.original.ModifiedDate).format("MM-DD-YYYY")
                            },
                        ]
                    }
                    defaultPageSize={data.PaymentData.length}
                    noDataText={"No Data"}
                    showPageSizeOptions={false}
                    showPagination={false}
                />
            </div>}
        </React.Fragment>
    )
}


export default PaymentDetails;