import React from "react";
import { Route, Switch, Router } from "react-router-dom";
import LoginPage from "../../pages/Login";
import DashboardPage from "../../pages/DashboardPage";
import history from "../../history";
import { VisitProjectForm } from "../../pages/VisitProjectForm";
import { VisitDetailForm } from "../../pages/VisitDetailForm";
import Tools from "../../pages/Tools";
import { Permissions } from "../../pages/Permissions";
import { ProjectFollowUp } from "../../pages/ProjectFollowUp";
import { ProjectReAssign } from "../../pages/ReAssigningProjects";
import ProjectOverview from "../../pages/ClientExternalScreen/index";

export const ProtectedRoute = (props) => {
  let token = window.localStorage.getItem("accessToken");
  let isValid = token && token.length > 5;
  return isValid ? (
    <Router history={history}>
      <Switch>
        <Route exact path="/dashboard" component={DashboardPage} />
        {/* <Route exact path="/generic-content" component={DashboardPage} /> */}
        <Route exact path="/users/:id" component={DashboardPage} />
        <Route exact path="/inbox" component={DashboardPage} />
        <Route exact path="/clients" component={DashboardPage} />
        <Route exact path="/users" component={DashboardPage} />
        <Route exact path="/all-users" component={DashboardPage} />
        <Route exact path="/my-tasks" component={DashboardPage} />
        <Route exact path="/my-tasks/:id" component={DashboardPage} />
        <Route exact path="/visit-list" component={DashboardPage} />
        <Route exact path="/visits/:id" component={DashboardPage} />
        <Route exact path="/add-new-visit/:id/:new" component={DashboardPage} />
        <Route exact path="/roles" component={DashboardPage} />
        <Route exact path="/expenses" component={DashboardPage} />
        <Route exact path="/reports" component={DashboardPage} />
        <Route exact path="/notifications" component={DashboardPage} />
        <Route
          exact
          path="/supervision-details/:id"
          component={DashboardPage}
        />
        <Route exact path="/notifications/sent" component={DashboardPage} />
        <Route exact path="/notifications/received" component={DashboardPage} />
        <Route exact path="/notifications/project" component={DashboardPage} />
        <Route exact path="/inquiry" component={DashboardPage} />
        <Route exact path="/payments" component={DashboardPage} />
        <Route exact path="/submissions2" component={DashboardPage} />
        <Route exact path="/submissions" component={DashboardPage} />
        <Route exact path="/vouchers" component={DashboardPage} />
        <Route exact path="/projects/:id" component={DashboardPage} />
        <Route exact path="/activity-logs/:id" component={DashboardPage} />
        <Route exact path="/project-tasks/:id" component={DashboardPage} />
        <Route exact path="/trade" component={DashboardPage} />
        <Route exact path="/security" component={DashboardPage} />
        <Route exact path="/content" component={DashboardPage} />
        <Route exact path="/banner" component={DashboardPage} />
        <Route exact path="/news" component={DashboardPage} />
        <Route exact path="/media" component={DashboardPage} />
        <Route exact path="/faqs" component={DashboardPage} />
        <Route exact path="/" component={LoginPage} />
        <Route exact path="/login" component={LoginPage} />
        <Route
          path="/visit-list-form/:id/:plot/:status"
          component={DashboardPage}
        />
        <Route path="/visit-detail-form/:id" component={DashboardPage} />
        <Route exact path="/Tools" component={DashboardPage} />
        <Route path="/permissions" component={DashboardPage} />
        <Route path="/ProjectFollowUp" component={DashboardPage} />
        <Route path="/ProjectReassign" component={DashboardPage} />
        <Route path="/projectRank" component={DashboardPage} />
        <Route exact path="/projectStatus" component={ProjectOverview} />
      </Switch>
    </Router>
  ) : (
    <Switch>
      <Route exact path="/" component={LoginPage} />
      <Route exact path="/projectStatus" component={ProjectOverview} />
      <Route exact path="/login" component={LoginPage} />
      <Route exact path="/dashboard" component={DashboardPage} />
      <Route component={DashboardPage} />
    </Switch>
  );
};
