import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid } from "@material-ui/core";
import { Table } from "../../components/Table";
import { all_users_module_columns } from "../../server/columns";
import { GetAllUsersPagged, GetAllRoleTypes, DeleteUser } from "../../server";
import * as Action2 from "../../redux/actions/user.actions";
import { useDispatch, useSelector } from "react-redux";
import Alert from "../../components/Alert";

function AllUsers(props) {
  const dispatch = useDispatch();
  const { id } = props.match.params;
  const [data, setData] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [nullSelected, setNullSelected] = useState(false);
  const [page, setPage] = useState(0);
  const [count, setCount] = useState(0);
  const [roles, setRoles] = useState([]);

  const [open, setOpen] = useState(false);
  const [success, setSuccess] = useState("");
  const [deleteID, setDelete] = useState(0);

  const handleClickOpen = (e, ID, open) => {
    setOpen(true);
    setDelete(ID);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    dispatch(Action2.setModule("Users"));
  }, []);

  const reloadContent = () => {
    setData([]);
    setLoading(true);
    setSuccess("");
    setError("");
    GetAllUsersPagged(page)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.UsersData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });
  };

  useEffect(() => {
    GetAllUsersPagged(page)
      .then((res) => {
        if (res.data.Result.result === true) {
          let users = res.data.Result.UsersData;
          let count = res.data.Result.TotalCount;
          setCount(count);
          setLoading(false);
          setData(users);
        } else {
          let ErrorMessage = res.data.message;
          setLoading(false);
          setError(ErrorMessage);
        }
      })
      .catch((err) => {
        let ErrorMessage = err.message;
        setLoading(false);
        setError(ErrorMessage);
      });

    GetAllRoleTypes()
      .then((res) => {
        let Roles = res.data.data;
        setRoles(Roles);
      })
      .catch((err) => console.log("Error fetching Roles"));
    // GetAllProjects()
    //   .then(res => {
    //     let data = res.data.Result.ProjectsData || [];
    //     setProjects(data);
    //   })
    //   .catch(error => {
    //     console.log("Error fetching projects...");
    //   });
  }, []);

  const changePage = (_page) => {
    setPage(_page);
    setLoading(true);
    setData([]);
    GetAllUsersPagged(_page)
      .then((res) => {
        let data = res.data.Result.UsersData || [];
        let count = res.data.Result.TotalCount;

        setData(data);
        setLoading(false);
        setCount(count);
      })
      .catch((error) => {
        let err = error.message;
        setError(err);
        setLoading(false);
      });
  };

  const handleDelete = (e) => {
    e.preventDefault();
    handleClose();
    DeleteUser(deleteID).then((res) => {
      setLoading(true);
      if (res.data.Result.result) {
        setSuccess(res.data.Result.message);
        setLoading(false);
      } else {
        setError("Error");
        setLoading(false);
      }
    });
  };

  return (
    <Grow in={true}>
      <div>
        <Alert
          open={error.length > 0}
          type="error"
          message={error}
          onClose={() => {
            setError("");
          }}
        />
        <Alert
          open={success.length > 0}
          type="success"
          message={success}
          onClose={() => {
            reloadContent();
          }}
        />
        <Typography variant="h6" gutterBottom>
          Users
        </Typography>
        <Grid container>
          <Grid item xs={12}>
            <Table
              title=""
              columns={all_users_module_columns}
              data={data}
              isLoading={loading}
              error={error}
              onChangePage={changePage}
              onSuccess={reloadContent}
              page={page}
              totalRecords={count}
              onRefresh={reloadContent}
              nullSelected={nullSelected}
              variant={2}
              projects={projects}
              roles={roles}
              // listType={"all-users"}
              DailogOpen={handleClickOpen}
            />
          </Grid>
        </Grid>
        <div>
          {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Open alert dialog
      </Button> */}
          <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">
              {"Comfirmation Message?"}
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                Do you really want to Delete this user?
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClose} color="primary">
                No
              </Button>
              <Button
                onClick={(e) => handleDelete(e)}
                color="primary"
                autoFocus
              >
                Yes
              </Button>
            </DialogActions>
          </Dialog>
        </div>
      </div>
    </Grow>
  );
}

export default withRouter(AllUsers);
